<template>
	<div class="Anexo1">

		<h2 class="secao_lei">ANEXO I - DISPOSIES TCNICAS</h2>
		<p class="corpo_decreto">Este Anexo estabelece critrios e parmetros tcnicos a serem observados pelos Responsveis Tcnicos pelo projeto e pela obra, quando da elaborao do projeto de construo, execuo de obras, instalao de equipamentos e adaptao das edificaes de diferentes usos, mesmo no havendo previso de representao grfica no projeto simplificado. </p>
		<h3 class="subsecao_lei">1 - DO CANTEIRO DE OBRAS </h3>
		<p class="corpo_lei"><strong class="artigo-lei">1.1. </strong>Durante a execuo da obra ou servio  obrigatria a manuteno do passeio desobstrudo e em perfeitas condies, conforme legislao municipal aplicvel, sendo vedada sua utilizao, ainda que temporria, como canteiro de obras ou para carga e descarga de materiais de construo.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">1.A. </strong>Durante a execuo da obra ou servio  obrigatria a manuteno do passeio desobstrudo e em perfeitas condies, conforme legislao municipal aplicvel, sendo vedada sua utilizao, ainda que temporria, como canteiro de obras ou para carga e descarga de materiais de construo. </p>
		<p class="corpo_lei"><strong class="destaque-lei">1.1.1. </strong>Os elementos do canteiro de obras no podem prejudicar a arborizao da rua, a iluminao pblica, a visibilidade de placas, avisos ou sinais de trnsito e outras instalaes de interesse pblico.</p>
		<p class="corpo_decreto"><strong class="destaque-decreto">1.A.1. </strong>Os elementos do canteiro de obras no podero prejudicar a arborizao da rua, a iluminao pblica, a visibilidade de placas, avisos ou sinais de trnsito e outros elementos ou instalaes de interesse pblico. </p>
		<p class="corpo_lei"><strong class="artigo-lei">1.2. </strong> obrigatrio o fechamento do canteiro de obras no alinhamento, por alvenaria ou tapume.</p>
		<p class="corpo_decreto"><strong class="destaque-decreto">1.A.2. </strong>O tapume dever ser mantido no alinhamento enquanto os servios da obra se desenvolverem a altura superior a 4,00 m (quatro metros) do passeio, sendo permitida a ocupao do passeio apenas para apoio de cobertura para proteo de pedestres.<strong class="destaque-decreto"></strong></p>
		<p class="corpo_decreto"><strong class="destaque-decreto">1.A.3. </strong> obrigatrio o fechamento do canteiro de obras no alinhamento, por alvenaria ou tapume com altura mnima de 2,20 m (dois metros e vinte centmetros). </p>
		<p class="corpo_lei"><strong class="artigo-lei">1.3. </strong>Durante o desenvolvimento de servios de fachada na obra situada no alinhamento ou prximo a ele,  obrigatrio o avano do tapume sobre o passeio de forma a proteger o pedestre.</p>
		<p class="corpo_decreto"><strong class="destaque-decreto">1.A.4. </strong>Durante o desenvolvimento de servios de fachada em obras situadas no alinhamento ou dele afastadas at 1,20 m (um metro e vinte centmetros) ser obrigatrio, mediante a emisso de Alvar de Autorizao, o avano do tapume sobre o passeio at, no mximo, metade de sua largura, de forma a proteger o pedestre. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">1.A.5. </strong>Quando a largura livre do passeio resultar inferior a 0,90 m (noventa centmetros) em logradouro sujeito a intenso trfego de veculos, o trnsito de pedestres pode ser desviado para parte protegida do leito carrovel a critrio do rgo Municipal de Trnsito. </p>
		<p class="corpo_lei"><strong class="artigo-lei">1.4. </strong>Concludo o servio de fachada ou paralisada a obra por perodo superior a 30 (trinta) dias, o tapume deve ser obrigatoriamente recuado para o alinhamento.</p>
		<p class="corpo_decreto"><strong class="destaque-decreto">1.A.6. </strong>Concludo o servio de fachada ou paralisada a obra por perodo superior a 30 (trinta) dias, o tapume deve ser obrigatoriamente recuado para o alinhamento. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">1.A.7. </strong>As instalaes de gruas devero observar o gabarito estabelecido pelo rgo Regional do DECEA e quando ultrapassarem o seu limite, devero ser submetidas  nova deliberao deste rgo. </p>
		<h3 class="subsecao_lei">2 - DA IMPLANTAO </h3>
		<p class="corpo_lei"><strong class="artigo-lei">2.1. </strong>A implantao de qualquer edificao no lote deve atender s disposies previstas no PDE e LPUOS, em especial aos recuos em relao s divisas do lote.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">2.A. </strong>A implantao de qualquer edificao no lote dever atender s disposies previstas no PDE e na LPUOS, em especial aos recuos em relao s divisas do lote. </p>
		<p class="corpo_lei"><strong class="artigo-lei">2.2. </strong>A edificao deve respeitar as normas referentes ao afastamento em relao s guas correntes ou dormentes, faixas de domnio pblico de rodovias e ferrovias, linhas de alta tenso, dutos e canalizaes.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">2.B. </strong>A edificao dever respeitar as normas que regem o afastamento em relao s guas correntes ou dormentes, faixas de domnio pblico de rodovias e ferrovias, linhas de alta tenso, dutos e canalizaes. </p>
		<p class="corpo_lei"><strong class="artigo-lei">2.3. </strong>Em atendimento ao disposto no Cdigo Civil, deve ser observado:</p>
		<p class="corpo_lei"><strong class="destaque-lei">I -</strong> reserva de espao para passagem de canalizao de guas provenientes de lotes a montante, inclusive para a canalizao de esgoto;</p>
		<p class="corpo_lei"><strong class="destaque-lei">II -</strong> distncia mnima de 1,50 m (um metro e cinquenta centmetros) para a abertura voltada para as divisas do lote, bem como metade dessa distncia quando a abertura estiver perpendicular  divisa do lote, independentemente da existncia ou da altura do muro de divisa.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">2.C. </strong>Em atendimento ao disposto no Cdigo Civil, dever ser observado: </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">I -</strong> Reserva de espao para passagem de canalizao de guas provenientes de lotes a montante, inclusive para a canalizao de esgoto; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">II -</strong> Distncia mnima de 1,50 m (um metro e cinquenta centmetros) para a abertura voltada para as divisas do lote, bem como metade dessa distncia quando a abertura estiver perpendicular  divisa do lote, independentemente da existncia do muro de divisa e de sua altura. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">III -</strong> No sero consideradas aberturas as paredes de tijolos de vidro translcido ou com desempenho similar para fins das disposies do COE e do inciso II deste item. </p>
		<p class="corpo_lei"><strong class="artigo-lei">2.4. </strong>Nos cruzamentos dos logradouros pblicos, deve ser previsto canto chanfrado de 3,50 m (trs metros e cinquenta centmetros), salvo se tal concordncia tiver sido fixada de forma diversa em arruamento ou plano de melhoramento pblico.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">2.D. </strong>Nos cruzamentos dos logradouros pblicos, dever ser previsto canto chanfrado de 3,50 m (trs metros e cinquenta centmetros), salvo se tal concordncia tiver sido fixada de forma diversa em arruamento ou plano de melhoramento pblico. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">2.D.1. </strong>Quando houver exigncia de doao para alargamento do passeio pblico pela LPUOS ou por legislao especifica, dever ser previsto tambm a doao do canto chanfrando de 3,50 m, normal  bissetriz do ngulo formado pelo prolongamento dos novos alinhamentos, salvo se tal concordncia tiver sido fixada de forma diversa em arruamento ou plano de melhoramento pblico situao em que a doao dever seguir tal configurao. </p>
		<p class="corpo_lei"><strong class="artigo-lei">2.5. </strong>Para os terrenos edificados,  facultativa a construo de muro de fecho em suas divisas, observadas as disposies do PDE e LPUOS.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">2.E. </strong>Para os terrenos edificados  facultativa a construo de muro de fechamento em suas divisas e alinhamento, observadas as disposies do PDE e da LPUOS. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">2.E.1. </strong>Quando executados, os muros devem observar altura mxima de: </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">I -</strong> 4,00 m (quatro metros), acima do passeio, quando junto ao alinhamento; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">II -</strong> 3,00 m (trs metros), quando junto s demais divisas, medidos a partir do nvel em que se situarem, excetuados os muros de arrimo que tero altura compatvel com o desnvel de terra. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">2.E.1.1. </strong>O muro junto ao alinhamento no poder ser totalmente vedado, devendo ser interrompido por elementos vazados, transparentes ou gradis, na proporo de 25% (vinte e cinco por cento) de sua extenso horizontal, observado o limite de 15 m de extenso vedada e as disposies do PDE e LPUOS. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">2.E.1.2. </strong>No se aplica o item 2.E.1.1 aos muros de arrimo e aos muros em testadas com extenso mxima de 20 m (vinte metros). </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">2.E.1.3. </strong>No se aplica o disposto no item 2.E.1 quando se tratar de anteparo vertical, gradil, muro, alambrado ou assemelhado que apresentem superfcie vazada uniformemente distribuda superior a 80% (oitenta por cento) de sua superfcie total. </p>
		<h3 class="subsecao_lei">3 - DAS CONDIES AMBIENTAIS </h3>
		<p class="corpo_lei"><strong class="artigo-lei">3.1. </strong>A execuo de qualquer tipo de obra junto a represa, lago, lagoa, rio, crrego e demais corpos dgua naturais deve atender s disposies de rea de Preservao Permanente  APP estabelecidas na legislao ambiental federal, estadual e municipal pertinente.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">3.A. </strong>A descaracterizao da rea de Preservao Permanente  APP dever ser comprovada pelo interessado se constatada a ausncia de elementos que a caracterizem, de acordo com a legislao pertinente. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">3.A.1. </strong>A execuo de qualquer tipo de obra junto a represa, lago, lagoa, rio, crrego e demais corpos dgua naturais, considerados reas de Preservao Permanente  APP, dever atender s disposies da legislao ambiental federal, estadual e municipal pertinentes; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">3.A.2. </strong>As reas de Preservao Permanente  APP podero ser descaracterizadas desde que constatada a ausncia de elementos que a caracterizem. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">3.A.2.1. </strong> competncia da Secretaria Municipal do Verde e Meio Ambiente a emisso de Parecer Tcnico Conclusivo sobre a matria.</p>
		<p class="corpo_lei"><strong class="artigo-lei">3.2. </strong>Junto a corpo dgua canalizado em galeria fechada, a execuo de qualquer tipo de obra deve observar afastamentos de forma a constituir faixa no edificvel, de acordo com o regulamento.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">3.B. </strong>Devero ser observados os seguintes afastamentos mnimos, de forma a constituir faixa no edificvel, de acordo com as seguintes situaes: </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">3.B.1. </strong>2,00 m (dois metros) a contar de suas faces externas, no caso de galeria ou canalizao existente com largura igual ou inferior a 1,00 m (um metro); </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">I -</strong> uma vez e meia a largura da benfeitoria, observado o mnimo de 3,00 m (trs metros) a contar de suas faces externas, no caso de galeria ou canalizao existente com largura superior a 1,00 (um metro); </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">II -</strong> a largura da faixa ser calculada com base na seo retangular equivalente, considerada a mesma rea de seo transversal e altura til da canalizao, no caso de canalizaes com seo trapezoidal ou seo mista; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">III -</strong> em funo da dimenso da bacia hidrogrfica e da topografia local, o rgo municipal competente poder fixar recuo superior ao estabelecido neste item. </p>
		<p class="corpo_lei"><strong class="artigo-lei">3.3. </strong>O manejo arbreo decorrente da implantao do projeto de que trata o COE depende de licena do rgo municipal competente, observada a legislao municipal pertinente.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">3.C. </strong>Para efeito do clculo da rea permevel exigida pela LPUOS, podero ser consideradas, alm das reas ajardinadas sobre o solo natural, as reas executadas com pavimentao semipermevel. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">3.C.1. </strong>Na hiptese de utilizao de pavimentao semi-permevel, apenas a rea correspondente ao percentual efetivo de drenagem do pavimento adotado dever ser considerada no clculo da rea permevel. </p>
		<p class="corpo_lei"><strong class="artigo-lei">3.4. </strong>O despejo das guas pluviais e das guas servidas canalizadas, inclusive daquelas provenientes do funcionamento de equipamento, bem como a ligao de esgoto, devem ser feitos por canalizao ligada  rede coletora, de acordo com as normas municipais e aquelas emanadas da concessionria competente, sob pena de multa renovvel a cada 30 (trinta) dias.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">3.D. </strong>O fechamento do terreno no poder impedir o escoamento das guas nem as operaes de limpeza e manuteno da faixa no-edificvel. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">3.D.1. </strong>Fica proibido o lanamento das guas servidas no sistema de drenagem, na sarjeta ou diretamente na calada, e ainda o lanamento sob regime de presso hidrulica, devendo escoar sob regime de escoamento livre.<strong class="destaque-decreto"></strong></p>
		<p class="corpo_decreto"><strong class="artigo-decreto">3.E. </strong>O despejo das guas servidas canalizadas, inclusive daquelas provenientes do funcionamento de equipamento, bem como a ligao de esgoto, devero ser feitos por canalizao ligada  rede coletora, de acordo com as normas municipais e da concessionria competente, sob pena de multa renovvel a cada 30 (trinta) dias. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">3.E.1. </strong>As Prefeituras Regionais devero solicitar  concessionria de servios pblicos de coleta, tratamento e destinao final de esgoto a relao dos endereos dos imveis que no dispem de ligao de esgoto s redes coletoras e a relao das vias que dispem da referida rede. </p>
		<p class="corpo_lei"><strong class="destaque-lei">3.4.1. </strong>A edificao situada em rea desprovida de rede coletora pblica de esgoto deve ser provida de instalao destinada ao armazenamento, tratamento e destinao de esgoto, de acordo com as normas pertinentes.</p>
		<p class="corpo_decreto"><strong class="destaque-decreto">3.E.2. </strong>A edificao situada em rea desprovida de rede coletora pblica de esgoto dever ser provida de instalaes destinadas ao armazenamento, tratamento e destinao de esgoto, de acordo com as normas pertinentes.<strong class="subtitulo_txt"></strong></p>
		<p class="corpo_lei"><strong class="destaque-lei">3.4.2. </strong>No ser permitido o despejo de guas pluviais sobre as caladas e os imveis vizinhos, devendo ser conduzidas por canalizao sob o passeio  rede coletora, de acordo com as normas emanadas do rgo competente, sob pena de multa renovvel a cada 30 (trinta) dias.</p>
		<p class="corpo_decreto"><strong class="destaque-decreto">3.E.2.1. </strong>As instalaes a que se refere o item 3.E.2 devero ser implantadas no interior do imvel. </p>
		<p class="corpo_decreto"><strong class="artigo-decreto">3.F. </strong>Nos imveis em que a conexo com escoamento por gravidade no for tecnicamente vivel, ser dispensada a ligao em marcha ou ligao factvel, at que a concessionria indique a soluo tcnica que permita a conexo, cabendo ao morador comprovar a eventual impossibilidade de execut-la. </p>
		<p class="corpo_decreto"><strong class="artigo-decreto">3.G. </strong>As guias e sarjetas dos logradouros integram a rede coletora de guas pluviais. </p>
		<p class="corpo_decreto"><strong class="artigo-decreto">3.H. </strong>As tubulaes para lanamento das guas pluviais oriundas dos lotes particulares nas sarjetas ou no sistema pblico de drenagem podero ser: </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">I -</strong> guas provenientes das chuvas;. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">II -</strong> guas provenientes da lavagem de reas descobertas dos lotes, desde que no haja a veiculao de produtos poluentes; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">III -</strong> guas provenientes do rebaixamento temporrio do lenol fretico, desde que no haja a veiculao de sedimentos; </p>
		<p class="corpo_decreto"><strong class="artigo-decreto">3.I. </strong>O dimetro mximo das tubulaes de descarga das guas pluviais dever ser calculado em funo da rea de cada lote, conforme regulamentao do rgo municipal competente. </p>
		<p class="corpo_decreto"><strong class="artigo-decreto">3.J. </strong>No ser permitido: </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">I -</strong> o lanamento de guas servidas no sistema pblico de drenagem ou nas sarjetas; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">II -</strong> o lanamento das guas no sistema pblico de drenagem ou nas sarjetas sob regime de presso hidrulica, devendo escoar sob regime de escoamento livre; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">III -</strong> o lanamento das tubulaes diretamente no passeio, devendo ser conduzidas sob o mesmo, at seu lanamento nas sarjetas ou no sistema de drenagem. </p>
		<p class="corpo_decreto"><strong class="artigo-decreto">3.K. </strong>A implantao da obra ficar condicionada  prvia execuo das benfeitorias indispensveis  estabilidade e saneamento locais.<strong class="subtitulo_txt"></strong></p>
		<p class="corpo_lei"><strong class="artigo-lei">3.5. </strong>Qualquer movimento de terra deve ser executado com o devido controle tecnolgico, a fim de assegurar a estabilidade, prevenir eroses e garantir a segurana dos imveis e logradouros limtrofes, bem como no impedir ou alterar o curso natural do escoamento de guas pluviais e fluviais.</p>
		<p class="corpo_lei"><strong class="artigo-lei">3.6. </strong>O despejo do entulho da obra, bem como o material descartado pelo movimento de terra deve ser feito em local licenciado para tal finalidade, de acordo com a legislao municipal especfica.</p>
		<p class="corpo_lei"><strong class="artigo-lei">3.7. </strong>Toda edificao a ser construda com rea superior a 750,00 m (setecentos e cinqenta metros quadrados) deve ser dotada de abrigo compartimentado e suficientemente dimensionado para a guarda dos diversos tipos de lixo, tais como o orgnico, o reciclvel e o txico, localizado no interior do lote e com acesso direto ao logradouro.</p>
		<p class="corpo_lei"><strong class="destaque-lei">3.7.1. </strong>No se aplica o disposto no subitem 3.7 s residncias unifamiliares e s habitaes agrupadas horizontalmente sem formar condomnio.</p>
		<p class="corpo_lei"><strong class="artigo-lei">3.8. </strong>A edificao nova com rea construda superior a 1.500,00 m (mil e quinhentos metros quadrados) deve ser provida de instalao destinada a receber sistema de aquecimento de gua por meio do aproveitamento da energia solar, quando destinada a:</p>
		<p class="corpo_lei"><strong class="destaque-lei">I -</strong> uso residencial, exceto as residncias unifamiliares e as unidades habitacionais agrupadas horizontalmente sem formar condomnio com at 3 (trs) banheiros;</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">3.L. </strong>Nas edificaes novas destinadas ao uso residencial multifamiliar com rea superior a 1.500 m (mil e quinhentos metros quadrados) que possuam unidades com at 3 (trs) banheiros cada, devero ser executadas em seus sistemas de instalaes hidrulicas, prumada e respectiva rede de distribuio, de modo a permitir a instalao do reservatrio trmico e placas coletoras de energia solar. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">3.L.1. </strong>Para fins de aplicao do item 3.L deste decreto entende-se por banheiro o aposento dotado de chuveiro, possuindo ou no, em suas instalaes, aquecimento de gua por toda e qualquer fonte de energia.</p>
		<p class="corpo_lei"><strong class="destaque-lei">II -</strong> uso no residencial que disponha de instalaes para vestirio e banho ou local onde se desenvolva atividade que utilize gua aquecida;</p>
		<p class="corpo_lei"><strong class="destaque-lei">III -</strong> qualquer uso, quando for construda piscina de gua aquecida.</p>
		<p class="corpo_lei"><strong class="destaque-lei">3.8.1. </strong>O sistema de instalaes hidrulicas e os equipamentos de aquecimento de gua por energia solar devem ser dimensionados para atender, no mnimo, 40% (quarenta por cento) de toda a demanda anual de energia necessria para o aquecimento da gua.</p>
		<p class="corpo_decreto"><strong class="destaque-decreto">3.L.2. </strong>Os sistemas de instalaes hidrulicas e os equipamentos de aquecimento de gua por energia solar de que tratam este decreto devero ser dimensionados para atender, no mnimo, 40% (quarenta por cento) de toda a demanda anual de energia necessria para o aquecimento de gua sanitria e de piscinas, de acordo com a Metodologia de Avaliao da Contribuio Solar.<strong class="subtitulo_txt"></strong></p>
		<p class="corpo_lei"><strong class="destaque-lei">3.8.1.1. </strong>Admite-se desempenho inferior ao estabelecido neste subitem 3.8.1, no caso de comprovada inviabilidade tcnica para alcanar o percentual mnimo estabelecido.</p>
		<p class="corpo_decreto"><strong class="destaque-decreto">3.L.3. </strong>O disposto no item 3.8 do COE no se aplica s edificaes nas quais seja tecnicamente invivel alcanar as condies que correspondam  demanda anual de energia necessria para o aquecimento de gua por energia solar. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">3.L.3.1. </strong>Para a comprovao da inviabilidade tcnica dever ser apresentado atestado emitido por profissional habilitado com a respectiva ART ou RRT. </p>
		<p class="corpo_lei"><strong class="destaque-lei">3.8.1.2. </strong>Admite-se a adoo de outro sistema ou tecnologia que assegure o mesmo desempenho da reduo do consumo de energia estabelecido neste subitem 3.8.1</p>
		<p class="corpo_decreto"><strong class="destaque-decreto">3.L.4. </strong>Poder ser aceita a adoo de outros sistemas de cogerao de energia ou tecnologias economizadoras, desde que assegurem o mesmo desempenho da reduo do consumo de energia eltrica e que tenham eficincia semelhante  da energia solar, mediante apresentao de relatrio tcnico e responsabilidade tcnica, a serem avalizados pela CEUSO. </p>
		<p class="corpo_lei"><strong class="artigo-lei">3.9. </strong>Toda edificao deve dispor de instalao permanente para gs combustvel e, quando utilizado, o recipiente de gs deve ser armazenado fora da edificao, em ambiente exclusivo e dotado de abertura para ventilao permanente.</p>
		<p class="corpo_lei"><strong class="artigo-lei">3.10. </strong>As unidades condominiais, inclusive as habitacionais, devem dispor de sistema de medio individualizada do consumo de gua, energia e gs.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">3.M. </strong>O ambiente ou compartimento que contiver equipamento ou instalao com funcionamento a gs dever dispor de ventilao permanente, assegurada por abertura direta para o exterior.<strong class="destaque-decreto"></strong></p>
		<p class="corpo_decreto"><strong class="artigo-decreto">3.N. </strong>As solues construtivas, paisagsticas e o inventrio dos indivduos arbreos propostos e existentes devero ser demonstrados e quantificados nas peas grficas do projeto objeto de licenciamento.</p>
		<p class="corpo_decreto"><strong class="destaque-decreto">3.N.1. </strong> de inteira responsabilidade do profissional habilitado o enquadramento dos indivduos arbreos existentes e propostos nas categorias estabelecidas pela LPUOS. </p>
		<p class="corpo_decreto"><strong class="artigo-decreto">3.O. </strong>O movimento de terra quando desvinculado de obra de edificao e em terrenos erodidos, erodveis ou contaminados dever atender s NTOs pertinentes a cada situao. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">3.O.1. </strong>Quando previsto movimento de terra vinculado a Alvar de Execuo de Edificao Nova ou Reforma, deve ser observado o seguinte: </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">I -</strong> a execuo das contenes e do movimento de terra necessrios  implantao do projeto devero atender s NTOs cabveis; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">II -</strong> o resduo excedente ser destinado s reas adequadas a seu recebimento ou ser provido local adequado ao seu emprstimo. Estas reas podem ser particulares ou regularmente licenciadas como de Destinao de Resduos Inertes (Bota-Fora) com a devida classificao e licena de operao vlida na data da realizao desta fase da obra; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">III -</strong> no caso de interveno em reas contaminadas, o movimento de terra dever respeitar a classificao dos resduos, de acordo com o Plano de Interveno aprovado pelo rgo pblico competente; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">IV -</strong> eventuais danos a terceiros ou ao patrimnio pblico so de responsabilidade do proprietrio ou possuidor e dos responsveis tcnicos pela obra. </p>

