				</div>
				<div id="g-ai0-36" class="g-Camada_2 g-aiAbs" style="top:72.7131%;left:0%;width:83.2292%;">
					<p>A execuo de qualquer tipo de obra junto a represa, lago, lagoa, rio, crrego e demais corpos d'gua naturais deve atender s disposies de rea de Preservao Permanente - APP estabelecidas na legislao ambiental federal, estadual e municipal.</p>
					<p>No caso de canalizaes com seo trapezoidal ou mista, a largura da faixa ser calculada com base na seo retangular equivalente, considerada a mesma rea de seo transversal e altura til da canalizao.</p>
