		<h3 class="subsecao_lei">4 - DAS CONDIES DE ACESSIBILIDADE</h3>
		<p class="corpo_lei"><strong class="artigo-lei">4.1. </strong>Devem atender s condies de acessibilidade s pessoas com deficincia ou mobilidade reduzida estabelecidas no COE e legislao correlata a edificao nova e a edificao existente em caso de sua reforma, requalificao ou regularizao, quando destinada a uso:</p>
		<p class="corpo_lei"><strong class="destaque-lei">I -</strong> pblico, entendido como aquele administrado por rgo ou entidade da Administrao Pblica Direta e Indireta ou por empresa prestadora de servio pblico e destinado ao pblico em geral;</p>
		<p class="corpo_lei"><strong class="destaque-lei">II -</strong> coletivo, entendido como aquele destinado  atividade no residencial.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">4.A. </strong>Considera-se, exclusivamente, para fins de acessibilidade: </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">I -</strong> Uso privado: espao ou compartimento de utilizao exclusiva da populao permanente da edificao de uso residencial; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">II -</strong> Uso restrito: espao, compartimento, ou elemento interno ou externo, disponvel estritamente para pessoas autorizadas, segundo definies previstas nas NTOs de Acessibilidade. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">4.A.1. </strong>Pelo menos um dos itinerrios que comuniquem horizontal e verticalmente a todas as dependncias e servios da edificao, entre si e com o exterior, deve cumprir os requisitos de acessibilidade. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">4.A.2. </strong>A construo, as reas objeto de reforma, a ampliao, a regularizao ou a mudana de uso de edificaes abertas ao pblico, de uso pblico ou privadas de uso coletivo devero ser executadas de modo a serem acessveis, observados os parmetros tcnicos estabelecidos na NBR 9050 em vigor ou naquela que vier a substitui-la. </p>
		<p class="corpo_lei"><strong class="destaque-lei">4.1.2. </strong>As reas comuns da edificao multifamiliar tambm devem observar as condies de acessibilidade.<strong class="destaque-decreto"></strong></p>
		<p class="corpo_decreto"><strong class="artigo-decreto">4.B. </strong>As edificaes residenciais multifamiliares, condomnios e conjuntos habitacionais necessitam ser acessveis em suas reas de uso comum, devendo as unidades autnomas acessveis e adaptveis estar conectadas s rotas acessveis. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">4.B.1. </strong>A rota acessvel  um trajeto contnuo, desobstrudo e sinalizado, que conecta os ambientes externos e internos de espaos e edificaes. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">4.B.2. </strong>A rota acessvel poder coincidir com a rota de fuga. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">4.B.3. </strong>O percurso entre o estacionamento de veculos e os acessos dever compor uma rota acessvel. </p>
		<p class="corpo_lei"><strong class="artigo-lei">4.2. </strong>Ficam dispensadas do atendimento das exigncias estabelecidas neste item 4:</p>
		<p class="corpo_lei"><strong class="destaque-lei">I -</strong> a edificao residencial unifamiliar, a unidade habitacional no conjunto de habitaes agrupadas horizontalmente e a unidade habitacional na edificao de uso multifamiliar;</p>
		<p class="corpo_lei"><strong class="destaque-lei">II -</strong> o espao e o compartimento de utilizao restrita e exclusiva em edificao destinada a uso no residencial;</p>
		<p class="corpo_lei"><strong class="destaque-lei">III -</strong> o espao onde se desenvolve atividade especfica que justifique a restrio de acesso;</p>
		<p class="corpo_lei"><strong class="destaque-lei">IV -</strong> o andar superior de edificao de pequeno porte destinado a uso no residencial.</p>
		<p class="corpo_decreto"><strong class="destaque-decreto">4.B.4. </strong>Ficam dispensadas do atendimento s exigncias das condies de acessibilidade estabelecidas no artigo 40 do COE: </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">I -</strong> a edificao residencial unifamiliar, a unidade habitacional no conjunto de habitaes agrupadas horizontalmente e a unidade habitacional na edificao de uso multifamiliar, na forma prevista pela legislao federal aplicvel; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">II -</strong> os espaos e compartimentos de utilizao restrita e exclusiva, onde no haja permanncia humana, caracterizados como espaos, salas ou elementos internos ou externos, disponveis estritamente para pessoas autorizadas nos termos da NBR 9050, ou outra norma tcnica que vier a suced-la, tais como casas de mquinas, barriletes, passagem de uso tcnico e outros com funes similares; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">III -</strong> o andar superior ou inferior de edificao existente com at dois pavimentos e rea construda total de at 150 m (cento e cinquenta metros quadrados) no pavimento no acessvel, destinado ao uso no residencial, desde que a atividade instalada no pavimento contguo da edificao seja a mesma ou funcionalmente complementar  atividade desenvolvida no pavimento acessvel; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">IV -</strong> o espao destinado ao orador em local de reunio, com dimenses compatveis ao uso de uma pessoa. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">4.B.4.1. </strong>No se aplica a dispensa de atendimento das condies de acessibilidade prevista no inciso III do item 4.B.4., s seguintes atividades: </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">I -</strong> estabelecimentos bancrios e instituies financeiras; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">II -</strong> instituies de ensino de todas as modalidades, etapas e nveis de ensino; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">III -</strong> estabelecimento de prestao de servios de utilidade ou interesse pblico. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">4.B.4.2. </strong>A dispensa prevista no item 4.B.4 deste decreto no exime a aplicao da Lei Federal n 13.146/2015  Lei Brasileira de Incluso da Pessoa com Deficincia e das normas tcnicas de acessibilidade vigentes, em especial a NBR 9050, ou outra norma tcnica que vier a suced-la. </p>
		<p class="corpo_lei"><strong class="artigo-lei">4.3. </strong>Na reforma e na requalificao da edificao existente, com ou sem mudana de uso, caso haja inviabilidade tcnica de atendimento s condies de acessibilidade, deve ser realizada a adaptao razovel, nos termos do regulamento, no podendo ser reduzidas as condies j implantadas.</p>
		<p class="corpo_decreto"><strong class="destaque-decreto">4.B.5. </strong>Na reforma e requalificao de imveis, as condies de atendimento  acessibilidade devero ser atendidas, salvo hipteses de impraticabilidade tcnica, situao em que dever ser proposto projeto de adaptao razovel. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">4.B.5.1. </strong>Entende-se por adaptaes razoveis as adaptaes, modificaes e ajustes necessrios e adequados que no acarretem nus desproporcional e indevido, quando requeridos em cada caso. O nus desproporcional caracteriza-se pela impraticabilidade do atendimento  determinao de adaptao da edificao, nos termos do item 3.1.24 da NBR 9050, ou norma tcnica que a suceder. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">4.B.5.2. </strong>Como justificativa da impraticabilidade do atendimento  determinao de adaptao da edificao, devero ser apresentados os seguintes documentos assinados pelo proprietrio ou possuidor, responsvel(eis) tcnico(s) pelo projeto e pelos equipamentos, acompanhados das respectivas ART(s) / RRT(s): </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">I -</strong> memorial justificativo das obras propostas; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">II -</strong> declarao de impraticabilidade do atendimento  determinao da adaptao. </p>
		<p class="corpo_lei"><strong class="artigo-lei">4.4. </strong>A edificao deve ser dotada de rampa com largura mnima de 1,20 m (um metro e vinte centmetros) para vencer desnvel entre o logradouro pblico ou rea externa e o piso correspondente  soleira de ingresso, admitida a instalao de equipamento mecnico de transporte permanente para esta finalidade.</p>
		<p class="corpo_decreto"><strong class="destaque-decreto">4.B.6. </strong>A edificao dever ser dotada de rampa com largura mnima de 1,20 m (um metro e vinte centmetros) para vencer desnvel entre o logradouro pblico ou rea externa e o piso correspondente  soleira de ingresso, admitida a instalao de equipamento mecnico de transporte permanente para esta finalidade. </p>
		<p class="corpo_lei"><strong class="destaque-lei">4.4.1. </strong>O equipamento mecnico de transporte permanente destinado s pessoas com deficincia ou mobilidade reduzida, quando prevista sua instalao, pode ocupar as faixas de recuo de frente, laterais e de fundo, no sendo considerado rea computvel no clculo do coeficiente de aproveitamento e da taxa de ocupao.</p>
		<p class="corpo_lei"><strong class="artigo-lei">4.5. </strong>O nico ou pelo menos um dos elevadores da edificao deve ser acessvel, podendo ser substitudo por rampa quando o desnvel a vencer for igual ou inferior a 12,00 m (doze metros), observadas as normas pertinentes.</p>
		<p class="corpo_decreto"><strong class="destaque-decreto">4.B.7. </strong>No mnimo um dos elevadores da edificao dever ser acessvel, podendo ser substitudo por rampa quando o desnvel a vencer for igual ou inferior a 12,00 m (doze metros), observadas as normas pertinentes. </p>
		<p class="corpo_lei"><strong class="artigo-lei">4.6. </strong>A edificao deve dispor de pelo menos uma instalao sanitria em local acessvel e com dimenses adaptadas ao uso por pessoa com deficincia ou mobilidade reduzida, em quantidade e localizao adequada ao uso a que se destina.</p>
		<p class="corpo_decreto"><strong class="destaque-decreto">4.B.8. </strong>As edificaes novas e as reas a serem ampliadas ou nas reformas em edificaes regularmente existentes devero dispor de pelo menos uma instalao sanitria em local acessvel e com dimenses para o uso por pessoa com deficincia ou mobilidade reduzida, em quantidade e localizao adequadas ao uso a que se destina, na proporo prevista na NBR 9050, ou outra norma que venha a substitu-la. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">4.B.8.1. </strong>O sanitrio destinado  pessoa com deficincia ou com mobilidade reduzida dever ter entrada independente dos demais sanitrios pblicos ou coletivos podendo ser includo no clculo do nmero mnimo de instalaes sanitrias exigidas para a atividade. </p>
		<p class="corpo_lei"><strong class="artigo-lei">4.7. </strong>Devem ser fixadas vagas especiais para estacionamento de veculo para uso por pessoa com deficincia ou mobilidade reduzida, e para idosos, em nmero proporcional ao nmero de vagas para automveis previsto no projeto, na proporcionalidade da tabela abaixo, observado o mnimo de 1 (uma) vaga:</p>

