		<p class="corpo_decreto"><strong class="destaque-decreto">4.B.9. </strong>Devero ser fixadas vagas especiais de estacionamento de veculo destinadas ao uso por pessoa com deficincia ou mobilidade reduzida, em nmero proporcional ao nmero de vagas para automveis previstos no projeto, observado o mnimo de 1 (uma) vaga, atendendo-se a tabela, constante na Tabela do item 8.I desde decreto. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">4.B.9.1. </strong>No estacionamento coletivo com mais de 10 (dez) vagas, as vagas destinadas s pessoas com deficincia ou mobilidade reduzida devero ser acrescidas s vagas previstas. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">4.B.9.2. </strong>Devero ser sinalizadas as vagas especiais de estacionamento de veculos para idosos na proporo de 5%, conforme o Estatuto do Idoso, dentre as vagas para automveis previstas no projeto, observado o mnimo de 1 (uma) vaga. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">4.B.10. </strong>Nos teatros, cinemas, auditrios, bibliotecas, estdios, ginsios de esporte, locais de espetculos e de conferncias, e similares, sero reservados espaos livres e assentos para a pessoa com deficincia, de acordo com a capacidade de lotao da edificao, observadas as seguintes propores: </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">I -</strong> Cadeiras ou poltronas especiais para uso de Pessoas Obesas  P.O.  1% da capacidade total de assentos, atendido no mnimo 1 assento; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">II -</strong> Assento para Pessoa com Mobilidade Reduzida  P.M.R. e pessoas com deficincia visual  1% da capacidade total de assentos, atendido no mnimo 1 assento; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">III -</strong> Espao para Pessoa com Cadeira de Rodas  P.C.R.  2% da capacidade total de assentos, atendido no mnimo 1 espao reservado. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">4.B.11. </strong>Os novos hotis, pousadas e similares devem ser construdos observando-se os princpios do desenho universal, alm de adotar todos os meios de acessibilidade, conforme legislao em vigor. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">4.B.11.1. </strong>Os estabelecimentos j existentes devero disponibilizar, pelo menos, 10% (dez por cento) de seus dormitrios acessveis, garantida, no mnimo, 1 (uma) unidade acessvel.</p>

		<!-- 09_anexo-I_d03.html -->
		<div id="g-09_anexo-I_d03-box" class="ai2html">
			<div id="g-09_anexo-I_d03-01" class="g-artboard g-artboard-v4" data-min-width='960'>
				<img id="g-09_anexo-I_d03-01-img" class="g-aiImg" :src="caminho + '09_anexo-I_d03-01.png'"/>
