					<p class="g-pstyle0">AERAO E INSOLAO</p>
				</div>
				<div id="g-ai0-2" class="g-Camada_1 g-aiAbs g-aiPointText" style="top:1.651%;margin-top:-9.8px;left:90.2396%;width:115px;">
