				</div>
				<div id="g-ai0-3" class="g-Camada_1 g-aiAbs" style="top:6.0933%;left:0%;width:41.9792%;">
					<p class="g-pstyle2">CLCULO DE AFASTAMENTO (A)</p>
					<p>&nbsp;</p>
					<p>A edificao, instalao ou equipamento, a partir de 10m de altura em relao ao perfil natural do terreno deve observar afastamento contnuo, lateral e de fundo, que pode ser escalonado, e que deve ser dimensionado de acordo com frmula a seguir, respeitado o limite mnimo de 3m.</p>
				</div>
				<div id="g-ai0-4" class="g-Camada_1 g-aiAbs g-aiPointText" style="top:9.6071%;margin-top:-8.1px;left:63.8874%;width:31px;">
					<p class="g-pstyle3">H</p>
				</div>
				<div id="g-ai0-5" class="g-Camada_1 g-aiAbs g-aiPointText" style="top:11.4434%;margin-top:-8.1px;left:85.4501%;width:31px;">
					<p class="g-pstyle3">H</p>
				</div>
				<div id="g-ai0-6" class="g-Camada_1 g-aiAbs g-aiPointText" style="top:15.7838%;margin-top:-8.1px;left:69.7902%;width:88px;">
