				</div>
				<div id="g-ai0-13" class="g-Camada_1 g-aiAbs" style="top:38.8967%;left:79.4649%;width:18.8542%;">
					<p class="g-pstyle7">Se H > 10m:</p>
					<p class="g-pstyle8">rea retangular com lados iguais ou superiores a 2A por 3A</p>
				</div>
