				</div>
				<div id="g-ai0-22" class="g-Camada_1 g-aiAbs g-aiPointText" style="transform: matrix(0,-1,1,0,0,0);transform-origin: 50% 54.3112158976086%;-webkit-transform: matrix(0,-1,1,0,0,0);-webkit-transform-origin: 50% 54.3112158976086%;-ms-transform: matrix(0,-1,1,0,0,0);-ms-transform-origin: 50% 54.3112158976086%;top:50.9661%;margin-top:-11.6px;left:53.2466%;width:67px;">
					<p class="g-pstyle12">H &le; 10m</p>
				</div>
				<div id="g-ai0-23" class="g-Camada_1 g-aiAbs g-aiPointText" style="transform: matrix(0.8379,-0.5458,0.5458,0.8379,0,0);transform-origin: 50% 72.9065692342107%;-webkit-transform: matrix(0.8379,-0.5458,0.5458,0.8379,0,0);-webkit-transform-origin: 50% 72.9065692342107%;-ms-transform: matrix(0.8379,-0.5458,0.5458,0.8379,0,0);-ms-transform-origin: 50% 72.9065692342107%;top:50.5904%;margin-top:-18.1px;left:12.6076%;width:59px;">
					<p class="g-pstyle9">A1+A2</p>
				</div>
				<div id="g-ai0-24" class="g-Camada_1 g-aiAbs" style="top:55.0062%;left:15.9375%;width:29.8958%;">
					<p class="g-pstyle7">Quando os blocos forem compostos pelo mesmo embasamento, a altura (H) ser contada a partir da cota de nvel do piso de laje de cada bloco</p>
				</div>
				<div id="g-ai0-25" class="g-Camada_1 g-aiAbs" style="top:68.8621%;left:0%;width:45.8333%;">
					<p class="g-pstyle13">EDIFICAES SEM RECUO</p>
					<p class="g-pstyle14">Quando houver edificaes vizinhas sem recuos laterais admite-se a justaposio no trecho em que a altura da edificao nova for igual  altura da edificao existente. Quando ultrapassar a altura dever ser observado o afastamento (A) em funo da altura (H) medida a partir do topo da edificao existente vizinha, admitindo-se o escalonamento.</p>
					<p class="g-pstyle14">Em edificaes dispensadas de recuos de frente, a faixa de aerao e ventilao poder ser voltada para o logradouro pblico, limitando-se ao seu eixo.</p>
				</div>
				<div id="g-ai0-26" class="g-Camada_1 g-aiAbs g-aiPointText" style="top:73.1842%;margin-top:-9.8px;left:97.0703%;width:40px;">
					<p class="g-pstyle15">H3</p>
				</div>
				<div id="g-ai0-27" class="g-Camada_1 g-aiAbs g-aiPointText" style="top:73.8519%;margin-top:-9.8px;left:90.647%;width:40px;">
					<p class="g-pstyle15">H1</p>
				</div>
				<div id="g-ai0-28" class="g-Camada_1 g-aiAbs g-aiPointText" style="top:77.1073%;margin-top:-9.8px;left:81.4396%;width:39px;">
					<p class="g-pstyle15">A1</p>
				</div>
				<div id="g-ai0-29" class="g-Camada_1 g-aiAbs g-aiPointText" style="top:80.7799%;margin-top:-9.8px;left:75.6602%;width:40px;">
					<p class="g-pstyle15">H2</p>
				</div>
				<div id="g-ai0-30" class="g-Camada_1 g-aiAbs g-aiPointText" style="top:80.7799%;margin-top:-9.8px;left:71.0432%;width:39px;">
					<p class="g-pstyle15">A2</p>
				</div>
				<div id="g-ai0-31" class="g-Camada_1 g-aiAbs g-aiPointText" style="top:84.3691%;margin-top:-9.8px;left:81.8547%;width:39px;">
					<p class="g-pstyle15">A3</p>
