		</div>

		<h3 class="subsecao_lei">5 - DAS CONDIES DE AERAO E INSOLAO </h3>
		<p class="corpo_decreto"><strong class="artigo-decreto">5.A. </strong>A implantao no lote de qualquer edificao, obra complementar, mobilirio, instalao e equipamento, alm do atendimento das disposies do PDE, da LPUOS e do COE, e especialmente do item 5 do Anexo I do COE, devero observar as regras fixadas por este decreto. </p>
		<p class="corpo_lei"><strong class="artigo-lei">5.1. </strong>A edificao, instalao ou equipamento, a partir de 10 m (dez metros) de altura em relao ao perfil natural do terreno deve observar afastamento contnuo, lateral e de fundo, que pode ser escalonado, e que deve ser dimensionado de acordo com frmula a seguir, respeitado o mnimo de 3 m (trs metros).</p>
		<p class="formula"><span class="lei-formula">A</span> = (<span class="lei-formula">H</span>  6)  10</p>
		<p class="corpo_lei">onde:</p>
		<p class="formula"><span class="lei-formula">A</span> = afastamento lateral e de fundo;</p>
		<p class="formula"><span class="lei-formula">H</span> = altura da edificao em metros contados a partir do perfil do terreno.</p>
		<p class="corpo_decreto"><strong class="destaque-decreto">5.A.1. </strong>Para a aplicao do item 5.1 do Anexo I do COE, a altura H da edificao medida em metros, ser contada a partir da cota de nvel mais baixa do perfil natural do terreno referente ao plano de fachada considerado, at a cota de nvel mais alta da edificao, podendo ser adotado o escalonamento da edificao. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">5.A.1.1. </strong>Para efeito de aplicao do clculo da altura H da edificao, sero adotadas as cotas e curvas de nvel indicadas no Levantamento Topogrfico elaborado pelo responsvel tcnico. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">5.A.1.2. </strong>No clculo da altura H da edificao no sero considerados: </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">I -</strong> platibandas que envolve o telhado e o guarda corpo de proteo contra queda com at 1,20 m (um metro e vinte); </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">II -</strong> anteparos verticais como gradis, alambrados ou similar que apresentem superfcie vazada igual ou superior a 80% de sua superfcie total; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">III -</strong> tico. </p>
		<p class="corpo_lei"><strong class="artigo-lei">5.2. </strong>A distncia mnima obrigatria entre blocos de uma mesma edificao  igual  soma dos afastamentos de cada bloco calculados conforme item 5.1 deste Cdigo.</p>
		<p class="corpo_decreto"><strong class="destaque-decreto">5.A.2. </strong>A distncia mnima obrigatria entre blocos de uma mesma edificao  igual  soma dos afastamentos de cada bloco calculados conforme item 5.1 do Anexo I do COE. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">5.A.2.1. </strong>Quando os blocos de uma mesma edificao forem compostos pelo mesmo embasamento, a altura H ser contada a partir da cota de nvel do piso de laje de cada bloco. </p>
		<p class="corpo_lei"><strong class="artigo-lei">5.3. </strong>Observadas as normas pertinentes, a aerao e a insolao naturais dos compartimentos podem ser proporcionadas pelos seguintes espaos, para os quais as aberturas devem estar voltadas:</p>
		<p class="corpo_lei"><strong class="destaque-lei">I -</strong> recuo obrigatrio previsto na LPUOS;</p>
		<p class="corpo_lei"><strong class="destaque-lei">II -</strong> espao do logradouro;</p>
		<p class="corpo_lei"><strong class="destaque-lei">III -</strong> afastamento previsto no item 5.1 e 5.2 deste Cdigo;</p>
		<p class="corpo_lei"><strong class="destaque-lei">IV -</strong> rea livre descoberta interna.</p>
		<p class="corpo_decreto"><strong class="destaque-decreto">5.A.3. </strong>Nos casos de aerao e insolao naturais, proporcionadas atravs do Espao do Logradouro estabelecido no item 5.3 do Anexo I do COE, com ou sem a previso de recuo de frente, o afastamento A no poder ultrapassar a distncia entre a face da edificao e o eixo do logradouro. </p>
		<p class="corpo_lei"><strong class="destaque-lei">5.3.1. </strong>A rea livre interna ao lote deve apresentar as seguintes dimenses de acordo com a altura H da edificao contada do perfil do terreno:</p>
		<p class="corpo_lei"><strong class="destaque-lei">I -</strong> quando H igual ou inferior a 10 m (dez metros): rea mnima de 5,00 m (cinco metros quadrados) e largura mnima de 1,50 m (um metro e cinquenta centmetros);</p>
		<p class="corpo_lei"><strong class="destaque-lei">II -</strong> quando H superior a 10 m (dez metros): dimenso correspondente a um retngulo com lados iguais ou superiores, respectivamente, a 2A por 3A.</p>
		<p class="corpo_decreto"><strong class="destaque-decreto">5.A.4. </strong>A rea livre descoberta interna ao lote corresponde ao poo interno descoberto da edificao e dever apresentar as seguintes dimenses de acordo com a altura H da edificao contada do perfil natural do terreno: </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">I -</strong> rea mnima de 5,00 m (cinco metros quadrados) e largura mnima de 1,50 m (um metro e cinquenta centmetros), quando H for igual ou inferior a 10 m (dez metros); </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">II -</strong> retngulo conforme o inciso II do item 5.3.1 do Anexo I do COE, situao em que poder ser adotado o escalonamento, quando H for superior a 10 m (dez metros). </p>
		<p class="corpo_lei"><strong class="artigo-lei">5.4. </strong>No caso de rea sujeita a diretrizes urbansticas prprias, operao urbana ou outra situao onde haja a dispensa da observncia dos recuos previstos no PDE e LPUOS, fica dispensado o atendimento ao afastamento A estabelecido neste Cdigo, sendo a edificao insolada e aerada pelo espao do logradouro pblico.</p>
		<p class="corpo_lei"><strong class="artigo-lei">5.5. </strong>Quando houver edificao vizinha implantada sem atendimento de recuo nas divisas laterais e de fundo, admite-se a justaposio da nova edificao  edificao lindeira, sem prejuzo dos ndices de ocupao e aproveitamento previstos na LPUOS.</p>
		<p class="corpo_decreto"><strong class="destaque-decreto">5.A.5. </strong>Quando houver edificao vizinha implantada sem o atendimento de recuo nas divisas laterais e de fundos, admite-se a justaposio da nova edificao  edificao lindeira existente, sem prejuzo do atendimento aos ndices de aproveitamento, dimensionamento e ocupao previstos pela LPUOS. </p>
		<p class="corpo_lei"><strong class="destaque-lei">5.5.1. </strong>A justaposio fica dispensada da anlise especfica de rgo tcnico quando, no trecho da divisa onde ocorrer a justaposio, a altura da nova edificao no ultrapassar a altura da edificao lindeira existente.</p>
		<p class="corpo_decreto"><strong class="destaque-decreto">5.A.5.1. </strong>A justaposio fica dispensada da anlise especfica de rgo tcnico competente quando, no trecho da divisa onde ocorrer a justaposio, a altura da nova edificao no ultrapassar a altura da edificao lindeira existente. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">5.A.5.2. </strong>Quando adotada a justaposio e a altura da nova edificao ultrapassar a altura da edificao lindeira existente, dever ser observado o afastamento A e altura H a partir do topo da edificao existente, no trecho da justaposio, admitindo-se o escalonamento. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">5.A.6. </strong>Nas edificaes novas, os compartimentos e ambientes devero ser posicionados e dimensionados de forma a proporcionar conforto ambiental, trmico, acstico, e proteo contra a umidade, obtidos pelo adequado dimensionamento e emprego dos materiais das paredes, cobertura, pavimento e aberturas, bem como das instalaes e equipamentos conforme dimenses mnimas elencadas na <strong>tabela abaixo</strong>.</p>

