					<td rowspan="3">2,00</td>
				</tr>
				<tr>
					<td>Estar</td>
				</tr>
				<tr>
					<td>Estudo</td>
				</tr>
				<tr>
					<td>Sade</td>
					<td>Repouso</td>
					<td>2,50</td>
					<td>5,00</td>
					<td>2,00</td>
				</tr>
				<tr>
					<td rowspan="2">Educao</td>
					<td>Repouso</td>
					<td rowspan="2">2,50</td>
