		<h5><strong>Nota (a):</strong> As habitaes devero conter, no mnimo, espaos destinados a estar, repouso, instalao sanitria e cozinha.</h5>

		<p class="corpo_lei"><strong class="destaque-lei">5.6. </strong>(VETADO)</p>
			<h3 class="subsecao_lei">6 - DAS CONDIES DE SEGURANA DE USO E CIRCULAO </h3>
			<p class="corpo_lei"><strong class="artigo-lei">6.1. </strong>Toda edificao e equipamento devem atender s disposies construtivas consideradas essenciais para a segurana de uso e circulao dos usurios, estabelecidas nas normas pertinentes ao assunto.</p>
			<p class="corpo_lei"><strong class="artigo-lei">6.2. </strong>A edificao existente que no apresente condies de segurana de uso deve ser adaptada s condies de segurana de uso, exceto quando se enquadrar em uma das seguintes situaes:</p>
			<p class="corpo_lei"><strong class="destaque-lei">I -</strong> esteja desobrigada de sadas de emergncia e rotas de sada protegidas, de acordo com normas pertinentes;</p>
			<p class="corpo_lei"><strong class="destaque-lei">II -</strong> seja destinada a uso residencial;</p>
			<p class="corpo_lei"><strong class="destaque-lei">III -</strong> tenha sido objeto de adaptao s normas de segurana e se mantenha sem alterao de ordem fsica ou de utilizao em relao ao regularmente licenciado;</p>
			<p class="corpo_lei"><strong class="destaque-lei">IV -</strong> tenha sido licenciada nos termos da Lei n 11.228, de 25 de junho de 1992, e se mantenha sem alterao de ordem fsica ou de utilizao em relao ao regularmente licenciado;</p>
			<p class="corpo_lei"><strong class="destaque-lei">V -</strong> tenha sido licenciada nos termos da Lei n 8.266, de 20 de junho de 1975, e se mantenha sem alterao de ordem fsica ou de utilizao em relao ao regularmente licenciado.</p>
			<p class="corpo_lei"><strong class="artigo-lei">6.3. </strong>Toda edificao existente a ser reformada, requalificada ou reconstruda deve ser adaptada s condies de segurana de uso.</p>
			<p class="corpo_lei"><strong class="artigo-lei">6.4. </strong>O clculo da populao, o dimensionamento, a quantidade e o tipo de escada, as distncias mximas a percorrer e a necessidade de previso de elevadores de emergncia so estabelecidos em funo do uso e altura da edificao, de acordo com as normas pertinentes relativas a sadas de emergncia em edificaes.</p>
			<p class="corpo_lei"><strong class="artigo-lei">6.5. </strong>A edificao que apresentar alto potencial de risco a incndio ou emergncias deve dispor de sistema de segurana especial, constitudo do conjunto de instalaes e equipamentos que deve entrar em funcionamento e ser utilizado de forma adequada em situao de emergncia, de acordo com o regulamento.</p>
			<p class="corpo_lei"><strong class="destaque-lei">6.5.1. </strong>A edificao destinada a comrcio de venda de mercadorias em geral, prestao de servios automotivos, indstria, oficina e depsito, em funo de sua altura, rea e material predominantemente depositado, manipulado ou comercializado, deve dispor de sistema especial de segurana.</p>
			<p class="corpo_decreto"><strong class="artigo-decreto">6.A. </strong>Disposies Gerais<strong class="destaque-decreto"></strong></p>
			<p class="corpo_decreto"><strong class="destaque-decreto">6.A.1. </strong>Dever dispor de sistema especial de segurana:<strong class="destaque-decreto"></strong></p>
			<p class="corpo_decreto"><strong class="destaque-decreto">I -</strong> a edificao com mais de 12 m (doze metros) e com ao menos uma escada protegida ou  prova de fumaa, de acordo com as NTOs e ITs pertinentes. </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">II -</strong> a edificao com at 12 m (doze metros) altura e que necessitem de instalao de chuveiros automticos. </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">6.A.2. </strong>Entende-se como altura da edificao, para efeito do dimensionamento de sadas, rotas de fuga e quantidade de escadas necessrias, a diferena entre a cota de um dos pavimentos de sada e a cota do ltimo pavimento, excludo o tico, e pavimentos duplex, trplex quando no houver acesso a partir das reas comuns para estes pavimentos. </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">6.A.3. </strong>O tipo e a quantidade mnima de escadas de uma edificao so determinados pelas NTOs e ITs, em funo da altura e atividade exercida. O clculo da populao e as distncias mximas a serem percorridas podero implicar na necessidade de mais escadas, alm do mnimo. </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">6.A.4. </strong>Considera-se sistema especial de segurana o conjunto das instalaes e equipamentos, dimensionados e executados de acordo com as NTOs e ITs, os quais devero entrar em funcionamento e ser utilizados de forma adequada em situao de emergncia, sendo constitudo por: </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">I -</strong> Iluminao de emergncia; </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">II -</strong> Sinalizao de rotas de fuga e sadas;</p>
			<p class="corpo_decreto"><strong class="destaque-decreto">III -</strong> Alarme de acionamento automtico e/ou deteco de fumaa; </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">IV -</strong> Equipamento mvel de combate a incndio; </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">V -</strong> Equipamento fixo de combate a incndio com acionamento fixo ou no; </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">VI -</strong> Outros equipamentos conforme NTO e legislao complementar. </p>
			<p class="corpo_decreto"><strong class="artigo-decreto">6.B. </strong>Lotao das Edificaes </p>
			<p class="corpo_decreto">Considera-se lotao de uma edificao o nmero de usurios, calculado em funo de sua rea e utilizao. </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">6.B.1. </strong>A lotao de uma edificao ser obtida pelo somatrio das lotaes dos seus andares ou compartimentos onde se desenvolverem diferentes atividades, calculada tomando-se a rea til efetivamente utilizada no andar para o desenvolvimento de determinada atividade, dividida pelo ndice correspondente determinado nas NTOs, ITs e deste decreto. </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">6.B.2. </strong>A rea a ser considerada para o clculo da lotao ser obtida excluindo-se da rea bruta, aquela correspondente s paredes, s unidades sanitrias, aos espaos de circulao horizontais e verticais efetivamente utilizados para escoamento, vazios de elevadores, monta-cargas, passagem de dutos de ventilao e depsitos de at 30,00 m (trinta metros quadrados). </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">6.B.3. </strong>Nas edificaes destinadas a locais de reunies e centro de compras, da rea a ser considerada para o clculo da lotao, no podero ser excludos os espaos destinados  circulao horizontal que ultrapassarem 1,50 m (um metro e cinquenta) de largura. </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">6.B.4. </strong>Nas edificaes destinadas a locais de reunies devero ser estabelecidos as seguintes ndices: </p>
			<div class="anchor-tabela frame-7">
				<p class="titulo-quadro">Tabela  Lotao para locais de reunies</p>
