			<p class="corpo_decreto"><strong class="destaque-decreto">6.B.5. </strong>Em casos especiais outros clculos de lotao podero ser aceitos desde que justificados tecnicamente. </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">6.B.6. </strong>Nas salas de cinema, teatro, auditrios e restaurantes e assemelhados dotados de assentos fixos, a lotao ser correspondente ao nmero de lugares oferecidos e no em funo da relao m/pessoa. </p>
			<p class="corpo_decreto"><strong class="artigo-decreto">6.C. </strong>Dimensionamento dos Espaos de Circulao </p>
			<p class="corpo_decreto">Consideram-se espaos de circulao as escadas, as rampas e os corredores. </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">6.C.1. </strong>O dimensionamento de escadas e demais espaos de circulao dever ser feito com base nas NTOs e ITs. </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">6.C.2. </strong>A capacidade dos elevadores, escadas rolantes ou outros dispositivos de circulao por meios mecnicos, no ser considerada para efeito do clculo de escoamento do edifcio. </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">6.C.3. </strong>No pavimento de sada da edificao, os espaos de circulao sero dimensionados de acordo com a capacidade de escoamento das escadas a que do continuidade, acrescidos da populao do prprio andar que tambm venha a utilizar a via de escoamento.</p>
			<p class="corpo_decreto"><strong class="destaque-decreto">6.C.4. </strong>As portas de acesso que proporcionarem escoamento devero abrir no sentido da sada e, ao abrir, no podero reduzir as dimenses mnimas exigidas para a via de escoamento. </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">6.C.5. </strong>As portas de acesso da edificao situadas no pavimento de sada, necessrias ao escoamento da populao, devero abrir no sentido da sada, e, quando abrirem, no podero obstruir o passeio pblico. </p>
			<p class="corpo_decreto"><strong class="artigo-decreto">6.D. </strong>Disposio de escadas e sadas </p>
			<p class="corpo_decreto">Os espaos de circulao horizontal e vertical devero ser dispostos segundo a utilizao, rea, altura e lotao da edificao. </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">6.D.1. </strong>A distncia mxima a percorrer, medida em metros e tomada pelo percurso real, ser aquela estabelecida de acordo com as NTOs e ITs. </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">6.D.2. </strong>Nos compartimentos ou recintos em que a distncia de qualquer ponto at a porta de acesso for inferior a 10,00 m (dez metros), a distncia mxima prevista ser calculada a partir da porta. </p>
			<p class="corpo_decreto"><strong class="artigo-decreto">6.E. </strong>Espaos de Circulao Protegidos </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">6.E.1. </strong>Sero considerados protegidos os espaos de circulao que, por suas caractersticas construtivas, permitirem o escoamento em segurana, dos setores a que servirem, atendendo s seguintes disposies: </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">I -</strong> mantenham isolamento de qualquer outro espao interno da edificao, por meio de elementos construtivos e portas resistentes, conforme estabelecido nas NTOs; </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">II -</strong> tenham uso exclusivo como circulao, estando permanentemente desobstrudos; </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">III -</strong> contenham apenas as instalaes eltricas prprias do recinto e do sistema de segurana; </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">IV -</strong> no contenham aberturas para dutos ou galerias de instalao ou servio, excetuadas as portas dos elevadores; </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">V -</strong> tenham os revestimentos das paredes e pisos ensaiados conforme as NTOs. </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">6.E.2. </strong>Alm dos tipos de escadas mencionados nas NTOs,  considerada  prova de fumaa a escada aberta para o exterior, limitada  altura de 27 m (vinte e sete metros), sem obrigatoriedade de comunicao atravs de vestbulo/antecmara protegidos, desde que: </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">I -</strong> possua ventilao natural atravs de abertura em, pelo menos, 50% (cinquenta por cento) de seu permetro, com altura igual ou superior  metade de seu p direito; </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">II -</strong> suas aberturas estejam distanciadas, no mnimo, 5,00 m (cinco metros) de outra abertura da mesma edificao; </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">III -</strong> a face aberta da escada esteja distanciada, no mnimo, 5 m (cinco metros) de outra edificao no mesmo lote e das divisas do imvel. </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">6.E.3. </strong>Nos andares enterrados, destinados exclusivamente a estacionamento de veculos, ser dispensado o vestbulo/antecmara de acesso  escada  prova de fumaa. </p>
			<p class="corpo_decreto"><strong class="artigo-decreto">6.F. </strong>Condies Construtivas Especiais </p>
			<p class="corpo_decreto">Alm das disposies gerais, de acordo com o uso, populao e altura, as edificaes devero atender s condies construtivas especiais estabelecidas nesta seo. </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">6.F.1. </strong>Devero constituir-se em setores de incndio, delimitados por elementos resistentes ao fogo RF-120 (piso/parede) e RF-60 (portas): </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">I -</strong> os andares destinados exclusivamente a estacionamento de veculos; </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">II -</strong> as reas destinadas a abrigar as seguintes atividades, instalaes e equipamentos: </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">a)</strong> casa de mquinas ou de equipamentos que possam agravar o risco de incndio da edificao; </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">b)</strong> compartimentos em que a atividade desenvolvida possa agravar o risco de incndio inerente ao uso da edificao; </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">c)</strong> armazenagem de combustvel; </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">d)</strong> sala de medidores de energia eltrica e gs; </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">e)</strong> centrais de instrumentos contra incndio; </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">f)</strong> antecmaras ou reas de refgio. </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">6.F.1.1. </strong>A exigncia deste item poder ser substituda pela instalao de chuveiros automticos com agente extintor apropriado, no andar, setor ou compartimento em que ocorrer a situao. </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">6.F.2. </strong>Quando, em funo do tipo de proteo dos espaos de circulao, for recomendvel manter abertas as portas resistentes ao fogo, estas devero estar acopladas a sistema de fechamento automtico ou acionadas por central de controle. </p>
			<p class="corpo_decreto"><span class="bolinha">6.F.3.</span><strong class="destaque-decreto"> </strong>As edificaes destinadas a locais de reunio, que abriguem salas de cinema, teatros e auditrios dotados de assentos fixos dispostos em filas, devero atender aos seguintes requisitos: </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">I -</strong> mximo de 16 (dezesseis) assentos em fila, quando houver corredores em ambos os lados; </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">II -</strong> mximo de 8 (oito) assentos em fila, quando houver corredor em um nico lado; </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">III -</strong> setorizao, atravs de corredores transversais, que disporo de, no mximo, 14 (catorze) filas; </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">IV -</strong> vo livre entre o assento e o encosto do assento fronteiro de, no mnimo, 0,50 m (cinquenta centmetros); </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">V -</strong> vo livre de no mnimo de 1,20 m (um metro e vinte centmetros) entre o assento da ultima fileira e o fundo da sala; </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">VI -</strong> vo livre de no mnimo 1,70 m (um metro e setenta centmetros) entre o assento da primeira fileira e o palco. </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">6.F.4. </strong>Para assentos e espaos destinados a pessoas com deficincia ou mobilidade reduzida devero ser observados os requisitos tcnicos constantes nas NTOs de acessibilidade em especial a NBR 9050 ou outra que vier a suced-la. </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">6.F.5. </strong>Em funo do tipo de edificao, natureza dos materiais, altura, populao e condies de segurana apresentadas, a Prefeitura poder admitir a dispensa de determinados equipamentos e instalaes que se tornem desnecessrios em face da existncia de outras instalaes de segurana equivalentes.</p>
			<p class="corpo_decreto"><strong class="destaque-decreto">6.F.6. </strong>Os equipamentos cadastrados devero ser instalados conforme normas tcnicas aplicveis, garantindo a segurana e integridade do entorno, atravs de proteo adequada contra vazamentos, incndios, emanao de gases e vapores nocivos, odores ou temperaturas extremas, bem como guardar as distncias mnimas abaixo indicadas: </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">I -</strong> Tanques enterrados devero respeitar os afastamentos mnimos de 1,50 m (um metro e meio) do(s) logradouro(s), divisas do lote e edificaes e de 1,00 m (um metro) entre si; </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">II -</strong> Tanques areos devero respeitar os afastamentos mnimos de 3,00 m (trs metros) do(s) logradouro(s), das divisas do lote e entre si; </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">III -</strong> Bombas de abastecimento de lquidos, dispensers de abastecimento de gs e filtros de diesel devero respeitar o afastamento mnimo de 4,50 m (quatro metros e meio) do(s) logradouro(s) e de 1,50 m (um metro e meio) das divisas do lote e das edificaes; </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">IV -</strong> Elevadores para troca de leo e equipamentos de lavagem dos veculos, quando descobertos, devero respeitar o afastamento mnimo de 3,00 m (trs metros) do(s) logradouro(s) e das divisas do lote; </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">V -</strong> As terminaes dos respiros devero respeitar o afastamento mnimo de 3,00 m (trs metros) das divisas do lote. </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">6.F.6.1. </strong>Os equipamentos j aprovados, cujas distncias no atendam ao disposto neste Decreto, podero ser substitudos por outros mais modernos, nos mesmos locais em que foram aprovados. </p>
			<p class="corpo_decreto"><strong class="artigo-decreto">6.G. </strong>Adaptao de edificaes existentes </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">6.G.1. </strong>Em caso de adaptao de edificaes existentes s condies de segurana de uso, e que necessitem de mais de uma escada protegida, de acordo com as NTOs e ITs, a metade delas dever atender s caractersticas exigidas para este tipo de escada, podendo as demais serem substitudas por: </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">I -</strong> interligao entre blocos no mesmo lote ou entre edificaes vizinhas, por passarela ou passadio protegido; </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">II -</strong> reas de refgio, delimitadas por elementos construtivos e portas resistentes, conforme estabelecido nas NTOs e ITs, situadas, no mnimo, a cada quatro andares, com capacidade para abrigar 50% (cinquenta por cento) da lotao dos andares superiores, na proporo de 0,50 m (cinquenta decmetros quadrados) por pessoa, interligadas  escada protegida. </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">6.G.2. </strong>Sero aceitas escadas em leque ou em curva, inclusive como escada protegida ou  prova de fumaa, limitando-se a sua capacidade de escoamento, respectivamente,  metade, se em leque, e a dois teros, se em curva, de uma escada com lances retos. </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">6.G.3. </strong>As condies de segurana em locais de grande concentrao de pessoas sero regradas por Portaria. </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">6.G.4. </strong>A previso do elevador de emergncia no uso residencial multifamiliar ser obrigatria quando a altura da edificao for superior a 80 m (oitenta metros). </p>
			<h3 class="subsecao_lei">7 - DO EQUIPAMENTO MECNICO </h3>
			<p class="corpo_decreto"><strong class="destaque-decreto">7.A.1. </strong>Na edificao constituda de hall privativo que no disponha de interligao com as escadas, o elevador dever ser dotado de sistema de segurana (gerador de energia eltrica) que garanta a sua movimentao, mesmo em caso de pane no sistema ou falta de energia eltrica. </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">7.A.2. </strong>Todos os elevadores instalados para transporte de passageiros, cargas e automveis, independente do percurso e quantidades de paradas, devero possuir dispositivo de operao de emergncia em incndio, de fase 1, obrigatrio para todos os elevadores, e de fase 2, aos elevadores de emergncia, conforme estabelecido por Portaria. </p>
			<p class="corpo_lei"><strong class="artigo-lei">7.1. </strong>Todo equipamento mecnico, independentemente de sua posio no imvel, deve ser instalado de forma a no transmitir, ao imvel vizinho e ao logradouro pblico, rudo, vibrao e temperatura em nveis superiores aos previstos nos regulamentos oficiais prprios.</p>
			<p class="corpo_lei"><strong class="destaque-lei">7.1.1. </strong>O guindaste, ponte rolante e outros equipamentos assemelhados devem observar o afastamento mnimo das divisas estabelecido na LPUOS em funo da sua altura em relao ao perfil do terreno.</p>
			<p class="corpo_decreto"><strong class="artigo-decreto">7.B. </strong>O guindaste, ponte rolante e outros equipamentos assemelhados devero atender o afastamento mnimo das divisas estabelecido pela LPUOS em funo da sua altura em relao ao perfil do terreno.</p>
			<p class="corpo_lei"><strong class="artigo-lei">7.2. </strong>O elevador e os demais equipamentos mecnicos de transporte vertical no podem se constituir no nico meio de circulao e acesso do pedestre  edificao.</p>
			<p class="corpo_lei"><strong class="artigo-lei">7.3. </strong>A edificao com mais de 5 (cinco) andares ou que apresente desnvel superior a 12,00 m (doze metros) contado do piso do ltimo andar at o piso do andar inferior, includos os pavimentos destinados a estacionamento, deve ser servida por elevador de passageiro, observadas as seguintes condies:</p>
			<p class="corpo_lei"><strong class="destaque-lei">I -</strong> no mnimo, 1 (um) elevador, em edificao com at 10 (dez) andares ou com desnvel igual ou inferior a 24,00 m (vinte e quatro metros);</p>
			<p class="corpo_lei"><strong class="destaque-lei">II -</strong> no mnimo, 2 (dois) elevadores, em edificao com mais de 10 (dez) andares ou com desnvel superior a 24,00 m (vinte e quatro metros).</p>
			<p class="corpo_decreto"><strong class="artigo-decreto">7.C. </strong>Os elevadores de passageiros devero atender o dimensionamento e a localizao definidos pela NTO, alm das disposies estabelecidas pelo COE. </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">7.C.1. </strong>O hall de acesso a no mnimo um elevador, em todos os pavimentos, excludo o trreo, dever estar interligado  circulao vertical da edificao (escadas e/ou rampas) por espao de circulao coletiva (largura mnima de 1,20 m), podendo os demais elevadores estar interligados s escadas e/ou rampas por espao de circulao privativa (largura mnima de 0,80 m) de uso comum ao edifcio, atendidas as condies de segurana estabelecidas no COE e neste decreto. </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">7.C.2. </strong>Os elevadores acessveis de passageiros devero atender a quantidade, o dimensionamento e a localizao definidos pela NBR 9050 e NBR NM 313, ou aquelas que vierem substitu-las. </p>
			<p class="corpo_lei"><strong class="destaque-lei">7.3.1. </strong>Todo andar deve ser servido pelo nmero mnimo de elevadores exigidos, inclusive aquele destinado a estacionamento.</p>
			<p class="corpo_decreto"><strong class="artigo-decreto">7.D. </strong>Os elevadores podem estar em prumadas diferentes, desde que respeitado o clculo de trfego estabelecido nas NTOs, excetuado o elevador de emergncia, que dever atender a todos os andares na mesma caixa de corrida, inclusive subsolos. </p>
			<p class="corpo_decreto"><strong class="artigo-decreto">7.E. </strong>Os equipamentos mecnicos, independentemente do porte, no sero considerados como rea edificada. </p>
			<p class="corpo_decreto"><strong class="artigo-decreto">7.F. </strong>A quantidade mnima de elevadores estabelecido no COE poder ser revista por legislao especifica. </p>
			<p class="corpo_decreto"><strong class="artigo-decreto">7.G. </strong>Em casos de adaptaes da edificao podero ser aceitos para fins de acessibilidade, plataforma de elevao vertical motorizada e elevador de uso exclusivo, atendendo dimensionamento e localizao definidos pela NBR 9050 ou outra que a substituir, normas tcnicas e legislao especficas para estes equipamentos.</p>
			<p class="corpo_lei"><strong class="destaque-lei">7.3.2. </strong>No cmputo dos andares, no clculo do desnvel e na obrigatoriedade de parada, no so considerados o tico, o pavimento de cobertura sem utilizao, o andar destinado  zeladoria e o andar de uso privado de andar contguo.</p>
			<h3 class="subsecao_lei">8 - DO ESTACIONAMENTO </h3>
			<p class="corpo_lei"><strong class="artigo-lei">8.1. </strong>Os espaos para acesso, circulao e estacionamento de veculos devem ser projetados, dimensionados e executados livres de qualquer interferncia estrutural ou fsica.</p>
			
			<p class="corpo_decreto"><span class="bolinha">8.A.</span><strong class="destaque-decreto"> </strong>Dever ser garantido o acesso a pedestres independente da circulao de veculos, entre o alinhamento do imvel e o ingresso  edificao, por faixa exclusiva com largura mnima de 1,20 m (um metro e vinte centmetros), excetuados dessa exigncia as residncias unifamiliares e o conjunto de habitaes agrupadas horizontalmente. </p>
			<p class="corpo_decreto"><strong class="artigo-decreto">8.B. </strong>A acomodao transversal do acesso entre o perfil do logradouro e os espaos de circulao e estacionamento dever ser feita dentro do imvel, de forma a no criar degraus ou desnveis abruptos na calada. </p>
			<p class="corpo_decreto"><strong class="artigo-decreto">8.C. </strong>O rebaixamento de guia destinado ao acesso de veculos no poder exceder a 50% (cinquenta por cento) da extenso da testada do imvel,  exceo da edificao residencial unifamiliar e do conjunto de habitaes agrupadas horizontalmente com frente e acesso para logradouro pblico.</p>
			<p class="corpo_decreto"><strong class="artigo-decreto">8.D. </strong>As faixas de circulao de veculo devem apresentar dimenso para cada sentido de trfego de no mnimo:</p>
			<p class="corpo_decreto"><strong class="destaque-decreto">I -</strong> 2,75 m (dois metros e setenta e cinco centmetros) de largura e 2,30 m (dois metros e trinta centmetros) de altura livre de passagem quando destinada  circulao de automvel e utilitrio;</p>
			<p class="corpo_decreto"><strong class="destaque-decreto">II -</strong> 3,50 m (trs metros e cinquenta centmetros) de largura e 3,50 m (trs metros e cinquenta centmetros) de altura livre de passagem quando destinada  circulao de caminho e nibus.<strong class="subtitulo_txt"></strong></p>
			<p class="corpo_decreto"><strong class="destaque-decreto">8.D.1. </strong> admitida uma nica faixa de circulao quando esta se destinar, no mximo, ao trnsito de 60 (sessenta) veculos em edificaes de uso residencial e 30 (trinta) veculos nos demais usos.</p>
			<p class="corpo_decreto"><strong class="destaque-decreto">8.D.2. </strong>No caso da faixa de circulao servir a automvel, utilitrio e caminho prevalece o parmetro mais restritivo.</p>
			<p class="corpo_decreto"><strong class="destaque-decreto">8.D.3. </strong>As faixas de circulao em curva tero largura aumentada em razo do raio interno, expresso em metros, e da declividade, expressa em porcentagem, tomada no desenvolvimento interno da curva, conforme disposto na <strong>tabela abaixo</strong>.</p>

