			<p class="corpo_lei"><strong class="artigo-lei">8.2. </strong>O acesso de veculos em lote de esquina deve distar, no mnimo, 6,00 m (seis metros) do incio do ponto de encontro do prolongamento dos alinhamentos dos logradouros, salvo na edificao residencial unifamiliar e no conjunto de habitaes agrupadas horizontalmente.</p>
			<p class="corpo_lei"><strong class="destaque-lei">8.2.1. </strong>Em virtude das caractersticas do logradouro, a distncia estabelecida no subitem 8.2 pode ser alterada a critrio da Prefeitura.</p>
			<p class="corpo_lei"><strong class="artigo-lei">8.3. </strong>A rampa de veculo deve observar recuo de 4,00 m (quatro metros) do alinhamento do logradouro para seu incio e apresentar declividade mxima de:</p>
			<p class="corpo_lei"><strong class="destaque-lei">I -</strong> 20% (vinte por cento), quando destinada  circulao de automvel e utilitrio;</p>
			<p class="corpo_lei"><strong class="destaque-lei">II -</strong> 12% (doze por cento), quando destinada  circulao de caminho e nibus.</p>
			<p class="corpo_decreto"><strong class="artigo-decreto">8.E. </strong>A rampa de veculo dever atender ao disposto no item 8.3 do Anexo I do COE, exceto nas residncias unifamiliares, casas geminadas e superpostas, as quais podero iniciar a rampa a partir do alinhamento, desde que atendidos os demais parmetros urbansticos. </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">8.E.1. </strong>As rampas para automveis e utilitrios em residncias unifamiliares, casas geminadas ou superpostas e o acesso s garagens privativas atravs de vias internas de conjunto residencial horizontal podero ter declividade mxima de 25% (vinte e cinco por cento). </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">8.E.2. </strong>A seo transversal da rampa dever apresentar declividade de no mximo 2% (dois por cento). </p>
			<p class="corpo_lei"><strong class="destaque-lei">8.3.1. </strong>O piso entre o alinhamento e o incio da rampa pode ter inclinao de at 5% (cinco por cento).</p>
			<p class="corpo_lei"><strong class="artigo-lei">8.4. </strong>O piso do estacionamento pode ter inclinao de, no mximo, 5% (cinco por cento).</p>
			<p class="corpo_lei"><strong class="artigo-lei">8.5. </strong>Deve ser previsto espao de manobra e estacionamento de veculo de forma que essas operaes no sejam executadas no espao do logradouro pblico.</p>
			<p class="corpo_decreto"><strong class="artigo-decreto">8.F. </strong>O estacionamento coletivo dever ter rea de acumulao, acomodao e manobra de veculo dimensionada de forma a comportar no mnimo 3% (trs por cento) de sua capacidade. </p>
			<p class="corpo_decreto"><strong class="artigo-decreto">8.G. </strong>No caso de estacionamento com acesso controlado, o espao de acumulao dever estar situado entre o alinhamento e o local do controle. </p>
			<p class="corpo_decreto"><strong class="artigo-decreto">8.H. </strong>Ser admitida, exceto no caso das vagas de estacionamento de veculos de pessoas com deficincia, a manobra de at dois veculos para liberar a movimentao de um terceiro. </p>
			<p class="corpo_decreto"><strong class="artigo-decreto">8.I. </strong>A dimenso da vaga de estacionamento  estabelecida em funo do tipo de veculo, enquanto a do espao de manobra e acesso  dada em funo do ngulo formado pelo comprimento da vaga e a faixa de acesso, de acordo com a <strong>tabela a seguir</strong>.</p>
			<p class="corpo_decreto"><strong class="artigo-decreto">8.J. </strong>As vagas para pessoas com deficincia devero ser dimensionadas com 2,50 m (dois metros e cinquenta centmetros) de largura para o veculo acrescidas de 1,20 m (um metro e vinte centmetros) para a faixa de transferncia, sendo admitido que duas vagas compartilhem o mesmo espao de transferncia. </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">8.J.1. </strong>O piso da vaga de estacionamento de veculo de pessoa com deficincia poder ter inclinao mxima de 3% (trs por cento). </p>
			<p class="corpo_lei"><strong class="artigo-lei">8.6. </strong>Devem ser previstas vagas para motocicletas, em funo do tipo de estacionamento, em acrscimo s vagas destinadas para automveis, observada a proporcionalidade mnima de 5% (cinco por cento) em relao quelas.</p>

