			<p class="corpo_lei"><strong class="artigo-lei">8.7. </strong>Admite-se a utilizao de equipamento mecnico para estacionamento de veculos, observadas as normas tcnicas aplicveis e as disposies da LOE e LPUOS sobre acesso e circulao de veculos entre o logradouro pblico e o imvel.</p>
			<p class="corpo_decreto"><strong class="artigo-decreto">8.K.</strong><strong class="destaque-decreto"> </strong> admitida a utilizao de equipamento mecnico para estacionamento e circulao de veculos, como atividade principal ou complementar a outra atividade, desde que permitidas na LPUOS. </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">8.K.1. </strong>As exigncias para acesso e circulao de veculos entre o logradouro pblico e a edificao devero observar as disposies da LPUOS e da LOE. </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">8.K.2. </strong>No se aplicam os itens 8.D, 8.H e 8.I deste Anexo no interior dos estacionamentos que utilizem equipamentos mecnicos automatizados para a guarda de veculos.<strong class="subtitulo_txt"></strong></p>
			<p class="corpo_lei"><strong class="artigo-lei">8.8. </strong>O espao destinado a bicicletas previsto na LPUOS ser dimensionado e configurado de acordo com regulamentao.</p>
			<p class="corpo_decreto"><strong class="artigo-decreto">8.L. </strong>Nos espaos de estacionamento, as vagas destinadas a bicicletas podero estar instaladas horizontalmente ou verticalmente presas em suporte, devendo estar localizadas preferencialmente no piso mais prximo do logradouro pblico. </p>
			<p class="corpo_decreto"><strong class="artigo-decreto">8.M. </strong>Nos estacionamentos privativos e coletivos sero permitidas coberturas de polietileno de alta densidade sobre estrutura tubular, para sombreamento e proteo dos veculos. </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">8.M.1. </strong>A cobertura de polietileno dever apresentar estabilidade, segurana, resistncia, conforto trmico e acstico e resistncia ao fogo de acordo com as NTOs, bem como permeabilidade possibilitando a passagem do ar e da gua. </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">8.M.2. </strong>As coberturas no podero ser executadas sobre os acessos e circulao de veculos, nem sobre os recuos de frente exigidos pela LPUOS. </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">8.M.3. </strong>As coberturas podero ocupar os recuos laterais previstos pela LPUOS, desde que no ultrapassem a altura de 2,30 m junto a essas divisas. </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">8.M.4. </strong>Dentro dos limites a seguir estabelecidos, as coberturas no sero computadas para fins de clculo de taxa de ocupao, cota de garagem e coeficiente de aproveitamento previstos pela LPUOS: I. 70% da rea do terreno, quando destinadas a atividade estacionamento; II. 25% da rea do terreno, quando o estacionamento constituir-se complemento da atividade principal. </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">8.M.4.1. </strong>Quando houver divergncia entre a rea constante do documento de propriedade apresentada e as apuradas no levantamento topogrfico ser considera a menor rea apurada, observando o remanescente do imvel quando o terreno for objeto de doao de rea para Municipalidade. </p>
			<p class="corpo_decreto"><strong class="destaque-decreto">8.M.5. </strong>Poder ser aceito outro material semelhante ao disposto do item 8.M desde que assegurado tecnicamente por profissional habilitado que atenda as NTOs. </p>

