    <h1 class="cap_lei">Simplificando o Licenciamento em So Paulo</h1>
    <p>O Cdigo de Obras e Edificaes  COE, sancionado pelo Prefeito Joo Dria em maio de 2017 e em vigor desde julho de 2017, apresenta um novo modelo de licenciamento de edificaes. O COE vem para resolver entraves que perduram por anos e dificultam a transformao de So Paulo em sua vocao de cidade dinmica e global.</p>
	<p>A primeira conquista deste Cdigo  a simplificao. Com base na experincia administrativa da SMUL em liberar edificaes, o texto da lei tambm detalhou procedimentos internos para facilitar e desburocratizar a anlise dos projetos pelos tcnicos. A nova lei est menos burocrtica e em consonncia com as diretrizes do Plano Diretor Estratgico e da Lei de Parcelamento, Uso e Ocupao do Solo.  Visa, assim, deixar os procedimentos e o processo de licenciamento mais simples, tanto para o cidado, quanto para os tcnicos, alm de contribuir para o crescimento e desenvolvimento da cidade.</p>
	<p>Com este COE, os detalhes internos das edificaes ficam a critrio do proprietrio e do autor do projeto, que agora possuem responsabilidades explcitas. Deste modo, a Prefeitura poder se focar nos aspectos urbansticos, ambientais, de sustentabilidade, acessibilidade e segurana dos empreendimentos.</p>
	<p>Hoje, na administrao pblica, perseguimos o princpio da transparncia. Com os regramentos administrativos mais claros, existem poucas exigncias tcnicas complementares, o que d agilidade ao processo. Em relao aos cdigos anteriores, este no legisla quando j existem normas tcnicas, evitando sobreposies de normas, alm de possibilitar que o profissional habilitado exera seu oficio com responsabilidade.</p>
	<p>Este COE traz avanos significativos. Foram revistos os prazos, indeferimentos, instncias, alm de outros assuntos importantes que tornaram a legislao edilcia mais moderna. Agora h poucas regras edilcias e urbansticas efetivamente relevantes. A racionalizao e agilizao; a responsabilizao de empreendedores e profissionais; a modernizao e informatizao dos procedimentos so os principais destaques a lei.</p>
	<p>Com o novo COE, a Secretaria Municipal de Urbanismo e Licenciamento est mais perto de atingir sua meta de modernizar o licenciamento na cidade de So Paulo. A expectativa  de diminuir consideravelmente o tempo de licenciamento para os empreendimentos na cidade, tornar os processos eletrnicos, e assim, ter projetos com maior qualidade no Municpio.</p>
	<p> </p>
	<p><strong>Heloisa Maria de Salles Penteado Proena</strong><br />Secretria Municipal de Urbanismo e Licenciamento</p>

	<h1>Apresentao</h1>
	<h2>O que  um Cdigo de Obras?</h2>
	<p>O Cdigo de Obras e Edificaes (COE)  o instrumento que disciplina as regras gerais de licenciamento da atividade edilcia, bem como de fiscalizao da execuo, manuteno e utilizao de obras e equipamentos. Alm disso, estabelece normas edilcias especficas a serem observadas no Municpio de So Paulo, em complemento s fixadas por Normas Tcnicas relacionadas  matria.</p>
	<p>O COE do Municpio de So Paulo, aprovado e sancionado em 09 de maio de 2017, vem para completar o Marco Regulatrio Municipal, ao lado do Plano Diretor Estratgico (PDE) e da Lei de Parcelamento, Uso e Ocupao do Solo (LPUOS), revisados recentemente. </p>
	<p>No novo COE os componentes prioritrios de avaliao so urbansticos, ambientais e de vizinhana. Asseguram que a Prefeitura mantenha seu compromisso em estabelecer instrumentos de planejamento urbano, capazes de organizar melhor os espaos da cidade de modo a torn-la um lugar mais agradvel para se viver.</p>
	<h2>Como foi o processo de reviso?</h2>
	<p>Considerando que o cdigo anterior encontra-se em vigor h 25 anos e com base na experincia prtica da sua aplicao, a Prefeitura de So Paulo iniciou um processo de avaliao e atualizao dessas normas, tendo em vista oferecer resposta a novas demandas relacionadas  produo e consumo do ambiente construdo, bem como adequao ao marco atual da disciplina urbanstica, inaugurado com a aprovao do novo Plano Diretor Estratgico  PDE.</p>
	<p>A primeira providncia desse trabalho foi a elaborao e consolidao de todas as alteraes e incluses promovidas no Cdigo de Obras e Edificaes, desde junho de 1992 at o presente. </p>
	<p>A partir do texto compilado, iniciou-se o processo de reviso do COE no mbito da <strong>Secretaria Municipal de Licenciamento  SEL</strong><sup class="referencias-notas">1</sup>, atual Secretaria Municipal de Urbanismo e Licenciamento  SMUL, envolvendo representantes da Secretaria Executiva dos rgos Colegiados (SEC), Coordenadorias de Edificao Residencial (RESID), Edificao de Servios e Uso institucional (SERVIN), Edificao de Uso Comercial e Industrial (COMIN), Parcelamento do Solo e Habitao de Interesse Social (PARHIS), Atividade Especial e Segurana de Uso (SEGUR) e Gabinete da Secretaria.</p>

	<div id="box_00_apresentacao" class="glossario-box">
		<p class="corpo-referencias-pequeno"><sup>1</sup> Conforme Decreto N 57.576, a Secretaria Municipal de Licenciamento (SEL) teve suas atribuies fundidas com a Secretaria Municipal de Desenvolvimento Urbano (SMDU), tornando-se em janeiro de 2017 a atual Secretaria Municipal de Urbanismo e Licenciamento (SMUL).</p>
		<p class="corpo-referencias-pequeno">O mesmo decreto alterou as seguintes denominaes:	</p>
		<p class="corpo-referencias-pequeno">Secretaria Municipal das Subprefeituras para Secretaria Municipal das Prefeituras Regionais (SMPR);</p>
		<p class="corpo-referencias-pequeno">Secretaria Municipal dos Negcios Jurdicos para Secretaria Municipal de Justia (SMJ)</p>
		<p class="corpo-referencias-pequeno">Secretaria Municipal dos Transportes para Secretaria Municipal de Mobilidade e Transportes (SMT)</p>
		<p class="corpo-referencias-pequeno">Secretaria Municipal de Infraestrutura Urbana para Secretaria Municipal de Servios e Obras (SMSO)</p>
		<p class="corpo-referencias-pequeno">Secretaria Municipal de Finanas e Desenvolvimento Econmico para Secretaria Municipal da Fazenda (SF)</p>
		<p class="corpo-referencias-pequeno">Secretaria Municipal da Pessoa com Deficincia e Mobilidade Reduzida para Secretaria Municipal da Pessoa com Deficincia (SMPED)</p>
	</div>

	<p>Em paralelo, realizaram-se rodadas de discusso com entidades representativas de profissionais ligados  engenharia e arquitetura, do setor imobilirio e da construo civil, que agregam a maior parte do pblico que, necessariamente, utiliza o COE na elaborao dos projetos submetidos ao licenciamento, em especial: Associao Brasileira dos Escritrios de Arquitetura (ASBEA), Sindicato das Empresas de Compra, Venda, Locao e Administrao de Imveis Residenciais e Comerciais de So Paulo (SECOVI-SP) e Sindicato da Indstria da Construo Civil do Estado de So Paulo (SINDUSCON-SP).</p>
	<p>Em novembro de 2013 foi apresentado ao Sr. Prefeito um plano de trabalho contendo os princpios orientadores do novo Cdigo: (<strong>1) Simplificao, (2) Clareza, (3) Estrutura da Lei, (4) Responsabilizao, (5) Atualizao, (6) Adequao ao novo Plano Diretor</strong>, conforme descrio ao lado.</p>

