		<div id="g-ai0-7" class="g-img01 g-aiAbs" style="top:25.974%;left:37.9705%;width:25.7292%;">
			<p>O novo texto deve ter regras claras, permitindo objetividade na sua aplicao e reduzindo ao mximo a necessidade de interpretao, seja por tcnicos especializados ou por colegiados criados com essa nalidade.</p>
		</div>
