			<p>Conside<span class="g-cstyle3">r</span>ando os limi<span class="g-cstyle4">t</span>es da ca<span class="g-cstyle5">p</span>acidade de cont<span class="g-cstyle6">r</span><span class="g-cstyle4">o</span>le do <span class="g-cstyle7">P</span>oder P<span class="g-cstyle8">b</span>lico s<span class="g-cstyle8">o</span>b<span class="g-cstyle7">r</span>e o p<span class="g-cstyle7">r</span>ocesso de dese<span class="g-cstyle9">n</span><span class="g-cstyle4">v</span><span class="g-cstyle8">o</span>lvimen<span class="g-cstyle4">t</span>o u<span class="g-cstyle10">r</span><span class="g-cstyle11">b</span>an<span class="g-cstyle12">o</span>, op<span class="g-cstyle4">t</span>ou-se por uma p<span class="g-cstyle7">r</span>opos<span class="g-cstyle13">t</span>a <span class="g-cstyle6">r</span>estri<span class="g-cstyle13">t</span>a a <span class="g-cstyle7">r</span>eg<span class="g-cstyle14">r</span>as edilcias e u<span class="g-cstyle10">r</span><span class="g-cstyle11">b</span>ansticas e<span class="g-cstyle15">f</span>et<span class="g-cstyle5">iv</span>amen<span class="g-cstyle4">t</span>e <span class="g-cstyle7">r</span>ele<span class="g-cstyle5">v</span>an<span class="g-cstyle4">t</span>es.</p>
