			<p class="g-pstyle1">PLANO DIRETOR E LPUOS</p>
		</div>
		<div id="g-ai0-16" class="g-img01 g-aiAbs" style="top:71.3105%;left:69.8298%;width:24.4792%;">
			<p>Considerando a nova disciplina urbanstica do municpio, coloca-se como necessria e oportuna a atualizao e adequao do COE aos novos marcos legais.</p>
		</div>
		<div id="g-ai0-17" class="g-img01 g-aiAbs" style="top:71.4286%;left:37.76%;width:25.7292%;">
			<p>O novo COE deve estar em consonncia com os processos de modernizao administrativa, voltados para a informatizao do licenciamento de obras, bem como atualizado em relao a novas tecnologias construtivas, demandas da sociedade e sustentabilidade ambiental. Deve ainda abrir mo de exigncias edilcias j regradas por Normas Tcnicas.</p>
		</div>
		<div id="g-ai0-18" class="g-img01 g-aiAbs" style="top:71.5466%;left:3.0725%;width:25.7292%;">
			<p>Alm de explicitar claramente as responsabilidades de proprietrio, possuidor, responsvel tcnico pelo projeto e responsvel tcnico pela obra, o contedo do novo COE deve reetir essa disposio, evitando excesso de exigncias quanto a detalhes dos projetos que no cabem  municipalidade aferir ou controlar.</p>
		</div>
	</div>
	</div>

	<p>Com base nessas premissas, o grupo tcnico formado junto  SEL elaborou uma primeira minuta que foi encaminhada  apreciao das demais secretarias envolvidas com o tema: Secretaria Municipal das Subprefeituras (SMSP), Secretaria Municipal de Desenvolvimento Urbano (SMDU), Secretaria Municipal dos Negcios Jurdicos (SNJ), Secreta ria Municipal dos Transportes (SMT), Secretaria Municipal do Verde e do Meio Ambiente (SVMA), Secretaria Municipal de Infra-estrutura Urbana (SIURB), Secretaria Municipal da Pessoa com Deficincia (SMPED), Secretaria Municipal de Finanas e Desenvolvimento Econmico (SF).</p>
	<p>Mediante sugestes via e-mail e aps reunies com cada Secretaria envolvida, a minuta foi aperfeioada e complementada. As contribuies que no foram incorporadas ao texto da lei foram compiladas para um futuro decreto regulamentador. Novamente procedeu-se  apresentao e discusso das alteraes com as entidades profissionais e empresariais parceiras (ASBEA, SECOVI, SINDUSCON, etc.).</p>
	<p>Dentre as principais alteraes e inovaes incorporadas  minuta do Projeto de Lei destacam-se:</p>
	<ul>
		<li>Previso de regras especficas para requalificaes de edificaes existentes;</li>
		<li>Reduo de exigncias tcnicas j disciplinadas por Normas Tcnicas;</li>
		<li>Previso de licenciamento com base em projeto simplificado, cabendo  anlise do rgo licenciador a verificao da compatibilidade do projeto proposto com as normas urbansticas e ambientais da cidade, incluindo melhoramentos virios, reas de preservao permanente, manejo arbreo, bem como segurana de uso, acessibilidade e condies de aerao da edificao; </li>
		<li>Responsabilizao de empreendedores e profissionais envolvidos no projeto e na obra, sendo o proprietrio e o possuidor do imvel, juntamente com o responsvel tcnico pelo projeto e com responsvel tcnico pela obra, solidariamente responsveis pelo correto atendimento  legislao municipal, normas tcnicas oficiais pertinentes e correta execuo da obra.</li>
		<li>No considerao, para efeito do licenciamento de obras, dos planos de melhoramento virio anteriores a 1988 no executados e sem decreto de utilidade pblica em vigor;</li>
		<li>Considerao do traado real dos melhoramentos virios j executados para efeito do licenciamento de obras;</li>
		<li>Eliminao da obrigatoriedade de licenciamento de atividades de baixo impacto urbanstico, tais como: obras complementares at 30 m, muros, alterao interna de unidade autnoma, reparo e limpeza, restauro de bem tombado com licena  especfica do rgo de preservao;</li>
		<li>Restrio da figura do projeto modificativo a pequenas alteraes em relao ao projeto j licenciado, em conformidade com o que foi estabelecido pelo PDE e LPUOS;</li>
		<li>Consolidao do cadastro de edificaes regulares com base nos dados de regularidade existentes;</li>
		<li>Diminuio do nmero de instncias de recurso visando abreviar as decises;</li>
		<li>Definio dos valores das taxas de anlise de forma progressiva em funo da complexidade dos projetos e da participao de maior nmero de tcnicos e secretarias na deciso dos grandes projetos, bem como iseno para obras residenciais de pequeno porte, habitao de interesse social e obras da administrao pblica direta e indireta.</li>
	</ul>
	<p>Aps a sano da Lei 16.642/2017, o Grupo de Trabalho da SMUL elaborou o Decreto regulamentador n 57.776/17 no prazo de 60 dias, com a colaborao das mesmas entidades que haviam participado da elaborao da Lei.</p>
	<p>O resultado deste processo pode ser conferido a seguir. Na primeira parte, com o objetivo de facilitar a compreenso, o leitor encontra o texto da lei combinado com o Decreto Regulamentador n 57.776/17. Na sequncia, a Portaria n 221 indica a documentao necessria e os padres de apresentao dos projetos. Na parte final, h o ndice remissivo, no qual esto listados os principais termos que compem o texto da lei e o decreto, permitindo que o leitor realize a busca por assuntos especficos.</p><br />
	<p><strong>Pedro Luiz Ferreira da Fonseca</strong></p>

