		<h1 class="cap_lei">CAPTULO IV  DOS PROCEDIMENTOS ADMINISTRATIVOS</h1>

		<h2 class="secao_lei">Seo I - Dos Procedimentos Gerais</h2>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 55. </strong>O pedido instrudo pelo interessado deve ser analisado conforme a sua natureza, observadas as normas municipais, em especial as prescries da LOE, PDE, LPUOS, sem prejuzo da observncia das disposies estaduais e federais pertinentes.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 1 </strong>O pedido deve ser instrudo com todos os elementos necessrios  sua apreciao, nos termos das normas legais e regulamentares.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 2 </strong>Todos os documentos exigidos para a instruo dos pedidos podem ser substitudos por equivalentes eletrnicos ou por documentos disponveis nos cadastros e bancos de dados da Prefeitura.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 3 </strong>Os diversos pedidos referentes ao mesmo imvel, bem como os recursos contra os respectivos despachos, podem ser analisados em um nico processo.</p>
		<p class="corpo_lei"><span class="bolinha">Art. 56.</span><strong class="artigo-lei"> </strong>O pedido deve ser deferido se o processo estiver devidamente instrudo e o projeto observar a legislao pertinente  matria.</p>
		<p class="corpo_lei"><span class="bolinha">Art. 57.</span><strong class="artigo-lei"> </strong>O processo que apresentar elementos incompletos ou incorretos ou necessitar de complementao da documentao ou esclarecimentos deve ser objeto de um nico comunicado (comunique-se) para que as falhas sejam sanadas.</p>

