		<p class="corpo_lei"><strong class="destaque-lei">Pargrafo nico. </strong>O prazo para atendimento do comunicado  de 30 (trinta) dias contados da data da sua publicao, podendo, ser prorrogado, uma nica vez, por igual perodo.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 48. </strong>A chamada do interessado para atendimento de comunicado, tambm identificado como comunique-se, para adequar o projeto  legislao aplicvel, apresentar documentos necessrios  anlise do processo, alm daqueles exigidos para cada tipo de pedido, ou prestar esclarecimentos indispensveis ao prosseguimento da anlise do pedido ser publicada no DOC. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 1 </strong>O prazo para atendimento do comunicado ser de 30 (trinta) dias, contados a partir da data da publicao da chamada no DOC. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 2 </strong> de inteira responsabilidade do proprietrio ou possuidor e dos responsveis tcnicos informar e registrar eventuais correes ou alteraes de dados cadastrais constantes do pedido de cada processo. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 3 </strong>A Prefeitura disponibilizar o andamento do processo na internet, com a indicao das publicaes de comunicados e despachos no DOC, sendo de inteira responsabilidade do interessado o seu acompanhamento. </p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 49. </strong>Quando forem verificados elementos incompletos ou incorretos ou a necessidade de complementao da documentao ou de esclarecimentos, ser emitido um nico comunicado especificando todas as exigncias a serem atendidas pelo interessado visando cumprir a legislao aplicvel ao caso. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 1 </strong>Aps o decurso do prazo de atendimento do comunicado, no ser admitida a juno de documentos relativos ao seu teor. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 2 </strong>O prazo de atendimento do comunicado emitido poder ser prorrogado uma nica vez, mediante pedido do interessado devidamente justificado, por mais 30 (trinta) dias, contados a partir da data da publicao do despacho de deferimento da prorrogao do prazo no DOC. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 3 </strong>Admite-se a emisso de comunicados complementares, no momento processual adequado, para atendimento, no prazo improrrogvel de 30 (trinta) dias, contados da data da publicao da chamada no DOC, uma nica vez para cada uma nas seguintes hipteses: </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">I -</strong> cumprir exigncias adicionais estabelecidas por rgo colegiado ou por rgo pblico que possa estabelecer condies adicionais, pela competncia; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">II -</strong> pagamento de outorga onerosa; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">III -</strong> comprovao do cumprimento da cota de solidariedade; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">IV -</strong> apresentao de documentos necessrios  formalizao de escritura de doao de reas para alargamento de calada ou para implantao de outros melhoramentos pblicos; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">V -</strong> recolhimento de eventuais diferenas de taxas resultantes da anlise do projeto; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">VI -</strong> aviso de DUP ou DIS que ocorrer durante a anlise do processo. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 4 </strong>O Diretor de Diviso Tcnica ou o Supervisor Tcnico de Uso do Solo e Licenciamento poder emitir ou autorizar a emisso de comunicado complementar ao comunicado emitido quando o seu atendimento resultar em dvida ou implicar em cumprimento de exigncias adicionais. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 5 </strong>Poder ser autorizada, a qualquer tempo e a critrio da chefia, a juno de anuncias e pareceres de outros rgos pblicos relacionados ao pedido de licenciamento em andamento. </p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 50. </strong>Por ocasio do despacho de deferimento do Alvar de Aprovao, Alvar de Aprovao e Execuo e Alvar de Execuo devero ser consultados os cadastros municipais a respeito da supervenincia de DUP ou DIS. </p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 51. </strong>O apostilamento  o meio pelo qual  aditado o documento de controle da atividade edilcia emitido para fazer constar eventuais dados omitidos e retificao de dados, desde que no implique na alterao do projeto, assim como fatos relevantes, tais como suspenso ou anulao de seus efeitos, baixa, transferncia e assuno de responsabilidade tcnica entre outros. </p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 58. </strong>No pedido de Certificado de Regularizao e nos pedidos de Certificado de Acessibilidade e Certificado de Segurana, quando houver necessidade de execuo de obras ou servios para a adaptao da edificao s normas tcnicas aplicveis, podem ser emitidas, respectivamente:</p>
		<p class="corpo_lei"><strong class="destaque-lei">I - </strong>Notificao de Exigncias Complementares  NEC, com prazo de at 180 (cento e oitenta) dias;</p>
		<p class="corpo_lei"><strong class="destaque-lei">II - </strong>Intimao para Execuo de Obras e Servios  IEOS, com prazo de at 180 (cento e oitenta) dias.</p>
		<p class="corpo_lei"><strong class="destaque-lei">Pargrafo nico. </strong>Nos casos previstos no caput deste artigo, a pedido do interessado, por motivo justificado, o prazo pode ser prorrogado uma nica vez por igual perodo.</p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 59. </strong>O pedido deve ser indeferido nas seguintes situaes:</p>
		<p class="corpo_lei"><strong class="destaque-lei">I - </strong>ausncia da documentao exigida ou projeto apresentado com insuficincia de informao de modo a impedir a anlise e deciso do pedido;</p>
		<p class="corpo_lei"><strong class="destaque-lei">II - </strong>projeto com infraes insanveis frente ao disposto no PDE e na LPUOS;</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 52. </strong>Nas hipteses previstas nos incisos I e II do artigo 59 do COE, os pedidos sero imediatamente indeferidos sem a emisso de comunicado.<strong class="subtitulo_txt"></strong></p>
		<p class="corpo_lei"><strong class="destaque-lei">III - </strong>no atendimento ao comunique-se no prazo concedido;</p>
		<p class="corpo_lei"><strong class="destaque-lei">IV - </strong>no atendimento  NEC no prazo concedido;</p>
		<p class="corpo_lei"><strong class="destaque-lei">V - </strong>no atendimento  IEOS no prazo concedido;</p>
		<p class="corpo_lei"><strong class="destaque-lei">VI - </strong>alterao do uso do projeto de edificao.</p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 60. </strong>O prazo para a interposio de recurso  de 30 (trinta) dias a contar da data da publicao do despacho de indeferimento, aps o qual o processo deve ser arquivado, sem prejuzo da ao fiscal correspondente e cobrana das taxas devidas.</p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 61. </strong>Os prazos fixados neste Cdigo so contados em dias corridos, a partir do primeiro dia til aps o evento de origem at o seu dia final inclusive.</p>
		<p class="corpo_lei"><strong class="destaque-lei">Pargrafo nico. </strong>Caso no haja expediente no dia final do prazo, prorroga-se automaticamente o seu trmino para o dia til imediatamente posterior.</p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 62. </strong>Os prazos de validade do Alvar de Aprovao e do Alvar de Execuo ficam suspensos enquanto perdurar qualquer um dos seguintes impedimentos ao incio ou prosseguimento da obra:</p>
		<p class="corpo_lei"><strong class="destaque-lei">I - </strong>deciso judicial determinando ou que implique a paralisao ou o no incio da obra;</p>
		<p class="corpo_lei"><strong class="destaque-lei">II - </strong>calamidade pblica;</p>
		<p class="corpo_lei"><strong class="destaque-lei">III - </strong>declarao de utilidade pblica ou interesse social.</p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 63. </strong>Os documentos de controle da atividade edilcia de que trata este Cdigo podem, enquanto vigentes, a qualquer tempo, mediante ato da autoridade competente, ser:</p>
		<p class="corpo_lei"><strong class="destaque-lei">I - </strong>revogados, atendendo a relevante interesse pblico;</p>
		<p class="corpo_lei"><strong class="destaque-lei">II - </strong>cassados, em caso de desvirtuamento, por parte do interessado, da licena concedida ou de descumprimento de exigncia estabelecida em sua emisso;</p>
		<p class="corpo_lei"><strong class="destaque-lei">III - </strong>anulados, em caso de comprovao de ilegalidade em sua expedio.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 53. </strong>A cassao e a anulao de Alvars de Aprovao e de Execuo de edificaes cabero aos Coordenadores da SMUL e aos Coordenadores de Planejamento e Desenvolvimento Urbano das PRs, no mbito de suas respectivas competncias, na forma estabelecida no COE e neste decreto.<strong class="destaque-decreto"></strong></p>
		<p class="corpo_decreto"><strong class="destaque-decreto">Pargrafo nico. </strong>Outros documentos de controle de atividade edilcia podero ser declarados nulos, observando-se o mesmo procedimento para a anulao de Alvar estabelecido no COE e neste decreto. </p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 64. </strong>O Certificado de Concluso, o Certificado de Regularizao, o Certificado de Acessibilidade e o Certificado de Segurana perdem sua eficcia caso ocorram alteraes de ordem fsica no imvel em relao s condies regularmente aceitas pela Prefeitura.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 54. </strong>Aps a emisso do Certificado de Concluso ou do Certificado de Regularizao de edificao, quando constadas alteraes fsicas no imvel ou o desvirtuamento do documento emitido, a edificao ser cadastrada como irregular. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">Pargrafo nico. </strong>No caso previsto no caput deste artigo, a regularizao da edificao depender da emisso de novo documento de atividade edilcia, na forma prevista no COE e neste decreto.</p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 65. </strong>O Alvar de Autorizao  expedido a ttulo precrio, podendo ser cancelado a qualquer tempo por desvirtuamento de seu objeto ou desinteresse em sua manuteno ou revalidao.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 55. </strong>O Alvar de Autorizao poder ser cancelado por despacho da autoridade competente para a sua emisso. </p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 66. </strong>O cadastro de equipamentos perde a eficcia caso ocorra alterao de ordem fsica no equipamento em relao s condies regularmente cadastradas na Prefeitura ou caso a respectiva renovao no seja solicitada dentro do prazo legal.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 56. </strong>O Cadastro e Manuteno de Equipamentos perder sua validade quando ocorrer uma das seguintes hipteses: </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">I -</strong> comprovao da falsidade ou erro das informaes prestadas; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">II -</strong> modificaes nas caractersticas da edificao, no uso, ou na atividade que implique em alterao das regras de segurana; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">III -</strong> decurso de seu prazo; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">IV -</strong> troca do equipamento j cadastrado, exceto quando relativo a sistema de segurana. </p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 67. </strong>Constatada a qualquer tempo a no veracidade das declaraes apresentadas nos pedidos de que trata esta lei, aplicam-se, ao proprietrio ou possuidor e profissionais envolvidos, as penalidades administrativas previstas neste Cdigo, sem prejuzo das sanes criminais cabveis.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 1 </strong>A atuao irregular do profissional deve ser comunicada ao rgo federal fiscalizador do exerccio profissional.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 2 </strong>Caso haja elementos que indiquem a prtica de infrao penal, a Prefeitura comunicar o fato  autoridade policial competente.</p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 68. </strong>Caso se tenha notcia de fato que possa ensejar a cassao ou anulao do documento expedido, nos termos dos incisos II e III do art. 63 deste Cdigo, a Prefeitura deve notificar o interessado para a apresentao de defesa no prazo de 30 (trinta) dias, de forma a garantir o contraditrio e a ampla defesa, podendo, na defesa, comprovar ter sido sanada a irregularidade.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 1 </strong>Por motivo relevante ou para evitar prejuzo de difcil reparao, a Prefeitura pode suspender os efeitos do documento emitido at deciso sobre sua anulao ou cassao.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 2 </strong>Decorrido o prazo para defesa, a Prefeitura pode efetuar as diligncias cabveis e pedir esclarecimentos a outro rgo pblico envolvido.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 3 </strong>No prazo de 30 (trinta) dias, a autoridade deve decidir a respeito da anulao ou cassao do documento.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 57. </strong>Caso se tenha notcia de fato que possa ensejar a cassao ou anulao de Alvar de Aprovao ou de Execuo de edificao, nos termos dos incisos II e III do artigo 63 do COE, a Coordenadoria competente notificar o interessado para apresentar defesa no prazo de 30 (trinta) dias contados da data da publicao da chamada no DOC, de forma a garantir o contraditrio e a ampla defesa, podendo, na defesa, comprovar ter sido sanada a irregularidade. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 1 </strong>Por motivo relevante ou para evitar prejuzo de difcil reparao, ser proferido despacho suspendendo os efeitos do Alvar de Aprovao ou do Alvar de Execuo de edificao at a deciso sobre sua anulao ou cassao. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 2 </strong>Decorrido o prazo para defesa, a Prefeitura poder efetuar as diligncias cabveis e pedir esclarecimentos a outro rgo pblico envolvido. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 3 </strong>No prazo de 30 (trinta) dias, a autoridade dever decidir a respeito da anulao ou cassao do Alvar de Aprovao ou do Alvar de Execuo de edificao. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 4 </strong>Do despacho que anular ou cassar o alvar, caber um nico recurso no prazo de 30 (trinta) dias, contados de sua publicao no DOC, a ser apreciado pelo Secretrio Municipal de Urbanismo e Licenciamento ou Prefeito Regional, no mbito de suas respectivas competncias. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 5 </strong>A deciso do recurso pelo Secretrio Municipal de Urbanismo e Licenciamento ou Prefeito Regional encerra definitivamente a instncia administrativa a respeito de declarao de nulidade, anulao ou cassao do alvar. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 6 </strong>As notificaes e comunicaes de decises de que tratam este artigo sero publicadas no DOC e podero ser enviadas ao interessado por meio do endereo eletrnico cadastrado no processo. </p>
		<h2 class="secao_lei">Seo II - Das Instncias e Prazos para Despacho</h2>
		<p class="corpo_lei"><span class="bolinha">Art. 69. </span> As instncias administrativas para a apreciao e deciso dos pedidos de que trata este Cdigo, protocolados a partir da data de sua vigncia, so as seguintes:</p>
		<p class="corpo_lei"><strong class="destaque-lei">I - </strong>para os pedidos de competncia da <strong>Secretaria Municipal de Licenciamento</strong><sup>17</sup>:</p>

		<div id="04_capitulo-IV_b01" class="glossario-box">
			<p class="corpo_referencias1"><sup>17 </sup>Conforme Decreto N 57.576, a Secretaria Municipal de Licenciamento (SEL) teve suas atribuies fundidas com a Secretaria Municipal de Desenvolvimento Urbano (SMDU), tornando-se em janeiro de 2017 a atual Secretaria Municipal de Urbanismo e Licenciamento (SMUL). </p>
		</div>	

		<p class="corpo_lei"><strong class="destaque-lei">a)</strong> Diretor de Diviso Tcnica;</p>
		<p class="corpo_lei"><strong class="destaque-lei">b)</strong> Coordenador;</p>
		<p class="corpo_lei"><strong class="destaque-lei">c)</strong> Secretrio Municipal de Licenciamento;</p>
		<p class="corpo_lei"><strong class="destaque-lei">II - </strong>para os pedidos de competncia das Subprefeituras:</p>
		<p class="corpo_lei"><strong class="destaque-lei">a)</strong> Supervisor Tcnico de Uso do Solo e Licenciamentos;</p>
		<p class="corpo_lei"><strong class="destaque-lei">b)</strong> Coordenador de Planejamento e Desenvolvimento Urbano;</p>
		<p class="corpo_lei"><strong class="destaque-lei">c)</strong> Subprefeito.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 1 </strong>Cabe recurso  autoridade imediatamente superior quela que proferiu a deciso.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 2 </strong>A competncia para a apreciao dos pedidos e deciso em primeira instncia pode ser delegada aos tcnicos e chefes de seo, mediante portaria do Secretrio Municipal de Licenciamento ou do Secretrio Municipal de Coordenao das Subprefeituras, mantida a competncia originria para a apreciao e deciso dos recursos.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 3 </strong>Os despachos do Secretrio Municipal de Licenciamento e dos Subprefeitos em grau de recurso, bem como o decurso do prazo recursal encerram definitivamente a instncia administrativa.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 58. </strong>As instncias administrativas para a apreciao e deciso dos pedidos de que trata este Cdigo, protocolados a partir da data de sua vigncia, so as seguintes:<strong class="destaque-decreto"></strong></p>
		<p class="corpo_decreto"><strong class="destaque-decreto">I -</strong> para os pedidos de competncia da Secretaria Municipal de Urbanismo e Licenciamento: </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">a)</strong> Diretor de Diviso Tcnica; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">b)</strong> Coordenador; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">c)</strong> Secretrio Municipal de Urbanismo e Licenciamento; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">II -</strong> para os pedidos de competncia das Prefeituras Regionais: </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">a)</strong> Supervisor Tcnico de Uso do Solo e Licenciamento; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">b)</strong> Coordenador de Planejamento e Desenvolvimento Urbano; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">c)</strong> Prefeito Regional. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 1 </strong>Cabe recurso  autoridade imediatamente superior quela que proferiu a deciso. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 2 </strong>A competncia para a apreciao dos pedidos e deciso em primeira instncia pode ser delegada aos tcnicos e chefes de seo, mediante portaria do Secretrio Municipal de Urbanismo e Licenciamento ou do Secretrio Municipal das Prefeituras Regionais, mantida a competncia originria para a apreciao e deciso dos recursos.</p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 3 </strong>Os despachos do Secretrio Municipal de Urbanismo e Licenciamento e dos Prefeitos Regionais em grau de recurso, bem como o decurso do prazo recursal encerram definitivamente a instncia administrativa. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 4 </strong>O recurso intempestivo ser indeferido de imediato em primeira instncia administrativa. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 5 </strong>No licenciamento da edificao que envolver simultaneamente o parcelamento do solo como Plano Integrado, os pedidos de loteamento, desmembramento e reparcelamento ficam sujeitos s instncias do caput deste artigo.</p>

