		<p class="corpo_lei"><span class="bolinha">Art. 70.</span><strong class="artigo-lei"> </strong>O prazo para a deciso dos pedidos no pode exceder 90 (noventa) dias, inclusive quando se tratar de recurso.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 1 </strong>Prazos diferentes podem ser fixados por ato do Executivo, em funo da complexidade da anlise do pedido.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 2 </strong>O curso do prazo fixado no caput deste artigo fica suspenso durante a pendncia do atendimento, pelo interessado, das exigncias feitas no comunique-se.</p>
		<p class="corpo_lei"><span class="bolinha">Art. 71.</span><strong class="artigo-lei"> </strong>Escoado o prazo para a deciso do processo de Alvar de Aprovao, o interessado pode requerer o Alvar de Execuo.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 1 </strong>Decorridos 30 (trinta) dias contados da data do protocolo do pedido do Alvar de Execuo, caso o processo no tenha sido indeferido, a obra pode ser iniciada, sendo de inteira responsabilidade do proprietrio ou possuidor e profissionais envolvidos a adequao da obra s posturas municipais.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 2 </strong>Quando solicitado Alvar de Aprovao e de Execuo em conjunto, o prazo para a deciso  de 120 (cento e vinte) dias.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 3 </strong>Nos casos de incidncia de outorga onerosa, o incio da obra fica condicionado  comprovao de seu pagamento.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 59. </strong>O direito do incio de obras se dar aps 30 (trinta) dias do protocolo do pedido de Alvar de Execuo ou 120 (cento e vinte) dias do protocolo do pedido de Alvar de Aprovao e Execuo, exceto quando: </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">I -</strong> indeferido o pedido; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">II -</strong> incidir outorga onerosa. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 1 </strong>A contagem dos prazos previstos no caput deste artigo fica suspensa durante transcurso do prazo de atendimento de comunique-se. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 2 </strong>Para os fins do caput deste artigo, ser considerada incio das obras qualquer interveno para a execuo das obras relacionadas no artigo 23 do COE. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 3 </strong>Ser de inteira responsabilidade do proprietrio ou possuidor e profissionais habilitados a eventual necessidade de adequao da obra s posturas municipais. </p>

