					<p class="g-pstyle11">A Prefeitura tem <span class="g-cstyle24">30 dias</span> para analisar e deferir pedidos em geral, como Alvar de Execuo e 120 dias para analisar e deferir pedidos de Alvar de Aprovao e Execuo em protocolo nico.</p>
