<template>
	<div class="Capitulo7">
		<h1 class="cap_lei">CAPTULO VII  DO ALINHAMENTO E DO MELHORAMENTO VIRIO</h1>

		<p class="corpo_lei"><strong class="artigo-lei">Art. 102. </strong>Para os fins deste Cdigo, consideram-se fixados os atuais alinhamentos e nivelamento dos logradouros pblicos existentes no Municpio de So Paulo, oficializados ou pertencentes a loteamento aceito ou regularizado, bem como daqueles oriundos de melhoramento virio executado sob a responsabilidade do Poder Pblico Municipal, Estadual ou Federal.</p>
		<p class="corpo_lei"><strong class="destaque-lei">Pargrafo nico. </strong>No caso de indefinio, a pedido do interessado, a Prefeitura deve fornecer o alinhamento e nivelamento, mediante a emisso de certido.</p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 103. </strong>Enquanto no executados, devem ser observados os novos alinhamentos aprovados constantes das leis de melhoramento virio.</p>
		<p class="corpo_lei"><strong class="destaque-lei">Pargrafo nico. </strong>O disposto no caput deste artigo no se aplica aos planos de melhoramento publicados anteriormente a 8 de novembro de 1988, data da entrada em vigor da Lei n 10.676, de 7 de novembro de 1988, desde que no exista declarao de utilidade pblica em vigor por ocasio da emisso da aprovao do projeto.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 97. </strong>Desde que no exista declarao de utilidade pblica em vigor por ocasio da emisso do Alvar de Aprovao do projeto, no se aplicam, para efeito do licenciamento edilcio, os novos alinhamentos aprovados constantes de lei de melhoramento virio e do PRM, publicados anteriormente a 8 de novembro de 1988, inclusive no caso de processos em andamento.</p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 104. </strong>A alterao de alinhamento de logradouro pblico que importe em alargamento, estreitamento ou retificao, total ou parcial, deve ser objeto de plano de melhoramento virio aprovado por lei.</p>
		<p class="corpo_lei"><strong class="destaque-lei">Pargrafo nico. </strong>A alterao de nivelamento de logradouro pblico, parcial ou em toda sua extenso, pode ser definida por ato do Executivo.</p>
		<p class="corpo_lei"><span class="bolinha">Art. 105.</span><strong class="artigo-lei"> </strong> permitida a execuo de qualquer obra em imvel totalmente atingido por plano de melhoramento pblico e sem declarao de utilidade pblica ou de interesse social em vigor, observado o disposto na LOE, PDE e LPUOS.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 1 </strong>No caso de declarao de utilidade pblica ou de interesse social em vigor, permite-se a execuo de qualquer obra, a ttulo precrio, observado o disposto na LOE, PDE e LPUOS, no sendo devida ao proprietrio qualquer indenizao pela benfeitoria ou acesso quando da execuo do melhoramento pblico.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 2 </strong>Considera-se tambm como totalmente atingido por melhoramento pblico o imvel:</p>
		<p class="corpo_lei"><strong class="destaque-lei">I - </strong>cujo remanescente no possibilite a execuo de edificao que atenda ao disposto na LOE, PDE e LPUOS;</p>
		<p class="corpo_lei"><strong class="destaque-lei">II - </strong>no qual, por decorrncia de nova situao de nivelamento do logradouro, seja dificultada a implantao de edificaes, a critrio da Prefeitura.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 98. </strong>O Alvar para imveis totalmente atingidos por melhoramento pblico dever ser emitido com notas contendo: </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">I -</strong> a indicao do nmero da lei do melhoramento pblico e da DUP ou DIS em vigor, quando for o caso; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">II -</strong> a indicao da rea de terreno e da rea necessria ao melhoramento pblico.<strong class="destaque-decreto"></strong></p>
		<p class="corpo_decreto"><strong class="destaque-decreto">Pargrafo nico. </strong>Em se tratando de DUP ou DIS em vigor, por ocasio do despacho de deferimento do Alvar para imvel totalmente atingido por melhoramento pblico, dever ser includa nota para constar que o Alvar  emitido a ttulo precrio, no sendo devida indenizao por obras, benfeitorias ou acesso dele decorrentes por ocasio da execuo do melhoramento pblico, nos termos do  1 do artigo 105 do COE. </p>
		<p class="corpo_lei"><span class="bolinha">Art. 106. </span>A edificao nova e as novas partes da edificao existente, nas reformas com aumento de rea executadas em imvel parcialmente atingido por plano de melhoramento pblico aprovado por lei e sem declarao de utilidade pblica ou de interesse social em vigor, devem observar as seguintes disposies:</p>
		<p class="corpo_lei"><strong class="destaque-lei">I - </strong>atender aos recuos mnimos obrigatrios,  taxa de ocupao e ao coeficiente de aproveitamento estabelecidos no PDE e LPUOS, em relao ao lote original;</p>
		<p class="corpo_lei"><strong class="destaque-lei">II - </strong>observar solues que garantam, aps a execuo do plano de melhoramento pblico, o pleno atendimento, pelas edificaes remanescentes, das disposies previstas na LOE, PDE e LPUOS em relao ao lote resultante da desapropriao, inclusive com a previso de demolio total se for o caso.</p>
		<p class="corpo_lei"><span class="bolinha">Art. 107.</span><strong class="artigo-lei"> </strong>No caso de imvel parcialmente atingido por plano de melhoramento aprovado por lei e com declarao de utilidade pblica ou de interesse social em vigor, a edificao nova e as novas partes da edificao existente nas reformas com aumento de rea devem observar os recuos mnimos obrigatrios, a taxa de ocupao e o coeficiente de aproveitamento estabelecidos no PDE e LPUOS e as disposies do COE em relao ao lote resultante da desapropriao.</p>

