				<div id="g-ai0-13" class="g-Camada_2 g-aiAbs g-aiPointText" style="top:51.837%;margin-top:-10.6px;left:32.6619%;width:265px;">
					<p class="g-pstyle10">PROJETO (SEM DOAO DE REA)</p>
				</div>
