				<div id="g-ai0-27" class="g-Camada_2 g-aiAbs g-aiPointText" style="top:76.1499%;margin-top:-10.6px;left:31.9172%;width:268px;">
					<p class="g-pstyle10">PROJETO (COM DOAO DE REA)</p>
				</div>
				<div id="g-ai0-28" class="g-Camada_2 g-aiAbs g-aiPointText" style="top:76.1374%;margin-top:-10.5px;left:68.1577%;width:158px;">
					<p class="g-pstyle2"><span class="g-cstyle0">SI</span>T<span class="g-cstyle3">U</span><span class="g-cstyle23">A</span><span class="g-cstyle0"></span><span class="g-cstyle23"></span>O FUTU<span class="g-cstyle6">R</span><span class="g-cstyle1">A</span></p>
				</div>
				<div id="g-ai0-29" class="g-Camada_2 g-aiAbs" style="top:75.8562%;left:0.1042%;width:25.2083%;">
					<p class="g-pstyle3">COM DUP/DIS EM VIGOR</p>
					<p>&nbsp;</p>
					<p class="g-pstyle11">Em caso de DUP ou DIS em vigor, caso haja execuo de edificao na faixa a ser desapropriada, se deve constar nota de que o Alvar  emitido a ttulo precrio, sem direito a indenizao por obras e benfeitorias.</p>
				</div>
				<div id="g-ai0-30" class="g-Camada_2 g-aiAbs g-aiPointText" style="top:79.2679%;margin-top:-8.1px;left:31.7963%;width:161px;">
					<p class="g-pstyle7"><span class="g-cstyle37">R</span>ecuos e<span class="g-cstyle34"> </span><span class="g-cstyle39">T</span><span class="g-cstyle2">.</span><span class="g-cstyle36">O</span>. em funo </p>
				</div>
				<div id="g-ai0-31" class="g-Camada_2 g-aiAbs g-aiPointText" style="top:80.4025%;margin-top:-8.1px;left:31.7963%;width:167px;">
					<p class="g-pstyle7">do lo<span class="g-cstyle3">t</span>e <span class="g-cstyle34">r</span>esul<span class="g-cstyle37">t</span>an<span class="g-cstyle3">t</span>e. C.A. em </p>
				</div>
				<div id="g-ai0-32" class="g-Camada_2 g-aiAbs g-aiPointText" style="top:81.4561%;margin-top:-8.1px;left:31.7963%;width:139px;">
					<p class="g-pstyle7">funo do lo<span class="g-cstyle3">t</span>e original</p>
				</div>
				<div id="g-ai0-33" class="g-Camada_2 g-aiAbs g-aiPointText" style="top:89.4792%;margin-top:-8.1px;left:30.8388%;width:92px;">
					<p class="g-pstyle7">Alinhamen<span class="g-cstyle3">t</span>o </p>
				</div>
				<div id="g-ai0-34" class="g-Camada_2 g-aiAbs g-aiPointText" style="top:89.4792%;margin-top:-8.1px;left:67.4678%;width:92px;">
					<p class="g-pstyle7">Alinhamen<span class="g-cstyle3">t</span>o </p>
				</div>
				<div id="g-ai0-35" class="g-Camada_2 g-aiAbs g-aiPointText" style="top:90.6139%;margin-top:-8.1px;left:30.8408%;width:64px;">
					<p class="g-pstyle7">p<span class="g-cstyle20">r</span>evis<span class="g-cstyle2">t</span>o</p>
				</div>
				<div id="g-ai0-36" class="g-Camada_2 g-aiAbs g-aiPointText" style="top:90.6139%;margin-top:-8.1px;left:67.4697%;width:77px;">
					<p class="g-pstyle7">execu<span class="g-cstyle37">t</span>ado</p>
				</div>
				<div id="g-ai0-37" class="g-Camada_2 g-aiAbs g-aiPointText" style="top:95.5115%;margin-top:-15.5px;left:28.9524%;width:165px;">
					<p class="g-pstyle7">rea atingida pelo </p>
					<p class="g-pstyle7">melhoramento a ser doada</p>
				</div>
			</div>
		</div>

		<p class="corpo_lei"><strong class="destaque-lei"> 1 </strong>Observadas as condies estabelecidas no caput deste artigo, fica assegurado, ao proprietrio que doar  Prefeitura do Municpio de So Paulo a parcela do imvel necessria  execuo do melhoramento virio aprovado por lei, o direito de utilizar tambm essa parcela doada no clculo do coeficiente de aproveitamento.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 2 </strong>Atendidas as disposies do caput deste artigo, a execuo de edificao na faixa a ser desapropriada poder ser permitida pela Prefeitura, a ttulo precrio, devendo ser prevista sua demolio total e no sendo devida ao proprietrio qualquer indenizao pela benfeitoria ou acesso quando da execuo do melhoramento pblico.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 3 </strong>No caso de o melhoramento prever a instituio de faixa no edificvel, no se aplica o disposto no  1 deste artigo, devendo a faixa ser indicada no projeto da edificao e constar do documento a ser emitido.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 99. </strong>O Alvar de imvel parcialmente atingido por plano de melhoramento pblico, quando no houver doao de reas, dever ser emitido com notas contendo: </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">I -</strong> a indicao do nmero da lei do melhoramento pblico e da DUP ou DIS em vigor, quando for o caso; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">II -</strong> a indicao da rea do terreno, da rea necessria ao melhoramento pblico e da rea remanescente.<strong class="destaque-decreto"></strong></p>
		<p class="corpo_decreto"><strong class="destaque-decreto">Pargrafo nico. </strong>Nos casos previstos neste artigo e com DUP ou DIS em vigor, por ocasio do despacho de deferimento do Alvar, na hiptese de execuo de edificao na faixa a ser desapropriada, tambm dever constar nota de que o Alvar  emitido a ttulo precrio, no sendo devida indenizao por obras, benfeitorias ou acesso dele decorrentes por ocasio da execuo do melhoramento pblico, nos termos do  2 do artigo 107 do COE. </p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 100. </strong>A doao de reas e a comprovao do registro da escritura no Cartrio de Registro de Imveis devero ocorrer antes da emisso do Alvar de Execuo de edificao. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 1 </strong>Admite-se a doao voluntria de melhoramento pblico, sem DUP ou DIS em vigor, observando-se as disposies do caput e do  1 do artigo 107 do COE. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 2 </strong>Fica delegada aos Coordenadores de SMUL e aos Coordenadores de Planejamento e Desenvolvimento Urbano das PRs, no mbito de suas atribuies, a competncia para representar o Municpio nas escrituras de doao de reas necessrias  emisso do Alvar de Execuo de edificao. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 3 </strong>A relao de documentos necessrios  formalizao de escritura de doao, a padronizao de minutas de escritura e as respectivas rotinas sero estabelecidas em portaria do Secretrio de SMUL.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 101. </strong>Quando a lei de melhoramento previr a instituio de faixa de servido no edificvel, com ou sem DUP em vigor, no ser lavrada escritura de doao ou de servido, devendo a faixa ser indicada no projeto da edificao e o Alvar ser emitido com nota indicando o nmero da respectiva lei de melhoramento pblico, a metragem da rea no edificvel e sua localizao aproximada dentro dos limites do imvel.</p>

		
	</div>
</template>

