<template>
  <div class="Capitulo1">
	<div>
		<h1 class="cap_lei">CAPTULO I  DAS DISPOSIES GERAIS</h1>
		<p class="corpo_lei"><strong class="destaque-lei">LEI N 16.642/17</strong></p>
		<p class="corpo_lei">Aprova o Cdigo de Obras e Edificaes do Municpio de So Paulo; introduz alteraes nas Leis n 15.150, de 6 de maio de 2010, e n 15.764, de 27 de maio de 2013.</p>
		<p class="corpo_lei"><strong class="destaque-lei">JOO DORIA</strong>, Prefeito do Municpio de So Paulo, no uso das atribuies que lhe so conferidas por lei, faz saber que a Cmara Municipal, em sesso de 1 de julho de 2016, decretou e eu promulgo a seguinte lei:</p>
		<p class="corpo_decreto"><strong class="destaque-decreto">DECRETO N 57.776/17*</strong></p>
		<p class="corpo_decreto">Regulamenta a Lei n 16.642, de 9 de maio de 2017, que aprovou o Cdigo de Obras e Edificaes do Municpio de So Paulo; define os membros da Comisso de Edificaes e Uso do Solo  CEUSO. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">JOO DORIA</strong>, Prefeito do Municpio de So Paulo, usando das atribuies que lhe so conferidas por lei, e CONSIDERANDO as disposies dos artigos 122 e 123 do Cdigo de Obras e Edificaes do Municpio de So Paulo, DECRETA:</p>
		<p class="corpo_lei"> </p>
		<p class="corpo_lei">* Com as retificaes do Decreto n 57.815/17 consideradas.</p>
	</div>
	<div>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 1 </strong>Fica aprovado o Cdigo de Obras e Edificaes do Municpio de So Paulo  COE, que disciplina, no Municpio de So Paulo, as regras gerais a serem observadas no projeto, no licenciamento, na execuo, na manuteno e na utilizao de obras, edificaes e equipamentos, dentro dos limites do imvel, bem como os respectivos procedimentos administrativos, executivos e fiscalizatrios, sem prejuzo do disposto na legislao estadual e federal pertinente.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 1 </strong>Este decreto regulamenta os procedimentos administrativos e executivos e fixa as regras gerais e especficas a serem obedecidas no projeto, licenciamento, execuo, manuteno, utilizao e fiscalizao de obras, edificaes, equipamentos, obras complementares, terraos e mobilirios, dentro dos limites dos imveis em que se situam, nos termos do disposto na Lei n. 16.642, de 9 de maio de 2017, que aprovou o Cdigo de Obras e Edificaes do Municpio de So Paulo  COE. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 1 </strong>Integram este decreto os anexos a seguir discriminados:</p>
		<p class="corpo_decreto"><strong class="destaque-decreto">I -</strong> Anexo I  Disposies Tcnicas; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">II -</strong> Anexo II  Desenhos; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">III -</strong> Anexo III  Tabela de Multas; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">IV - </strong>Anexo IV  Tabelas Relativas s reas No Computveis. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 2 </strong>Os modelos de projeto simplificado, quadros, legendas, documentos e declaraes para instruir cada um dos pedidos de emisso de documentos de atividade edilcia de que trata o COE sero fixados mediante portaria da Secretaria Municipal de Urbanismo e Licenciamento  SMUL. </p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 2 </strong>Para os fins deste decreto, os rgos, entidades e expresses sero identificados pelas seguintes siglas ou abreviaturas:</p>
		<p class="corpo_decreto"><strong class="destaque-decreto">I - </strong>ART  Anotao de Responsabilidade Tcnica perante o CREA; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">II - </strong>AVCB  Auto de Vistoria do Corpo de Bombeiros; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">III - </strong>CAU  Conselho de Arquitetura e Urbanismo; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">IV - </strong>CCIR  Certificado de Cadastro de Imvel Rural; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">V - </strong>CEPAC  Certificado de Potencial Adicional Construtivo; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">VI - </strong>COE  Cdigo de Obras e Edificaes; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">VII - </strong>CREA Conselho Regional de Engenharia e Agronomia; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">VIII - </strong>DIS  Decreto de Declarao de Interesse Social; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">IX - </strong>DOC  Dirio Oficial da Cidade; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">X - </strong>DUP  Decreto de Declarao de Utilidade Pblica; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">XI - </strong>ISS  Imposto Sobre Servios de Qualquer Natureza; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">XII - </strong>IEOS  Intimao para Execuo de Obras e Servios; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">XIII - </strong>IT  Instruo Tcnica (Corpo de Bombeiros); </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">XIV - </strong>LOE  Legislao de Obras e Edificaes, incluindo o COE; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">XV - </strong>LPUOS  Legislao de Parcelamento, Uso e Ocupao do Solo; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">XVI - </strong>NEC  Notificao de Exigncias Complementares; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">XVII - </strong>NT  Norma Tcnica, expedida por rgo competente, exceto ABNT; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">XVIII - </strong>NTO  Norma Tcnica Oficial (registrada na Associao Brasileira de Normas Tcnicas  ABNT); </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">XIX - </strong>OU  Operao Urbana; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">XX - </strong>OUC  Operao Urbana Consorciada; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">XXI - </strong>PDE  Plano Diretor Estratgico; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">XXII - </strong>PR  Prefeitura Regional;</p>
		<p class="corpo_decreto"><strong class="destaque-decreto">XXIII - </strong>Prefeitura  Prefeitura do Municpio de So Paulo; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">XXIV - </strong>RRT  Registro de Responsabilidade Tcnica perante o CAU; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">XXV - </strong>SLCe  Sistema de Licenciamento Eletrnico de Construo; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">XXVI - </strong>SMPR  Secretaria Municipal das Prefeituras Regionais; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">XXVII - </strong>SMUL  Secretaria Municipal de Urbanismo e Licenciamento; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">XXVIII - </strong>TEV/COE  Taxa para Exame e Verificao dos Pedidos de Documentos de Controle da Atividade Edilcia. </p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 3 </strong>Enquadram-se como processos especiais, com procedimento definido neste decreto, os pedidos de emisso dos seguintes documentos de controle da atividade edilcia previstos no artigo 12 do COE: </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">I - </strong>Alvar de Aprovao; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">II - </strong>Alvar de Execuo; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">III - </strong>Projeto Modificativo; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">IV - </strong>Certificado de Concluso; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">V - </strong>Certificado de Regularizao; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">VI - </strong>Certificado de Acessibilidade; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">VII - </strong>Certificado de Segurana; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">VIII - </strong>Alvar de Autorizao; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">IX - </strong>Cadastro de Equipamento; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">X - </strong>Manuteno de Equipamento; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">XI - </strong>Ficha Tcnica; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">XII - </strong>Diretrizes de Projeto. </p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 4 </strong>Compete  Secretaria Municipal de Urbanismo e Licenciamento a anlise e deciso dos pedidos de documentos de controle da atividade edilcia de que tratam o COE e este decreto. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 1 </strong>A competncia de anlise e deciso poder ser transferida s PRs e SMPR por decreto, em razo da metragem da rea, da lotao ou da categoria de uso. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 2 </strong>Ficam mantidas as delegaes de competncias s PRs e SMPR, at a reviso dos respectivos decretos de delegao de competncia em vigor. </p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 2 </strong>A anlise dos projetos e dos pedidos de documentos de controle da atividade edilcia deve ser efetuada quanto  sua observncia:</p>
		<p class="corpo_lei"><strong class="destaque-lei">I - </strong>s normas do <strong>Plano Diretor Estratgico  PDE</strong><sup class="referencias-notas">1</sup>, da <strong>Legislao de Parcelamento, Uso e Ocupao do Solo  LPUOS</strong><sup class="referencias-notas">2</sup> e das <strong>Operaes Urbanas Consorciadas  OUC</strong><sup class="referencias-notas">3</sup>;</p>

		<div id="01_capitulo-I_b01" class="glossario-box">
			<p class="corpo_referencias1"><sup>1</sup> <span class="glossario-museo">Plano Diretor Estratgico  PDE</span> (Lei n 16.050, de 31 de julho de 2014), dispe sobre a Poltica de Desenvolvimento Urbano, o Sistema de Planejamento Urbano e o Plano Diretor Estratgico do Municpio de So Paulo e aplica-se  totalidade do seu territrio.</p>
			<p class="corpo_referencias1"><sup>2</sup> <span class="glossario-museo">Lei de Parcelamento, Uso e Ocupao do Solo  LPUOS</span> (Lei n 16.402, de 22 de maro de 2016) Dispe sobre o parcelamento, o uso e a ocupao do solo no territrio do Municpio de So Paulo de acordo com o Plano Diretor Estratgico  PDE, e legislao correlata.</p>
			<p class="corpo_referencias1"><sup>3</sup> <span class="glossario-museo">Operao Urbana / Operao Urbana Consorciada  OU / OUC</span> so instrumentos cujas diretrizes so previstas pelo PDE e aprovadas mediante lei municipal, realizadas sob a coordenao do Poder Pblico, envolvendo a iniciativa privada, empresas prestadoras de servios pblicos, moradores e usurios do local, em permetro delimitado do territrio a fim de conferir melhorias urbansticas que atendam s demandas sociais, ambientais e econmicas existentes.</p>
		</div>
		
		<p class="corpo_lei"><strong class="destaque-lei">II - </strong>aos planos de melhoramento virio aprovados;</p>
		<p class="corpo_lei"><strong class="destaque-lei">III - </strong>s servides administrativas;</p>
		<p class="corpo_lei"><strong class="destaque-lei">IV - </strong>s restries decorrentes das declaraes de utilidade pblica e de interesse social;</p>
		<p class="corpo_lei"><strong class="destaque-lei">V - </strong>s limitaes decorrentes do tombamento e da preservao de imveis;</p>
		<p class="corpo_lei"><strong class="destaque-lei">VI - </strong>s regras para mitigar o impacto ambiental e de vizinhana;</p>
		<p class="corpo_lei"><strong class="destaque-lei">VII - </strong>s restries para a ocupao de reas com risco ou contaminadas;</p>
		<p class="corpo_lei"><strong class="destaque-lei">VIII - </strong>a quaisquer leis ou regulamentos relacionados s caractersticas externas da edificao ou equipamento e sua insero na paisagem urbana;</p>
		<p class="corpo_lei"><strong class="destaque-lei">IX - </strong>s exigncias relativas s condies de segurana de uso das edificaes com alto potencial de risco de incndios e situaes de emergncia.</p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 3 </strong>Para fins de aplicao das disposies deste Cdigo ficam adotadas as seguintes definies:</p>
		<p class="corpo_lei"><strong class="destaque-lei">I - </strong>acessibilidade: condio de utilizao, com segurana e autonomia, total ou assistida, por pessoa com deficincia ou com mobilidade reduzida, de edificao, espao, mobilirio e equipamento;</p>
		<p class="corpo_lei"><strong class="destaque-lei">II - </strong>acessvel: edificao, espao, mobilirio e equipamento que possa ser utilizado e vivenciado por qualquer pessoa, inclusive aquela com deficincia ou mobilidade reduzida, conforme os parmetros definidos em norma tcnica pertinente;</p>
		<p class="corpo_lei"><strong class="destaque-lei">III - </strong>adaptao razovel: modificaes e ajustes necessrios e adequados que no acarretem nus desproporcional ou indevido, quando requeridos em cada caso, a fim de assegurar que as pessoas com deficincia ou mobilidade reduzida possam gozar ou exercer, em igualdade de oportunidades com as demais pessoas, os direitos humanos e liberdades fundamentais;</p>
		<p class="corpo_lei"><strong class="destaque-lei">IV - </strong>alinhamento: linha de divisa entre o terreno e o logradouro pblico;<strong class="destaque-lei"></strong></p>
		<p class="corpo_lei"><strong class="destaque-lei">V - </strong>andar: volume compreendido entre dois pavimentos consecutivos ou entre o pavimento e o nvel superior de sua cobertura;</p>
		<p class="corpo_lei"><span class="bolinha">VI</span><strong class="destaque-lei"> - </strong>tico: parte do volume superior de uma edificao, destinada a abrigar casa de mquinas, piso tcnico de elevadores, equipamentos, caixa dgua e circulao vertical;</p>
		<p class="corpo_lei"><span class="bolinha">VII</span><strong class="destaque-lei"> - </strong>beiral: prolongamento da cobertura que se sobressai das paredes externas da edificao;</p>
		<p class="corpo_lei"><strong class="destaque-lei">VIII - </strong>canteiro de obras: espao delimitado pelo tapume, destinado ao preparo e apoio  execuo da obra ou servio, incluindo os elementos provisrios que o compem, tais como estande de vendas, alojamento, escritrio de campo, depsitos, galeria, andaime, plataforma e tela protetora visando  proteo da edificao vizinha e logradouro pblico;</p>
		<p class="corpo_lei"><strong class="destaque-lei">IX - </strong>demolio: total derrubamento de uma edificao;</p>
		<p class="corpo_lei"><strong class="destaque-lei">X - </strong>edificao: obra coberta destinada a abrigar atividade humana ou qualquer instalao, equipamento e material;</p>
		<p class="corpo_lei"><strong class="destaque-lei">XI - </strong>edificao transitria: edificao de carter no permanente, passvel de montagem, desmontagem e transporte;</p>
		<p class="corpo_lei"><strong class="destaque-lei">XII - </strong>embargo: ordem de paralisao dos trabalhos na obra ou servio em execuo sem a respectiva licena ou por desatendimento  Legislao de Obras e Edificaes  LOE ou LPUOS;</p>
		<p class="corpo_lei"><strong class="destaque-lei">XIII - </strong>equipamento: elemento no considerado como rea construda, destinado a guarnecer ou completar uma edificao, a ela se integrando, tais como equipamentos mecnicos de transporte, tanques de armazenagem, bombas e sistemas de energia, aquecimento solar e a gs, podendo ser:</p>
		<p class="corpo_lei"><strong class="destaque-lei">a)</strong> equipamento permanente: equipamento de carter duradouro;</p>
		<p class="corpo_lei"><strong class="destaque-lei">b) </strong>equipamento transitrio: equipamento de carter no permanente, passvel de montagem, desmontagem e transporte;</p>
		<p class="corpo_lei"><strong class="destaque-lei">XIV - </strong>interdio: ordem e ato de fechamento e desocupao do imvel em situao irregular ou de risco em relao s condies de estabilidade, segurana ou salubridade;</p>
		<p class="corpo_lei"><strong class="destaque-lei">XV - </strong>mobilirio: elemento construtivo que no se enquadra como edificao ou equipamento, tais como:</p>
		<p class="corpo_lei"><strong class="destaque-lei">a)</strong> guarita e mdulo pr-fabricado;</p>
		<p class="corpo_lei"><span class="bolinha">b)</span> jirau, elemento constitudo de estrado ou passadio, instalado a meia altura em compartimento;</p>
		<p class="corpo_lei"><strong class="destaque-lei">c) </strong>abrigo ou telheiro sem vedao lateral em pelo menos 50% (cinquenta por cento) do permetro;</p>
		<p class="corpo_lei"><strong class="destaque-lei">d) </strong>estufas, quiosques, viveiros de plantas, churrasqueiras;</p>
		<p class="corpo_lei"><strong class="destaque-lei">e) </strong>dutos de lareiras;</p>
		<p class="corpo_lei"><span class="bolinha">f)</span><strong class="destaque-lei"> </strong>prgulas;</p>
		<p class="corpo_lei"><strong class="destaque-lei">XVI - </strong>movimento de terra: modificao do perfil do terreno ou substituio do solo em terrenos alagadios ou que implique em alterao topogrfica superior a 1,50 m (um metro e cinquenta centmetros) de desnvel ou a 1.000,00 m (mil metros cbicos) de volume;</p>
		<p class="corpo_lei"><strong class="destaque-lei">XVII - </strong>muro de arrimo: muro resistente, que trabalha por gravidade ou flexo, construdo para conter macio de terra, empuxo das guas de infiltrao, sobrecarga de construo, sobreaterro e situaes similares;</p>
		<p class="corpo_lei"><span class="bolinha">XVIII</span><strong class="destaque-lei"> - </strong>obra complementar: edificao secundria ou parte da edificao que, funcionalmente, complemente a atividade desenvolvida no imvel, tais como:</p>
		<p class="corpo_lei"><strong class="destaque-lei">a)</strong> passagem coberta de pedestre sem vedao lateral;</p>
		<p class="corpo_lei"><strong class="destaque-lei">b)</strong> abrigo de porta e porto, automvel, lixo, recipiente de gs e entrada e medidores de concessionrias;</p>
		<p class="corpo_lei"><strong class="destaque-lei">c)</strong> casa de mquina isolada, cabine de fora, cabine primria;</p>
		<p class="corpo_lei"><strong class="destaque-lei">d)</strong> reservatrio em geral, elevado e enterrado, chamin e torre isoladas;</p>
		<p class="corpo_lei"><strong class="destaque-lei">e) </strong>bilheteria, portaria, caixa eletrnico;</p>
		<p class="corpo_lei"><strong class="destaque-lei">XIX - </strong>obras de emergncia: obras de carter urgente, essenciais  garantia das condies de estabilidade, segurana ou salubridade do imvel;</p>
		<p class="corpo_lei"><strong class="destaque-lei">XX - </strong>pavimento: plano de piso;</p>
		<p class="corpo_lei"><strong class="destaque-lei">XXI - </strong>pavimento trreo: aquele definido na LPUOS;</p>
		<p class="corpo_lei"><strong class="destaque-lei">XXII - </strong>pavimento de acesso: aquele definido na LPUOS;</p>
		<p class="corpo_lei"><strong class="destaque-lei">XXIII - </strong>pea grfica: representao grfica de elementos para a compreenso de um projeto ou obra;</p>
		<p class="corpo_lei"><strong class="destaque-lei">XXIV - </strong>prgulas: vigas horizontais ou inclinadas, sem cobertura;</p>
		<p class="corpo_lei"><strong class="destaque-lei">XXV - </strong>pessoa com deficincia: aquela que tem impedimento de longo prazo de natureza fsica, mental, intelectual ou sensorial, o qual, em interao com uma ou mais barreiras, pode obstruir sua participao plena e efetiva na sociedade em igualdade de condies com as demais pessoas;</p>
		<p class="corpo_lei"><strong class="destaque-lei">XXVI - </strong>pessoa com mobilidade reduzida: aquela que tenha, por qualquer motivo, dificuldade de movimentao, permanente ou temporria, gerando reduo efetiva da mobilidade, da flexibilidade, da coordenao motora ou da percepo, incluindo idoso, gestante, lactante, pessoa com criana de colo e obeso;</p>
		<p class="corpo_lei"><strong class="destaque-lei">XXVII - </strong>reconstruo: obra destinada  recuperao e recomposio de uma edificao, motivada pela ocorrncia de incndio ou outro sinistro, mantendo-se as caractersticas anteriores, observadas as condies de adaptao  segurana de uso e de acessibilidade estabelecidas nesta lei;</p>
		<p class="corpo_lei"><span class="bolinha">XXVIII</span><strong class="destaque-lei"> - </strong>recuo: distncia entre o limite externo da edificao e a divisa do lote, medida perpendicularmente a esta;</p>
		<p class="corpo_lei"><strong class="destaque-lei">XXIX - </strong>reforma: interveno na edificao que implique alterao da rea construda ou da volumetria, com a simultnea manuteno de parte ou de toda a rea existente, com ou sem mudana de uso;</p>
		<p class="corpo_lei"><strong class="destaque-lei">XXX - </strong>reforma sem acrscimo de rea: interveno na edificao sem alterao da rea construda, que implique em modificao da estrutura, p-direito ou compartimentao vertical, com ou sem mudana de uso;</p>
		<p class="corpo_lei"><strong class="destaque-lei">XXXI - </strong>reparo: obra ou servio destinado  manuteno de um edifcio, sem implicar mudana de uso, acrscimo ou supresso de rea, alterao da estrutura, da compartimentao horizontal e vertical, da volumetria, e dos espaos destinados  circulao, iluminao e ventilao;</p>
		<p class="corpo_lei"><strong class="destaque-lei">XXXII - </strong>requalificao: interveno em edificao existente, visando  adequao e modernizao das instalaes, com ou sem mudana de uso;</p>
		<p class="corpo_lei"><strong class="destaque-lei">XXXIII - </strong>salincia: elemento arquitetnico, engastado ou aposto na edificao ou muro, tais como aba horizontal e vertical, marquise, jardineira, floreira, ornamento e brise;</p>
		<p class="corpo_lei"><span class="bolinha">XXXIV</span><strong class="destaque-lei"> - </strong>tapume: vedao provisria usada durante a construo, visando  proteo de terceiros e ao isolamento da obra ou servio;</p>
		<p class="corpo_lei"><span class="bolinha">XXXV</span><strong class="destaque-lei"> - </strong>terrao aberto: pea justaposta  edificao, constituda em balco aberto, sem ou com vedao, desde que retrtil ou vazada do tipo quebra-sol, em balano ou no, complementar  unidade residencial ou no residencial, no abrigando funo essencial ao pleno funcionamento da unidade;</p>

