					<p class="g-pstyle0">Lei Art. 3&ordm; Inciso VI, VII, XV, XVIII, XXXIV e XXXV</p>
				</div>
				<div id="g-ai0-2" class="g-Camada_1 g-aiAbs g-aiPointText" style="top:24.6689%;margin-top:-11.9px;left:0.9375%;width:71px;">
					<p class="g-pstyle1"><span class="g-cstyle0"></span>TI<span class="g-cstyle1">C</span><span class="g-cstyle2">O</span></p>
				</div>
				<div id="g-ai0-3" class="g-Camada_1 g-aiAbs g-aiPointText" style="top:24.6689%;margin-top:-11.9px;left:76.6679%;width:98px;">
