					<p class="g-pstyle2">RECUO</p>
				</div>
				<div id="g-ai0-12" class="g-Camada_1 g-aiAbs g-aiPointText" style="top:67.3055%;margin-top:-11.4px;left:51.3184%;width:90px;">
					<p class="g-pstyle2">TAPUME</p>
				</div>
				<div id="g-ai0-13" class="g-Camada_1 g-aiAbs g-aiPointText" style="top:67.3055%;margin-top:-11.4px;left:76.4583%;width:173px;">
					<p class="g-pstyle2">TERRAO ABERTO</p>
				</div>
				<div id="g-ai0-14" class="g-Camada_1 g-aiAbs" style="top:69.8149%;left:0.9375%;width:22.7083%;">
					<p>Edificao secundria ou parte da edificao que, funcionalmente, complemente a atividade desenvolvida no imvel.</p>
				</div>
				<div id="g-ai0-15" class="g-Camada_1 g-aiAbs" style="top:69.8149%;left:26.0417%;width:22.7083%;">
					<p>Distncia perpendicular entre o limite externo da edificao e a divisa do lote.</p>
				</div>
				<div id="g-ai0-16" class="g-Camada_1 g-aiAbs" style="top:69.8149%;left:51.3184%;width:22.7083%;">
					<p>Vedao provisria usada durante a construo, visando  proteo de terceiros e ao isolamento da obra ou servio.</p>
				</div>
				<div id="g-ai0-17" class="g-Camada_1 g-aiAbs" style="top:69.8149%;left:76.4583%;width:22.7083%;">
					<p>Pea justaposta  edificao, constituda em balco aberto, sem ou com vedao, desde que retrtil ou vazada do tipo quebra-sol, no abrigando funo essencial ao pleno funcionamento da unidade.</p>
				</div>
			</div>
		</div>

		<p class="corpo_lei"><strong class="destaque-lei">XXXVI - </strong>uso privado: espao ou compartimento de utilizao exclusiva da populao permanente da edificao;</p>
		<p class="corpo_lei"><strong class="destaque-lei">XXXVII - </strong>uso restrito: espao, compartimento, ou elemento interno ou externo, disponvel estritamente para pessoas autorizadas.</p>
		<p class="corpo_lei"><strong class="destaque-lei">Pargrafo nico. </strong>Ficam tambm adotadas as seguintes abreviaes:</p>
		<p class="corpo_lei"><strong class="destaque-lei">I - </strong>ART  Anotao de Responsabilidade Tcnica perante o CREA;</p>
		<p class="corpo_lei"><strong class="destaque-lei">II - </strong>CAU  Conselho de Arquitetura e Urbanismo;</p>
		<p class="corpo_lei"><strong class="destaque-lei">III - </strong>COE  Cdigo de Obras e Edificaes;</p>
		<p class="corpo_lei"><strong class="destaque-lei">IV - </strong>CREA  Conselho Regional de Engenharia e Agronomia;</p>
		<p class="corpo_lei"><strong class="destaque-lei">V - </strong>LOE  Legislao de Obras e Edificaes, incluindo o COE;</p>
		<p class="corpo_lei"><strong class="destaque-lei">VI - </strong>LPUOS  Legislao de Parcelamento, Uso e Ocupao do Solo;</p>
		<p class="corpo_lei"><strong class="destaque-lei">VII - </strong>PDE  Plano Diretor Estratgico;</p>
		<p class="corpo_lei"><strong class="destaque-lei">VIII - </strong>Prefeitura  Prefeitura do Municpio de So Paulo;</p>
		<p class="corpo_lei"><strong class="destaque-lei">IX - </strong>RRT  Registro de Responsabilidade Tcnica perante o CAU.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 5 </strong>Para fins de aplicao das disposies do COE e deste Cdigo ficam adotadas, alm das dispostas em lei, as seguintes definies: </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">I - </strong>baixa de responsabilidade tcnica: a cincia, por parte da Prefeitura, do desligamento do profissional responsvel pela execuo da obra regularmente licenciada, autorizada ou comunicada; <strong class="destaque-decreto"></strong></p>
		<p class="corpo_decreto"><strong class="destaque-decreto">II - </strong>assuno de responsabilidade tcnica: a aceitao, por parte da Prefeitura, de novo profissional, legalmente habilitado, que passa a responder pela direo tcnica relativa  execuo de obra regularmente licenciada, autorizada ou comunicada; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">III - </strong>transferncia de responsabilidade tcnica: a ocorrncia simultnea de baixa e assuno de responsabilidade tcnica. <strong class="destaque-decreto"></strong></p>
		<p class="corpo_decreto"><strong class="destaque-decreto">IV - </strong>demolio: total ou parcial derrubamento da edificao existente.</p>
	</div>
  </div>
</template>

