<template>
	<div class="Capitulo5">
		
		<h1 class="cap_lei">CAPTULO V  DA EDIFICAO EXISTENTE</h1>

		<h2 class="secao_lei">Seo I - Da Edificao Regular</h2>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 73. </strong>Para os fins de aplicao deste Cdigo, a edificao existente  considerada regular quando:</p>
		<p class="corpo_lei"><strong class="destaque-lei">I - </strong>tiver Certificado de Concluso ou documento equivalente;</p>
		<p class="corpo_lei"><strong class="destaque-lei">II - </strong>constar do Setor de Edificaes Regulares do Cadastro de Edificaes do Municpio.</p>
		<p class="corpo_lei"><strong class="destaque-lei">Pargrafo nico. </strong>A edificao cuja rea seja menor ou apresente divergncia de, no mximo, 5% (cinco por cento) para maior em relao  rea constante do documento utilizado para a comprovao de sua regularidade  considerada como regular para fins de aplicao da LPUOS e COE, em especial as disposies deste Captulo V.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 64. </strong>A edificao ser considerada regular no Cadastro de Edificaes do Municpio, institudo pela Lei n 8.382, de 13 de abril de 1976, quando: </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">I -</strong> for objeto de Certificado de Concluso ou documento equivalente; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">II -</strong> se encontrar lanada como regular.</p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 1 </strong> aceita como regular a edificao cuja rea seja menor ou apresente divergncia de, no mximo, 5% (cinco por cento) para maior, em relao  rea constante no documento utilizado para a comprovao de sua regularidade. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 2 </strong>As unidades condominiais regulares no Cadastro de Edificaes do Municpio que, por fora de modificao na conveno do condomnio ou reviso do lanamento tributrio, tiverem alteradas a sua rea construda e/ou a frao ideal, mantero a sua regularidade. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 3 </strong>A edificao cuja titularidade seja de pessoa jurdica de direito pblico do Municpio, do Estado de So Paulo e da Unio Federal e respectivas autarquias universitrias, ainda que implantada em imvel no constante do Cadastro de Edificaes do Municpio, fica considerada regular na situao existente em 31 de julho de 2014, data da Lei n 16.050, de 2014, cuja comprovao se dar por documentao fixada por portaria. </p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 65. </strong>O Certificado de Regularidade de Edificao  o documento emitido com base no Cadastro de Edificaes do Municpio, sendo equivalente ao Certificado de Concluso, podendo ser usado para comprovao de regularidade da edificao. </p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 66. </strong>A edificao ser considerada irregular no Cadastro de Edificaes do Municpio quando: </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">I -</strong> no se enquadrar nas hipteses previstas no caput do artigo 73 do COE e no caput do artigo 65 deste decreto; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">II -</strong> for objeto de Certificado de Concluso ou documento equivalente que tenha sido anulado ou cassado; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">III -</strong> estiver em desacordo com o Certificado de Concluso ou documento equivalente; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">IV -</strong> for objeto de Auto de Irregularidade ou documento equivalente. </p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 67. </strong>As edificaes includas no Setor de Edificaes Irregulares do Cadastro de Edificaes do Municpio podero ser transferidas a qualquer tempo para o Setor de Edificaes Regulares, mediante a apresentao, pelo interessado, de documentos que comprovem a regularidade. </p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 68. </strong>A Notificao de Irregularidade  o documento emitido pelo Cadastro de Edificaes do Municpio para atestar a irregularidade da edificao perante a legislao edilcia. </p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 69. </strong>A pedido do interessado, ser emitido Histrico da Edificao, documento que comprova a situao da edificao a partir de 1 de janeiro de 1976, no que se refere  sua regularidade ou irregularidade perante a legislao edilcia, com a eventual alterao da rea construda ao longo do tempo. </p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 70. </strong>A incluso da edificao no Setor de Edificaes Regulares ou no Setor de Edificaes Irregulares do Cadastro de Edificaes do Municpio estar sujeita  anlise individualizada do caso, independentemente do lanamento tributrio pela Secretaria Municipal da Fazenda. </p>
		<h2 class="secao_lei">Seo II - Da Reforma</h2>
		<p class="corpo_lei"><span class="bolinha">Art. 74.</span><strong class="artigo-lei"> </strong>A edificao regularmente existente pode ser reformada desde que a edificao resultante no crie nem agrave eventual desconformidade com a LOE, PDE ou LPUOS.</p>
		<p class="corpo_decreto"><span class="bolinha">Art. 71.</span><strong class="artigo-decreto"> </strong>Mediante procedimento administrativo e a pedido do interessado, a Prefeitura emitir Alvar de Aprovao de reforma para edificao existente que seja objeto de interveno e implique em alterao de rea construda, volumetria, modificao de estrutura, p-direito ou compartimentao vertical, com ou sem mudana de uso. <strong class="subtitulo_txt"></strong></p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 72. </strong>A cobrana de TEV/COE pelo pedido de Alvar de Aprovao de reforma incidir sobre a rea a ser reformada. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 1 </strong>As reas a serem eventualmente acrescidas e/ou regularizadas sero cobradas pelo valor da TEV/COE da rea a ser construda e rea a ser regularizada, respectivamente, conforme Anexo II do COE. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 2 </strong>A mera mudana de uso, sem alterao de ordem fsica no imvel, no incidir na cobrana de TEV/COE para Alvar de reforma. </p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 73. </strong>Em caso de necessidade de adaptao da edificao, ser expedida, pela Prefeitura, Notificao de Exigncias Complementares  NEC ou Intimao para Execuo de Obras e Servios  IEOS, com prazo para atendimento de at 180 (cento e oitenta) dias para a supresso das infraes  LOE e  LPUOS ou adaptao s normas de segurana ou acessibilidade, sendo que o prazo para despacho ficar suspenso durante a pendncia do atendimento das exigncias. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">Pargrafo nico. </strong>No caso de adaptao s normas de segurana ou acessibilidade, a IEOS produzir os mesmos efeitos do Alvar de Aprovao e do Alvar de Execuo para as obras necessrias  adaptao da edificao, mesmo que resulte em aumento da rea edificada.</p>

		<div id="g-05_capitulo-V_d01-box" class="ai2html">
			<div id="g-05_capitulo-V_d01-01" class="g-artboard g-artboard-v4" data-min-width='960'>
				<img id="g-05_capitulo-V_d01-01-img" class="g-aiImg" :src="caminho + '05_capitulo-V_d01-01.png'"/>
				<div id="g-ai0-1" class="g-Camada_1 g-aiAbs g-aiPointText" style="top:3.6384%;margin-top:-11.4px;left:0%;width:103px;">
					<p class="g-pstyle0">REFORMA</p>
				</div>
				<div id="g-ai0-2" class="g-Camada_1 g-aiAbs g-aiPointText" style="top:3.5321%;margin-top:-9.8px;left:82.4907%;width:190px;">
					<p class="g-pstyle1">Lei Art. 74, Decreto  Art. 71</p>
				</div>
				<div id="g-ai0-3" class="g-Camada_1 g-aiAbs" style="top:8.3929%;left:0%;width:67.5%;">
					<p>Conside<span class="g-cstyle0">r</span>a-se <span class="g-cstyle1">r</span>e<span class="g-cstyle2">f</span>o<span class="g-cstyle3">r</span>ma a in<span class="g-cstyle4">t</span>e<span class="g-cstyle5">r</span><span class="g-cstyle4">v</span>eno em edicao que al<span class="g-cstyle4">t</span>e<span class="g-cstyle6">r</span>e sua <span class="g-cstyle6">r</span>ea const<span class="g-cstyle3">r</span>uda ou <span class="g-cstyle4">vo</span>lumetria, com a simul<span class="g-cstyle7">t</span>nea manu<span class="g-cstyle4">t</span>eno de <span class="g-cstyle8">p</span>ar<span class="g-cstyle4">t</span>e ou de <span class="g-cstyle9">t</span>oda a <span class="g-cstyle6">r</span>ea exis<span class="g-cstyle4">t</span>en<span class="g-cstyle4">t</span>e, im<span class="g-cstyle10">p</span>licando ou no na mudana de uso:</p>
				</div>
				<div id="g-ai0-4" class="g-Camada_1 g-aiAbs" style="top:43.2143%;left:82.4241%;margin-left:-100.5px;width:20.9375%;">
					<p class="g-pstyle2">Edificao regularmente existente</p>
				</div>
				<div id="g-ai0-5" class="g-Camada_1 g-aiAbs" style="top:48.2143%;left:25.9295%;margin-left:-100.5px;width:20.9375%;">
					<p class="g-pstyle2">reas a acrescentar (reforma)</p>
				</div>
			</div>
		</div>

		<p class="corpo_lei"><strong class="artigo-lei">Art. 75. </strong>A edificao existente irregular, no todo ou em parte, que atenda ao disposto na LOE, PDE ou LPUOS pode ser regularizada e reformada, expedindo-se o Certificado de Regularizao para a rea a ser regularizada e Alvar de Aprovao para a reforma pretendida.</p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 76. </strong>A edificao irregular, no todo ou em parte, que no atenda na parte irregular ao disposto na LOE, PDE ou LPUOS pode ser reformada desde que seja prevista a supresso da infrao.</p>
		<p class="corpo_lei"><strong class="destaque-lei">Pargrafo nico. </strong>No caso previsto no caput deste artigo, o Certificado de Concluso para a reforma, parcial ou total, s pode ser concedido aps a supresso da infrao.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 74. </strong>Para a adaptao da edificao irregular, poder ser emitido somente Alvar de Aprovao de reforma, sendo que no ser concedido Certificado de Concluso, ainda que parcial, sem que a infrao  LOE ou  LPUOS tenha sido suprimida.</p>
		<h2 class="secao_lei">Seo III - Da Requalificao</h2>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 77. </strong>A edificao existente licenciada de acordo com a legislao edilcia vigente anteriormente a 23 de setembro de 1992, data da entrada em vigor da Lei n 11.228, de 25 de junho de 1992, e com rea regular lanada no Cadastro de Edificaes do Municpio pelo perodo mnimo de 10 (dez) anos, independentemente de sua condio de regularidade na data do protocolo do pedido, pode ser requalificada, nos termos deste Cdigo.</p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 78. </strong>Na requalificao, so aceitas solues que, por implicao de carter estrutural, no atendam s disposies previstas na LOE, PDE ou LPUOS, desde que no comprometam a salubridade, nem acarretem reduo de acessibilidade e de segurana de uso.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 1 </strong>As disposies deste artigo referem-se a condies existentes de implantao, iluminao, insolao, circulao, acessibilidade, estacionamento de veculos e segurana de uso da edificao.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 2 </strong>O projeto deve observar solues de acessibilidade que atendam aos princpios da <strong>adaptao razovel</strong><sup class="referencias-notas">21</sup> de acordo com o estabelecido neste Cdigo.</p>
		
		<div id="05_capitulo-V_b01" class="glossario-box">
			<p class="corpo_referencias1"><sup>21 </sup>Modificaes e ajustes necessrios e adequados que no acarretem nus desproporcional ou indevido, quando requeridos em cada caso, a fim de assegurar que as pessoas com deficincia ou mobilidade reduzida possam gozar ou exercer, em igualdade de oportunidades com as demais pessoas, os direitos humanos e liberdades fundamentais.</p>
		</div>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 75. </strong>Na requalificao de edificao existente,  admitida a ampliao da rea construda para suprir as necessidades de adequao e modernizao das instalaes da edificao.</p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 1 </strong> considerado no computvel o aumento de rea destinado  adaptao razovel  acessibilidade e  melhoria das condies de segurana de uso, higiene e salubridade da edificao existente, no sendo considerado para efeito de clculo do coeficiente de aproveitamento e da taxa de ocupao previstos na LPUOS. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 2 </strong>As intervenes podero ocupar as faixas de recuo quando no for possvel atend-las nas prprias edificaes. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 3 </strong>Excepcionalmente, podero ser aceitas rampas de acesso ao imvel que avancem sobre o logradouro pblico para atendimento das condies de acessibilidade, mediante anlise dos rgos municipais competentes. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 4 </strong>As intervenes na edificao no podero agravar os itens relativos  segurana, salubridade, higiene e acessibilidade at ento existentes. </p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 76. </strong>Nos pedidos de requalificao da edificao existente com ampliao de rea construda, devero ser apresentados os seguintes documentos assinados pelo proprietrio ou possuidor, responsvel(eis) tcnico(s) pelo projeto e pelos equipamentos, acompanhados das respectivas ART(s) e/ou RRT(s): </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">I -</strong> memorial justificativo das instalaes propostas compatveis com a requalificao pretendida; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">II -</strong> declarao de que o acrscimo  imprescindvel para a adequao da edificao, nos termos do artigo 75 deste decreto. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">Pargrafo nico. </strong>Solues tcnicas alternativas podero ser submetidas  apreciao da Comisso de Edificaes e Uso do Solo  CEUSO. </p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 77. </strong>Os pedidos de requalificao com acrscimo de rea no enquadrados nesta seo sero analisados e considerados no pedido de Alvar de reforma. </p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 78. </strong>Nas requalificaes, a mudana de uso da edificao ser admitida, desde que para uso permitido pela legislao vigente, observadas as condies de instalao e os parmetros de incomodidade estabelecidos na LPUOS. </p>
		<h2 class="secao_lei">Seo IV - Da Reconstruo</h2>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 79. </strong>A edificao regular pode ser reconstruda, no todo ou em parte, em caso de ocorrncia de incndio ou outro sinistro.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 1 </strong>Na reconstruo, deve ser prevista a adaptao da edificao s condies de segurana de uso e de acessibilidade de acordo com o estabelecido neste Cdigo.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 2 </strong>No caso previsto no  1 deste artigo ou quando se pretenda introduzir alteraes em relao  edificao anteriormente existente, a rea a ser acrescida deve ser analisada como reforma.</p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 80. </strong>A Prefeitura pode recusar, no todo ou em parte, a reconstruo nos moldes anteriores de edificao com ndices e volumetria em desacordo com o disposto na LOE, PDE ou LPUOS que seja considerada prejudicial ao interesse urbanstico.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 79. </strong>A edificao regular, em caso de ocorrncia de incndio ou outro sinistro, pode ser reconstruda, no todo ou em parte, dependendo de sua conformidade com a LOE e a LPUOS. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 1 </strong>A reconstruo de edificao que abrigava uso instalado irregularmente, em desacordo com o disposto na LPUOS, s ser permitida se for destinada a uso permitido na zona. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 2 </strong>A edificao deve ser adaptada s disposies de segurana de uso e de acessibilidade previstas no COE. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 3 </strong>Nos casos em que se pretendam acrscimos de rea ou volumetria em relao  edificao anteriormente existente, essas alteraes sero analisadas como reforma. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 4 </strong>A critrio da CEUSO, ouvida a Comisso Tcnica de Legislao Urbanstica  CTLU, a Prefeitura poder recusar, no todo ou em parte, a reconstruo nos moldes anteriores, de edificao com ndices e volumetria em desacordo com o disposto na LOE ou na LPUOS vigentes quando do protocolo do pedido, que seja considerada prejudicial ao interesse urbanstico.</p>
	</div>
</template>

