	<div class="Capitulo8">

		<h1 class="cap_lei">CAPTULO VIII  DAS DISPOSIES FINAIS E TRANSITRIAS</h1>

		<p class="corpo_lei"><span class="bolinha">Art. 108.</span><strong class="artigo-lei"> </strong>Para fins de aplicao dos ndices de ocupao e aproveitamento do solo, observados os limites estabelecidos na LPUOS, no  considerada <strong>rea construda computvel</strong><sup>22</sup>:</p>

		<div id="08_capitulo-VIII_b01" class="glossario-box">
			<p class="corpo_referencias1"><sup>22 </sup><span class="glossario-museo">rea computavel</span>  a soma das reas cobertas de todos os pavimentos de uma edificao, que so consideradas para o clculo do coeficiente de aproveitamento.</p>
			<p class="corpo_referencias2"><span class="glossario-museo">rea no computvel</span>  a soma das reas cobertas de uma edificao no considerada para o clculo do coeficiente de aproveitamento, nos termos dispostos na legislao pertinente.<span class="glossario-museo"></span></p>
			<h6 class="corpo-referencias-pequeno">Fonte: Quadro 01 da Lei n 16.050/14</h6>
		</div>

		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 102. </strong>Para fins de aplicao dos ndices de ocupao e aproveitamento do solo, observados os limites estabelecidos na LPUOS, so consideradas reas construdas no computveis aquelas constantes do artigo 108 do COE, atendidas as seguintes regras:<strong class="subtitulo_txt"></strong></p>
		<p class="corpo_lei"><strong class="destaque-lei">I - </strong>o terrao aberto, com rea construda mxima por pavimento equivalente a 5% (cinco por cento) da rea do terreno;</p>
		<p class="corpo_decreto"><strong class="destaque-decreto">I -</strong> no terrao aberto, com rea construda mxima por pavimento equivalente a 5% (cinco por cento) da rea do terreno, deve ser observado o remanescente do imvel quando o terreno for objeto de doao de rea para alargamento de passeio ou por melhoramento pblico; </p>
		<p class="corpo_lei"><strong class="destaque-lei">II - </strong>o mobilirio definido como jirau, constitudo de estrado ou passadio, inclusive em estrutura metlica instalado a meia altura em compartimento, com p-direito mximo de 2,30 m (dois metros e trinta centmetros), sem permanncia humana prolongada, ocupando, no mximo, 30% (trinta por cento) da rea do compartimento;</p>
		<p class="corpo_decreto"><strong class="destaque-decreto">II -</strong> o mobilirio definido como jirau, constitudo de estrado ou passadio ou piso similar, inclusive em estrutura metlica, nos termos do inciso II do artigo 108 do COE, deve limitar-se a 250 m (duzentos e cinquenta metros quadrados); </p>
		<p class="corpo_lei"><strong class="destaque-lei">III - </strong>os demais tipos de mobilirio e a obra complementar com rea construda de at 30,00 m (trinta metros quadrados);</p>
		<p class="corpo_decreto"><strong class="destaque-decreto">III -</strong> os demais tipos de obras complementares  edificao e de mobilirios ficam sujeitos s normas e aos parmetros constantes deste artigo, conforme as Tabelas 1 e 2 do Anexo IV deste decreto; </p>
		<p class="corpo_lei"><strong class="destaque-lei">IV - </strong>a salincia, com as seguintes caractersticas e dimenses em relao ao plano da fachada da edificao:</p>
		<p class="corpo_lei"><strong class="destaque-lei">a)</strong> elemento arquitetnico, ornato, ornamento, jardineira, floreira, brise, aba horizontal e vertical, com at 0,40 m (quarenta centmetros) de profundidade;</p>
		<p class="corpo_lei"><strong class="destaque-lei">b)</strong> viga, pilar com at 0,40 m (quarenta centmetros) de avano;</p>
		<p class="corpo_lei"><strong class="destaque-lei">c)</strong> beiral da cobertura com at 1,50 m (um metro e meio) de largura;</p>
		<p class="corpo_lei"><strong class="destaque-lei">d)</strong> marquise em balano, no sobreposta, que avance, no mximo, at 50% (cinquenta por cento) das faixas de recuo obrigatrio e com rea mxima de 30,00 m (trinta metros quadrados);</p>
		<p class="corpo_decreto"><strong class="destaque-decreto">IV -</strong> a implantao de salincias  edificao ficar sujeita s normas e aos parmetros deste artigo, conforme a Tabela 3 do Anexo IV deste decreto;<strong class="subtitulo_txt"></strong></p>
		<p class="corpo_lei"><strong class="destaque-lei">V - </strong>a rea tcnica, sem permanncia humana, destinada a instalaes e equipamentos;</p>
		<p class="corpo_decreto"><strong class="destaque-decreto">V -</strong> quanto  rea tcnica, sem permanncia humana, destinada a instalaes e equipamentos, em caso de dvida quanto ao enquadramento, pode ser consultado CEUSO para anlise e manifestao, bem como ser solicitado: </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">a)</strong> memorial justificativo das instalaes propostas compatveis com as reas tcnicas propostas, devidamente assinado pelo responsvel tcnico; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">b)</strong> quadro de reas total e por ambiente, correspondente s reas tcnicas propostas;</p>
		<p class="corpo_lei"><strong class="destaque-lei">VI - </strong>no pavimento destinado a estacionamento de veculos, motocicletas e bicicletas:</p>
		<p class="corpo_lei"><strong class="destaque-lei">a)</strong> o compartimento de uso comum de apoio ao uso da edificao, tal como vestirio, instalao sanitria e depsitos;</p>
		<p class="corpo_lei"><strong class="destaque-lei">b)</strong> as reas de uso comum de circulao de pedestres, horizontal e vertical;</p>
		<p class="corpo_decreto"><strong class="destaque-decreto">VI -</strong> no compartimento de uso comum de apoio ao uso da edificao, localizado no pavimento destinado a estacionamento de veculos, motocicletas e bicicletas, o vestirio de usurio de bicicleta deve ter rea mxima de 20 m (vinte metros quadrados) para os usos residenciais e 40 m (quarenta metros quadrados) para os no residenciais;<strong class="subtitulo_txt"></strong></p>
		<p class="corpo_lei"><strong class="destaque-lei">VII - </strong>no prdio residencial as reas cobertas de uso comum localizadas:</p>
		<p class="corpo_lei"><strong class="destaque-lei">a)</strong> no pavimento trreo;</p>
		<p class="corpo_lei"><strong class="destaque-lei">b)</strong> em qualquer pavimento, observado o limite de 3,00 m (trs metros quadrados) por habitao;</p>
		<p class="corpo_decreto"><strong class="destaque-decreto">VII - </strong>no prdio residencial, as reas cobertas de uso comum, quando localizadas em qualquer pavimento, observado o limite de 3 m (trs metros quadrados) por habitao, nos termos da alnea b do inciso VII do artigo 108 do COE, devem ser destinadas a lazer;<strong class="subtitulo_txt"></strong></p>
		<p class="corpo_lei"><strong class="destaque-lei">VIII - </strong>no prdio de uso no residencial:</p>
		<p class="corpo_lei"><strong class="destaque-lei">a)</strong> o pavimento trreo sem vedao, sendo admitido o fechamento do controle de acesso e as caixas de escada da edificao;</p>
		<p class="corpo_lei"><strong class="destaque-lei">b)</strong> a circulao vertical de uso comum;</p>
		<p class="corpo_decreto"><strong class="destaque-decreto">VIII -</strong> No prdio de uso no residencial:<strong class="destaque-decreto"></strong></p>
		<p class="corpo_decreto"><strong class="destaque-decreto">a)</strong> no pavimento trreo sem vedao, admite-se o fechamento do controle de acesso, caixas de escada da edificao e compartimentos de apoio, limitado a 30% (trinta por cento) da rea total do pavimento;</p>
		<p class="corpo_decreto"><strong class="destaque-decreto">b)</strong> nas coberturas de bombas nos postos de combustveis, desde que sem vedao, admite-se o avano de at 50% (cinquenta por cento) dos recuos estabelecidos pela LPUOS;</p>
		<p class="corpo_decreto"><strong class="destaque-decreto">c)</strong> a circulao vertical de uso comum inclui os espaos de circulao vertical protegidos e as reas de escadas de ligao ao heliponto. </p>
		<p class="corpo_lei"><strong class="destaque-lei">IX - </strong>(VETADO)</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 1 </strong>A rea construda do abrigo de lixo pode ser superior ao estabelecido no inciso III do caput deste artigo, quando tecnicamente justificado.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 2 </strong>As salincias a que se referem as alneas a, b e c do inciso IV do caput deste artigo no so consideradas para fins do clculo da rea construda e podem ocupar as faixas de recuo estabelecidas na LPUOS e dos afastamentos previstos neste Cdigo.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 3 </strong>Quando o recuo de frente for dispensado pela LPUOS admite-se o avano at 0,40 m (quarenta centmetros) de elemento arquitetnico, ornato, ornamento, jardineira, floreira, brise, aba horizontal e vertical, e terraos sobre o passeio pblico, desde que observada a altura livre de 3,00 m (trs metros) do nvel do passeio e que no interfira nas instalaes pblicas.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 4 </strong>No equipamento destinado  prestao de servio pblico de uso coletivo, a cobertura de quadra esportiva, quando destinada  captao de gua de chuva ou  instalao de sistema de energia solar de aquecimento de gua para utilizao pela prpria atividade desenvolvida na edificao, no ser computada no clculo do coeficiente de aproveitamento e da taxa de ocupao.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 5 </strong>As reas sob a projeo das salincias podero ser consideradas para clculo para os ndices de permeabilidade.</p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 1 </strong>As salincias a que se referem as alneas a, b e c do inciso IV do artigo 108 do COE no so consideradas para fins de clculo da rea construda desde que respeitados estritamente os parmetros da Tabela 3 do Anexo IV deste decreto. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 2 </strong>O elemento que ultrapassar qualquer limite das Tabelas 1 e 2 do Anexo IV deste decreto ser considerado no todo para efeito de observncia aos ndices estabelecidos pela LPUOS e pelo COE. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 3 </strong>Ser considerada computvel apenas a rea excedente aos limites fixados para terraos, jiraus e elementos fixados na Tabela 3 do Anexo IV deste decreto. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 4 </strong>O terrao aberto poder avanar at 10% (dez por cento) do afastamento A previsto nas condies de aerao e insolao do COE e deste decreto e at 20% (vinte por cento) sobre os recuos estabelecidos na LPUOS. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 5 </strong>O mobilirio definido como prgula, quando respeitado o limite estabelecido na Tabela 2 do Anexo IV deste decreto poder avanar, em qualquer pavimento, at 10% (dez por cento) do afastamento A previsto nas condies de aerao e insolao do COE e deste decreto e poder avanar sobre os recuos definidos pela LPUOS. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 6 </strong>O somatrio das obras complementares e mobilirio dever ocupar, no mximo, os limites estabelecidos para a rea livre do terreno, conforme Tabela 4 do Anexo IV deste decreto.</p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 7 </strong>As reas sob a projeo das salincias e terraos podero ser consideradas para clculo da taxa de permeabilidade. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 8 </strong>Para fins de aplicao deste artigo, quando houver divergncia entre a rea constante do documento de propriedade apresentada e as apuradas no levantamento topogrfico, ser considerada a menor rea apurada. </p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 103. </strong>As reas no computveis previstas no artigo 108 do COE so aplicveis aos pedidos relativos a projetos de edificao nova e aos acrscimos de rea em reforma protocolados a partir da data da vigncia do COE e deste decreto e aos processos em andamento nos termos da Lei n 16.402, de 22 de maro de 2016, com opo de anlise de acordo com a Lei n 16.642, de 2017. </p>

