		<p class="corpo_lei">II - AET 2  na rea externa ao Minianel Virio: vias classificadas pela legislao vigente como Estruturais N1, N2 e N3;</p>
		<p class="corpo_lei">III - AET 3  reas de Operao Urbana: em todas as vias, independentemente de sua classificao.</p>
		<p class="corpo_lei"> 2 Para fins de enquadramento das edificaes como Polos Geradores de Trfego  PGT, so consideradas vagas de estacionamento de veculos o somatrio das vagas oferecidas no projeto, excetuadas aquelas destinadas a carga e descarga, atendimento mdico de emergncia, segurana, motocicletas e bicicletas. <strong class="destaque-lei"></strong></p>
		<p class="corpo_lei"><strong class="destaque-lei"> </strong><span class="bolinha">Art. 13.</span> A Certido de Diretrizes  documento obrigatrio para a obteno de Alvar de Aprovao e Execuo, de Alvar de Execuo e de Certificado de Regularizao para empreendimento qualificado como Polo Gerador de Trfego.</p>
		<p class="corpo_lei"> 1 Quando se tratar de pedido de Alvar de Aprovao, este ser expedido mediante manifestao favorvel da CET em relao ao cumprimento, no projeto do empreendimento, do disposto nos incisos I a III do pargrafo nico do art. 4 desta lei.</p>
		<p class="corpo_lei"> 2 No caso de pedido de Certificado de Regularizao, tambm dever ser apresentado o Termo de Recebimento e Aceitao Definitivo  TRAD.</p>
		<p class="corpo_lei"> 3 No caso de projeto modificativo ou de mudana do projeto no decorrer da anlise, fica dispensada a apresentao de nova certido quando as alteraes em relao ao projeto original analisado por CET:</p>
		<p class="corpo_lei">a) no impliquem em alterao de uso, categoria de uso ou subcategoria de uso;</p>
		<p class="corpo_lei">b) no ultrapassem 5% (cinco por cento) nas reas computveis e no computveis, na taxa de ocupao e no nmero de vagas para veculos;</p>
		<p class="corpo_lei">c) no impliquem em alterao de acessos de pedestres e veculos.<strong class="destaque-lei"> </strong></p>

