				<div id="g-ai0-14" class="g-Camada_1 g-aiAbs" style="top:35.6364%;left:0%;width:74.7917%;">
					<p class="g-pstyle2">Procedimentos para aprovao de projetos, execuo de obras e servios referentes a minimizao de impacto no sistema virio:</p>
				</div>
				<div id="g-ai0-15" class="g-Camada_1 g-aiAbs g-aiPointText" style="top:50.7545%;margin-top:-8.3px;left:15.1945%;width:143px;">
