				</div>
				<div id="g-ai0-16" class="g-Camada_1 g-aiAbs" style="top:52.5391%;left:77.6042%;width:20.9375%;">
					<p class="g-pstyle6">* Em caso de projeto modificado ou de mudana do projeto no decorrer da anlise, fica dispensada a apresentao de nova certido desde que as alteraes em relao ao projeto original ampliado pela CET:</p>
					<p class="g-pstyle6">a) no impliquem em alterao de uso, categoria de uso ou subcategoria de uso;</p>
					<p class="g-pstyle6">b) no ultrapassem 5% (cinco porcento) nas reas computveis e no computveis na taxa de ocupao e no nmero de vagas para veculos;</p>
					<p class="g-pstyle6">c) no impliquem em alterao de acessos de pedestres e veculos.</p>
				</div>
