		<p class="corpo_lei"><strong class="artigo-lei">Art. 111. </strong>Os arts. 82 e 83 da Lei n 15.764, de 27 de maio de 2013, passam a vigorar com as seguintes alteraes:</p>
		<p class="corpo_lei"><strong class="destaque-lei"> </strong>Art. 82. ()</p>
		<p class="corpo_lei">IV - emitir parecer sobre a aplicao da legislao de edificaes e de uso, ocupao e parcelamento do solo e do Plano Diretor Estratgico  PDE, em especial no que diz respeito  implantao e afastamentos de edificao em especial relativas a nvel do pavimento trreo em funo de determinantes construtivas resultantes de:</p>
		<p class="corpo_lei">a) reas sujeitas a alagamento;</p>
		<p class="corpo_lei">b) restrio  construo de subsolo em terrenos contaminados e, quando exigido por rgo ambiental competente;</p>
		<p class="corpo_lei">c) lenol fretico em nveis prximos ao perfil do terreno;</p>
		<p class="corpo_lei">V <strong class="destaque-lei">- </strong>()</p>
		<p class="corpo_lei">VI - decidir quanto  dispensa dos recuos laterais e de fundo quando o lote vizinho apresentar edificao encostada na divisa do lote, conforme anlise caso a caso.</p>
		<p class="corpo_lei">()<strong class="destaque-lei"> </strong><strong class="destaque-decreto"></strong></p>
		<p class="corpo_lei"><strong class="destaque-lei"> </strong>Art. 83. A <strong>CEUSO</strong><sup>24</sup>  composta de 8 (oito) membros, todos com seus respectivos suplentes, sendo 4 (quatro) do Poder Pblico e 4 (quatro) da sociedade civil pertencentes a entidades ligadas  engenharia, arquitetura e construo civil, na forma definida em decreto.</p>
		<p class="corpo_lei">()<strong class="destaque-decreto"></strong></p>
		<p class="corpo_lei"> 2 Os representantes da CEUSO devem ser arquitetos ou engenheiros, podendo o representante da <strong>Secretaria Municipal dos Negcios Jurdicos</strong><sup>25</sup> ser Procurador do Municpio.</p>

		<div id="08_capitulo-VIII_b03" class="glossario-box">
			<p class="corpo_referencias1"><sup>24 </sup><span class="glossario-museo">Comisso de Edificaes e Uso do Solo  CEUSO</span> rgo normativo e consultivo sobre a legislao de obras, de edificaes, de parcelamento do solo, de acessibilidade e de segurana de uso das edificaes e equipamentos.</p>
			<p class="corpo_referencias1"><sup>25 </sup>Conforme Decreto N 57.576, a Secretaria Municipal dos Negcios Jurdicos teve sua denominao alterada para Secretaria Municipal de Justia - SMJ.</p>
		</div>

		<p class="corpo_lei">()<strong class="destaque-lei"> </strong></p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 104. </strong>Conforme previsto no artigo 111 do COE, que altera a Lei n 15.764, de 27 de maro de 2013, ficam definidos os seguintes membros da CEUSO:</p>
		<p class="corpo_decreto"><strong class="destaque-decreto">I -</strong> membros indicados pela Administrao Municipal, titular e suplente, a saber:<strong class="destaque-decreto"></strong></p>
		<p class="corpo_decreto"><strong class="destaque-decreto">a)</strong> 2 (dois) representantes da Secretaria Municipal de Urbanismo e Licenciamento  SMUL; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">b)</strong> 1 (um) representante da Secretaria Municipal das Prefeituras Regionais  SMPR; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">c)</strong> 1 (um) representante da Secretaria Municipal de Justia  SMJ;</p>
		<p class="corpo_decreto"><strong class="destaque-decreto">II -</strong> membros indicados pelas seguintes entidades, titular e suplente, a saber: </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">a)</strong> 1 (um) representante de entidades ligadas aos sindicatos e associaes de construo, incorporaes e comercializao de imveis; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">b)</strong> 1 (um) representante da Associao Brasileira dos Escritrios de Arquitetura  ASBEA;<strong class="destaque-decreto"></strong></p>
		<p class="corpo_decreto"><strong class="destaque-decreto">c)</strong> 1 (um) representante do Conselho de Arquitetura e Urbanismo de So Paulo  CAU-SP; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">d)</strong> 1 (um) representante do Conselho Regional de Engenharia e Agronomia do Estado de So Paulo  CREA-SP. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">Pargrafo nico. </strong>Os representantes devero ter formao em arquitetura ou engenharia, sendo que o representante de que trata a alnea c do inciso I do caput deste artigo poder ser Procurador do Municpio.<strong class="subtitulo_txt"></strong></p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 112. </strong>Ficam isentos do pagamento da TEV/COE e taxas em geral, bem como dispensados do pagamento dos preos pblicos os pedidos de parcelamento do solo de interesse social e de mercado popular.</p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 113. </strong>O parcelamento do solo de imvel cuja titularidade seja da Unio, do Estado e do Municpio fica sujeito ao prvio exame dos rgos municipais competentes, independendo da expedio dos documentos estabelecidos na LPUOS.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 1 </strong>Caso apresentado, o pedido de parcelamento do solo do Municpio, Estado e Unio fica isento do pagamento da TEV/COE e dispensado do pagamento dos preos pblicos.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 2 </strong>A iseno a que se refere o  1 deste artigo aplica-se, inclusive, s entidades da Administrao Pblica Indireta.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 105. </strong>Os pedidos de loteamento de imvel cuja titularidade seja da Unio, do Estado e do Municpio ficam sujeitos ao prvio exame dos rgos municipais competentes e emisso dos documentos estabelecidos pela LPUOS. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">Pargrafo nico. </strong>A Certido de Diretrizes poder ser dispensada mediante manifestao favorvel da Comisso Intersecretarial competente.</p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 114. </strong>Aplicam-se os procedimentos administrativos estabelecidos neste Cdigo aos processos em andamento na data de sua entrada em vigor.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 106. </strong>Aplicam-se os procedimentos administrativos estabelecidos no COE, aprovado pela Lei n 16.642, de 2017, e neste decreto aos processos j em andamento na data de sua entrada em vigor e sem despacho decisrio em ltima instncia. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">Pargrafo nico. </strong>Os processos em andamento que apresentarem elementos incompletos ou incorretos ou necessitarem de complementao da documentao podero ser objeto de mais 1 (um) nico comunicado (comunique-se) para que as falhas sejam sanadas, aplicando as disposies do  3 do artigo 49 deste decreto. </p>
		<p class="corpo_lei"><strong class="destaque-lei"> 1 </strong>A reconsiderao do despacho de primeira instncia administrativa protocolada anteriormente  data da entrada em vigor deste Cdigo deve ser apreciada pela segunda instncia conforme estabelecido no art. 69.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 107. </strong>A reconsiderao do despacho de primeira instncia administrativa protocolada anteriormente  data da entrada em vigor do COE e deste decreto dever ser apreciada pela segunda instncia conforme estabelecido no artigo 69 do COE.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 2 </strong>O recurso em trmite na ltima instncia decisria extinta por este Cdigo deve ser apreciado nesta instncia quando:</p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 1 </strong>O recurso em trmite na ltima instncia decisria extinta pelo COE e por este decreto deve ser apreciado nesta instncia quando:</p>
		<p class="corpo_lei"><strong class="destaque-lei">I - </strong>protocolado anteriormente  data de sua entrada em vigor;</p>
		<p class="corpo_decreto"><strong class="destaque-decreto">I -</strong> protocolado anteriormente  data de sua entrada em vigor; </p>
		<p class="corpo_lei"><strong class="destaque-lei">II - </strong>protocolado aps a data de sua entrada em vigor, porm dentro do prazo estabelecido pela legislao anterior para este fim.</p>
		<p class="corpo_decreto"><strong class="destaque-decreto">II -</strong> protocolado aps a data de sua entrada em vigor, porm dentro do prazo estabelecido pela legislao anterior para este fim. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 2 </strong>O recurso em trmite na instncia decisria da CEUSO, deve ser apreciado nesta instncia quando: </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">I -</strong> protocolado anteriormente  data de entrada em vigor do COE; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">II -</strong> protocolado aps a data de entrada em vigor do COE, porm dentro do prazo de recurso estabelecido pela legislao anterior para este fim. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 3 </strong>No caso de processos administrativos que se enquadrem no disposto no  2 deste artigo, o despacho da CEUSO encerra definitivamente a instncia administrativa. </p>
		<p class="corpo_lei"><strong class="destaque-lei"> 3 </strong>Aplicam-se as disposies dos  1 e 2 deste artigo aos processos em andamento nos termos da Lei n 11.522, de 3 de maio de 1994, Lei n 13.558, de 14 de abril de 2003, e Lei n 13.876, de 23 de julho de 2004.</p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 4 </strong>Aplicam-se, no que couber, as disposies dos  1 ao 4 deste artigo aos processos em andamento nos termos das Leis n 11.522, de 3 de maio de 1994, n 13.558, de 14 de abril de 2003, n 13.876, de 23 de julho de 2004, e n 8.382, de 1976. </p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 115. </strong>O pedido protocolado at a data do incio da vigncia deste Cdigo, ainda sem despacho decisrio ou com interposio de recurso dentro do prazo legal, deve ser analisado e decidido de acordo com os requisitos tcnicos da legislao anterior.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 1 </strong>No caso de que trata o caput deste artigo, no ser admitida qualquer mudana, alterao ou modificao que implique no agravamento das desconformidades em relao ao estabelecido neste Cdigo.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 108. </strong>Para fins de aplicao do  1 do artigo 115 do COE, entende-se por agravamento das desconformidades o acrscimo nas reas de terrao, obras complementares e mobilirios que ultrapassem os limites das reas no computveis estabelecidos no COE e neste decreto. </p>
		<p class="corpo_lei"><strong class="destaque-lei"> 2 </strong>Por opo e a pedido do interessado, a anlise e deciso podem ocorrer integralmente nos termos deste Cdigo.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 3 </strong>No caso previsto no  2 deste artigo, no ser admitida a apresentao do projeto simplificado previsto neste Cdigo, devendo o interessado, para tanto, desistir do processo em aberto, protocolar novo pedido e recolher as taxas devidas.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 109. </strong>Nos processos de Alvar em andamento e sem despacho decisrio em ltima instncia podero ser analisados nos termos do COE e deste decreto, desde que o interessado formalize o respectivo pedido at o dia 15 de dezembro de 2017, sem a apresentao de projeto simplificado. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 1 </strong>Aps essa data, o interessado dever protocolar novo pedido e recolher as taxas e preos pblicos devidos. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 2 </strong>Na hiptese prevista no caput deste artigo, os documentos a serem emitidos, as taxas e preos ou as eventuais diferenas a serem cobradas sero aquelas previstas pela legislao edilcia vigente  poca do protocolo do pedido inicial.</p>
		<p class="corpo_lei"><span class="bolinha">Art. 116.</span><strong class="artigo-lei"> </strong>O prazo de vigncia do Alvar de Aprovao a ser expedido em processo protocolado em data anterior  data da entrada em vigor deste Cdigo ser de 3 (trs) anos.</p>
		<p class="corpo_lei"><strong class="destaque-lei">Pargrafo nico. </strong>O disposto no caput deste artigo aplica-se aos Alvars de Aprovao e aos Alvars de Aprovao e Execuo j emitidos e vigentes na data da entrada em vigor deste Cdigo.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 110. </strong>Nos casos previstos no artigo 116 do COE, os pedidos de Alvar de Aprovao e Execuo expedidos conjuntamente ou em separado tero prazo de vigncia total de 5 (cinco) anos.<strong class="destaque-decreto"></strong></p>
		<p class="corpo_decreto"><strong class="destaque-decreto">Pargrafo nico. </strong>Quando o Alvar de Execuo for expedido aps o prazo de validade do Alvar de Aprovao, o prazo de vigncia do Alvar de Execuo ser contado a partir do seu deferimento.</p>

		<!-- 08_capitulo-VIII_d03.html -->
		<div id="g-08_capitulo-VIII_d03-box" class="ai2html">
			<div id="g-08_capitulo-VIII_d03-01" class="g-artboard g-artboard-v4" data-min-width='960'>
				<img id="g-08_capitulo-VIII_d03-01-img" class="g-aiImg" :src="caminho + '08_capitulo-VIII_d03-01.png'"/>
				<div id="g-ai0-1" class="g-Camada_1 g-aiAbs g-aiPointText" style="top:7.4742%;margin-top:-11.9px;left:0%;width:439px;">
					<p class="g-pstyle0">VALIDADE DE ALVARS ANTERIORES AO NOVO COE</p>
				</div>
				<div id="g-ai0-2" class="g-Camada_1 g-aiAbs g-aiPointText" style="top:6.7072%;margin-top:-9.8px;right:-0.0221%;width:92px;">
					<p class="g-pstyle1">Lei Art. 116</p>
				</div>
				<div id="g-ai0-3" class="g-Camada_1 g-aiAbs" style="top:17.8571%;left:0%;width:74.7917%;">
					<p>Validade de Alvars para processos em andamento e para Alvars de Aprovao ou Alvars de Aprovao e Execuo vlidos na data de entrada em vigor do novo COE.</p>
				</div>
				<div id="g-ai0-4" class="g-Camada_1 g-aiAbs g-aiPointText" style="top:85.9372%;margin-top:-10.6px;left:20.6389%;width:199px;">
					<p class="g-pstyle2">A<span class="g-cstyle0">L</span><span class="g-cstyle1">V</span>A<span class="g-cstyle2">R</span><span class="g-cstyle3"> </span>D<span class="g-cstyle3">E </span>A<span class="g-cstyle3">P</span><span class="g-cstyle4">R</span><span class="g-cstyle5">O</span><span class="g-cstyle1">V</span><span class="g-cstyle6">A</span><span class="g-cstyle6"></span><span class="g-cstyle5">O</span></p>
				</div>
				<div id="g-ai0-5" class="g-Camada_1 g-aiAbs g-aiPointText" style="top:85.9372%;margin-top:-10.6px;left:55.7957%;width:297px;">
					<p class="g-pstyle3"><span class="g-cstyle7">A</span><span class="g-cstyle8">L</span><span class="g-cstyle1">V</span><span class="g-cstyle7">A</span><span class="g-cstyle2">R</span><span class="g-cstyle7"> D</span>E<span class="g-cstyle7"> </span>A<span class="g-cstyle7">P</span><span class="g-cstyle4">R</span><span class="g-cstyle5">O</span><span class="g-cstyle1">V</span><span class="g-cstyle9">A</span><span class="g-cstyle6"></span>O<span class="g-cstyle7"> </span>E<span class="g-cstyle7"> </span>EXE<span class="g-cstyle7">C</span>U<span class="g-cstyle6"></span><span class="g-cstyle5">O</span></p>
				</div>
			</div>
		</div>

		<p class="corpo_lei"><strong class="artigo-lei">Art. 117. </strong>O Executivo,  vista da evoluo tecnolgica e dos costumes, promover a constante atualizao das prescries deste Cdigo, fixando, para tanto, os seguintes objetivos:</p>
		<p class="corpo_lei"><strong class="destaque-lei">I - </strong>promoo das adequaes e remanejamentos administrativos necessrios ao processo de modernizao e atualizao deste Cdigo, inclusive no que se refere  estrutura operacional da fiscalizao;</p>
		<p class="corpo_lei"><strong class="destaque-lei">II - </strong>estabelecimento de novos procedimentos que permitam a reunio do maior nmero de experincias e informaes de entidades e rgos tcnicos internos e externos  Prefeitura;</p>
		<p class="corpo_lei"><strong class="destaque-lei">III - </strong>estabelecimento de rotinas e sistemticas de consulta a entidades representativas da comunidade.</p>
		<p class="corpo_lei"><strong class="destaque-lei">Pargrafo nico. </strong>A Secretaria Municipal de Licenciamento  o rgo responsvel pela coordenao do procedimento de atualizao de que trata o caput deste artigo.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 111. </strong>Para a implementao das disposies do COE e deste decreto podero ser fixadas orientaes por portaria do Secretrio de SMUL e expedidas instrues normativas por Resoluo da CEUSO.<strong class="subtitulo_txt"></strong></p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 118. </strong>A no observncia das disposies deste Cdigo, de seu decreto regulamentar e das normas tcnicas aplicveis sujeita o proprietrio ou o possuidor e o profissional habilitado aos procedimentos fiscalizatrios e  aplicao das penalidades estabelecidas na Tabela de Multas constante do Anexo III desta lei, sem prejuzo das sanes administrativas e medidas judiciais cabveis.</p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 119. </strong>A Prefeitura pode firmar convnio com rgo de classe de arquitetos e engenheiros visando ao aprimoramento dos mecanismos de controle do exerccio profissional.</p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 120. </strong>A Prefeitura deve implantar sistema de gesto eletrnico visando  simplificao e transparncia do processo de licenciamento, para que o agente pblico e o muncipe possam acompanhar toda a tramitao dos pedidos.</p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 121. </strong>Os projetos para reas sob interveno urbanstica promovida pelo Poder Pblico, os equipamentos pblicos, os programas habitacionais de interesse social, bem como o Empreendimento Habitacional de Interesse Social em ZEIS  EZEIS, Empreendimento Habitacional de Interesse Social  EHIS, Empreendimento Habitacional do Mercado Popular  EHMP, Habitao de Interesse Social  HIS, Habitao do Mercado Popular  HMP e moradia econmica, definidos em legislao municipal, podem ser objeto de normas especiais diversas das adotadas por este Cdigo e apropriadas  finalidade do empreendimento, fixadas por ato do Executivo.</p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 122. </strong>O Executivo regulamentar esta lei no prazo de at 60 (sessenta) dias contados da data de sua publicao.</p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 123. </strong>Esta lei entrar em vigor no prazo de at 60 (sessenta) dias contados da data de sua publicao, junto com sua regulamentao, revogadas a Lei n 5.534, de 18 de julho de 1958, o art. 5 da Lei n 8.382, de 13 de abril de 1976, Lei n 9.843, de 4 de janeiro de 1985, Lei n 10.671, de 28 de outubro de 1988, Lei n 10.940, de 18 de janeiro de 1991, Lei n 11.228, de 25 de junho de 1992, Lei n 11.441, de 12 de novembro de 1993, Lei n 11.693, de 22 de dezembro de 1994, Lei n 11.859, de 31 de agosto de 1995, Lei n 11.948, de 8 de dezembro de 1995, Lei n 12.561, de 8 de janeiro de 1998, Lei n 12.597, de 16 de abril de 1998, Lei n 12.815, de 6 de abril de 1999, Lei n 12.821, de 7 de abril de 1999, Lei n 12.936, de 7 de dezembro de 1999, o art. 2 da Lei n 13.113, 16 de maro de 2001, Lei n 13.319, de 5 de fevereiro de 2002, Lei n 13.369, de 3 de junho de 2002, Lei n 13.779, de 11 de fevereiro de 2004, Lei n 14.459, de 3 de julho de 2007, Lei n 15.649, de 5 de dezembro de 2012, Lei n 15.831, de 24 de junho de 2013, e o art. 12 da Lei n 16.124, de 9 de maro de 2015.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 112. </strong>Este decreto entrar em vigor na data de sua publicao, revogados: <strong class="destaque-decreto"></strong></p>
		<p class="corpo_decreto"><strong class="destaque-decreto">I - </strong>o Decreto n 12.706, de 8 de maro de 1976; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">II - </strong>o Decreto n 17.866, de 11 de maro de 1982; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">III - </strong>o inciso III do artigo 3 e o artigo 8 do Decreto n 19.512, de 20 de maro de 1984; </p>
