		<p class="corpo_decreto"><strong class="destaque-decreto">XVII - </strong>o Decreto n 39.603, de 10 de junho de 2000; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">XVIII - </strong>o artigo 4 e o Anexo nico do Decreto n 41.788, de 13 de maro de 2002; </p>
