		<p class="corpo_decreto"><strong class="destaque-decreto">XXIII - </strong>o Decreto n 50.008, de 09 de setembro 2008; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">XXIV - </strong>o Decreto n 53.570, de 28 de novembro de 2012; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">XXV - </strong>o Decreto n 53.942, de 28 de maio de 2013; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">XXVI - </strong>o Decreto n 54.202, de 9 de agosto de 2013; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">XXVII - </strong>o artigo 8 do Decreto n 54.213, de 14 de agosto de 2013; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">XXVIII - </strong>o artigo 1 do Decreto n 55.036, de 15 de abril de 2014; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">XXIX - </strong>o Decreto n 55.131, de 20 de maio de 2014; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">XXX - </strong>o inciso I do artigo 18 do Decreto n 57.377, de 11 de outubro de 2016. </p>
		<p class="corpo_lei">PREFEITURA DO MUNICPIO DE SO PAULO, aos 9 de maio de 2017, 464 da fundao de So Paulo.</p>
		<p class="corpo_lei"><strong class="destaque-lei">JOO DORIA</strong>, PREFEITO</p>
