<template>
	<div class="Capitulo3">

		<h1 class="cap_lei">CAPTULO III  DAS TAXAS<br />(DAS ISENES DE TAXAS E DISPENSA DO PAGAMENTO DOS PREOS PBLICOS)</h1>

		<p class="corpo_lei"><strong class="artigo-lei">Art. 52. </strong>A Taxa para Exame e Verificao dos Pedidos de Documentos de Controle da Atividade Edilcia  TEV/COE, fundada no poder de polcia do Municpio, tem como fato gerador os pedidos de alvar, certificado, autorizao, cadastro e manuteno previstos neste Cdigo.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 1 </strong>Considera-se ocorrido o fato gerador no ato do protocolo dos pedidos de documentos e do cadastro de equipamentos.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 2 </strong>A fixao da alquota, base de clculo e ocorrncia do fato gerador, correspondentes a cada espcie de pedido, tem como base a Tabela constante do Anexo II deste Cdigo.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 3 </strong>A taxa deve ser integralmente recolhida no momento da ocorrncia do fato gerador, pelo proprietrio ou possuidor do imvel ou por quem efetivar o pedido.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 4 </strong>Na omisso total ou parcial do recolhimento de eventual diferena, cabe lanamento de ofcio, regularmente notificado o sujeito passivo, com prazo de 30 (trinta) dias para pagamento ou impugnao administrativa.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 5 </strong>O valor da taxa deve ser atualizado anualmente, em 1 de fevereiro, pela variao do ndice de Preos ao Consumidor Amplo  IPCA, apurado pelo Instituto Brasileiro de Geografia e Estatstica  IBGE, ou por outro ndice que venha a substitu-lo, verificada entre janeiro e dezembro do exerccio anterior.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 6 </strong>O dbito resultante do procedimento previsto no  4 deste artigo no pago at a data do vencimento deve ser atualizado da forma e pelo ndice de correo estabelecidos na Lei n 10.734, de 30 de junho de 1989, com a redao dada pela Lei n 13.275, de 4 de janeiro de 2002, e acrescido de juros moratrios calculados  taxa de 0,33% (trinta e trs centsimos por cento), por dia de atraso, sobre o valor do dbito, at o limite de 20% (vinte por cento), sem prejuzo, quando for o caso, do acrscimo de honorrios advocatcios, custas e demais despesas judiciais, conforme a legislao municipal pertinente.</p>
		<p class="corpo_lei"><span class="bolinha">Art. 53.</span><strong class="artigo-lei"> </strong>Ficam isentos do pagamento da TEV/COE e dispensados do pagamento dos preos pblicos os pedidos relativos a Empreendimento Habitacional de Interesse Social em ZEIS  EZEIS, Empreendimento Habitacional de Interesse Social  EHIS, Empreendimento Habitacional do Mercado Popular  EHMP, Habitao de Interesse Social  HIS, Habitao de Mercado Popular  HMP e moradia econmica, definidos em legislao municipal.</p>

		<!--03_capitulo-III_d01.html -->
		<div id="g-03_capitulo-III_d01-box" class="ai2html">
			<div id="g-03_capitulo-III_d01-01" class="g-artboard g-artboard-v4" data-min-width='960'>
				<img id="g-03_capitulo-III_d01-01-img" class="g-aiImg" :src="caminho + '03_capitulo-III_d01-01.png'"/>
				<div id="g-ai0-1" class="g-Camada_2 g-aiAbs g-aiPointText" style="top:2.4489%;margin-top:-11.8px;left:0%;width:331px;">
					<p class="g-pstyle0">ISENO DE PAGAMENTO DA TEV/COE</p>
				</div>
				<div id="g-ai0-2" class="g-Camada_2 g-aiAbs g-aiPointText" style="top:2.4524%;margin-top:-9.8px;left:93.3668%;width:85px;">
					<p class="g-pstyle1">Lei Art. 53</p>
				</div>
				<div id="g-ai0-3" class="g-Camada_2 g-aiAbs" style="top:5.3313%;left:0%;width:83.2292%;">
					<p class="g-pstyle2">Ficam isentos do pagamento da TEV/COE (Taxa para Exame e Verificao dos Pedidos de Documentos de Controle da Atividade Edilcia), e dispensados do pagamento dos preos pblicos os pedidos, dentre outros, relativos a:</p>
				</div>
				<div id="g-ai0-4" class="g-Camada_2 g-aiAbs" style="top:33.4753%;left:11.154%;margin-left:-76.5px;width:15.9375%;">
					<p class="g-pstyle3">E<span class="g-cstyle0">MP</span>REEND<span class="g-cstyle0">IM</span>EN<span class="g-cstyle1">T</span>O<span class="g-cstyle2"> </span>HA<span class="g-cstyle0">BI</span><span class="g-cstyle3">T</span><span class="g-cstyle4">A</span><span class="g-cstyle0">C</span>ION<span class="g-cstyle0">A</span><span class="g-cstyle5">L </span><span class="g-cstyle6">DO</span> <span class="g-cstyle6">ME</span><span class="g-cstyle7">R</span><span class="g-cstyle6">C</span>A<span class="g-cstyle6">DO</span> <span class="g-cstyle6">PO</span>P<span class="g-cstyle6">U</span><span class="g-cstyle8">L</span>A<span class="g-cstyle5">R</span></p>
				</div>
				<div id="g-ai0-5" class="g-Camada_2 g-aiAbs" style="top:33.4753%;left:46.2473%;margin-left:-76.5px;width:15.9375%;">
					<p class="g-pstyle4">EMPREENDIMENTO HABITACIONAL DE INTERESSE SOCIAL</p>
				</div>
				<div id="g-ai0-6" class="g-Camada_2 g-aiAbs" style="top:33.4753%;left:81.4434%;margin-left:-76.5px;width:15.9375%;">
					<p class="g-pstyle4">EMPREENDIMENTO HABITACIONAL DE INTERESSE SOCIAL EM ZEIS</p>
				</div>
				<div id="g-ai0-7" class="g-Camada_2 g-aiAbs" style="top:61.9913%;left:10.064%;margin-left:-76.5px;width:15.9375%;">
					<p class="g-pstyle4">HABITAO DE MERCADO POPULAR</p>
				</div>
				<div id="g-ai0-8" class="g-Camada_2 g-aiAbs" style="top:61.9913%;left:46.5268%;margin-left:-76.5px;width:15.9375%;">
					<p class="g-pstyle4">HABITAO DE INTERESSE SOCIAL</p>
				</div>
				<div id="g-ai0-9" class="g-Camada_2 g-aiAbs" style="top:90.0114%;left:11.1076%;margin-left:-76.5px;width:15.9375%;">
					<p class="g-pstyle4">ESTABELECIMENTO DE ENSINO MANTIDO POR INSTITUIO SEM FINS LUCRATIVOS</p>
				</div>
				<div id="g-ai0-10" class="g-Camada_2 g-aiAbs" style="top:90.0114%;left:46.9178%;margin-left:-76.5px;width:15.9375%;">
					<p class="g-pstyle4">HOSPITAL MANTIDO POR INSTITUIO SEM FINS LUCRATIVOS</p>
				</div>
				<div id="g-ai0-11" class="g-Camada_2 g-aiAbs" style="top:90.0114%;left:81.3099%;margin-left:-76.5px;width:15.9375%;">
					<p class="g-pstyle4">TEMPLO RELIGIOSO</p>
				</div>
			</div>
		</div>

		<p class="corpo_lei"><strong class="destaque-lei"> 1 </strong>Tambm so isentos os pedidos relativos a:<strong class="destaque-decreto"></strong></p>
		<p class="corpo_lei"><strong class="destaque-lei">I - </strong>estabelecimento de ensino mantido por instituio sem fins lucrativos;<strong class="destaque-decreto"></strong></p>
		<p class="corpo_lei"><strong class="destaque-lei">II - </strong>hospital mantido por instituio sem fins lucrativos;</p>
		<p class="corpo_lei"><strong class="destaque-lei">III - </strong>templo religioso.<strong class="destaque-decreto"></strong></p>
		<p class="corpo_lei"><strong class="destaque-lei"> 2 </strong>A iseno prevista no caput deste artigo estende-se aos demais programas habitacionais promovidos pelo setor pblico ou por entidades sob o controle acionrio do Poder Pblico, bem como aos programas promovidos por sociedades civis sem fins lucrativos conveniadas com a Secretaria Municipal de Habitao.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 3 </strong>A Prefeitura pode fornecer gratuitamente projetos de arquitetura e executivo para a construo de moradia econmica.<strong class="destaque-decreto"></strong></p>
		<p class="corpo_lei"><strong class="destaque-lei"> 4 </strong>Mediante convnio a ser firmado com o rgo de classe de engenheiros e arquitetos, a Prefeitura pode fornecer ainda, gratuitamente, assistncia e responsabilidade tcnica de profissional habilitado, para o acompanhamento das obras.</p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 54. </strong>Tambm ficam isentos do pagamento da TEV/COE e dispensados do pagamento dos preos pblicos os pedidos referentes a empreendimentos pblicos do Municpio, Estado e Unio e das entidades da Administrao Pblica Indireta.</p>
		<p class="corpo_lei"><strong class="destaque-lei">Pargrafo nico. </strong>O disposto no caput aplica-se aos pedidos referentes a edificao nova, reforma, requalificao e reconstruo de edificao existente, com ou sem mudana de uso, em imvel pblico reversvel de entidade da administrao direta e indireta.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 47. </strong>So isentos do pagamento da TEV/COE e dispensados do pagamento de preos pblicos, os pedidos de documentos previstos pelo COE e neste decreto para: </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">I -</strong> Empreendimento Habitacional de Interesse Social em ZEIS  EZEIS; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">II -</strong> Empreendimento Habitacional de Interesse Social  EHIS; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">III -</strong> Empreendimento Habitacional do Mercado Popular  EHMP; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">IV -</strong> Habitao de Interesse Social  HIS;</p>
		<p class="corpo_decreto"><strong class="destaque-decreto">V -</strong> Habitao do Mercado Popular  HMP; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">VI -</strong> moradia econmica;<strong class="destaque-decreto"></strong></p>
		<p class="corpo_decreto"><strong class="destaque-decreto">VII -</strong> templo religioso; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">VIII -</strong> estabelecimento de ensino, desde que mantido por instituio sem fins lucrativos;</p>
		<p class="corpo_decreto"><strong class="destaque-decreto">IX -</strong> hospital, desde que mantido por instituio sem fins lucrativos;</p>
		<p class="corpo_decreto"><strong class="destaque-decreto">X -</strong> entidade de assistncia social, desde que sem fins lucrativos, nos termos do artigo 7 da Lei n 7.083, de 7 de dezembro de 1967; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">XI -</strong> estdios destinados  competio e prtica de esportes, nos termos da Lei n 4.811, de 21 de outubro de 1955; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">XII -</strong> edificaes em imveis de propriedade de agremiaes desportivas, desde que destinadas s suas atividades sem venda de poules ou tales de aposta e quando o imvel estiver isento do pagamento de Imposto Predial e Territorial Urbano  IPTU, nos termos do pargrafo nico do artigo 1 da Lei n 4.634, de 5 de abril de 1955; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">XIII -</strong> Unio, Estado, Municpio, autarquias, empresas pblicas, fundaes pblicas e sociedades de economia mista. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 1 </strong>Nos pedidos previstos nos incisos I a XII do caput deste artigo, caber verificar se o projeto ou a edificao  compatvel com o uso ou atividade isenta e, em especial, nos casos dos seus incisos VIII ao XII, se consta, do estatuto social devidamente registrado, a finalidade exigida para a concesso dos benefcios. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 2 </strong>Os pedidos previstos no inciso XIII do caput deste artigo devero ser firmados pelo dirigente ou diretor do rgo da Administrao Pblica. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 3 </strong>O imvel de propriedade da Administrao Pblica Direta cedido a terceiros tambm est isento do pagamento de TEV/COE e dispensado do pagamento de preos pblicos, desde que o projeto ou a edificao seja compatvel com a finalidade constante do termo de cesso de uso ou contrato de concesso de servios pblicos e conste clusula de que o imvel ser revertido ao poder concedente ao trmino da cesso de uso ou da concesso. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 4 </strong>Os pedidos previstos no caput deste artigo observaro as seguintes normas: </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">I -</strong> no ato do protocolo de pedido de documento do COE para as edificaes previstas neste artigo, no ser emitida guia de recolhimento de TEV/COE e de preos pblicos; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">II -</strong> caso no se comprovem as condies legais da iseno e dispensa de pagamento, o interessado ser comunicado para recolher os valores, sob pena de indeferimento do pedido e inscrio do dbito na Dvida Ativa do Municpio; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">III -</strong> o recurso administrativo ser analisado mediante a comprovao da condio da iseno ou dispensa do pagamento da TEV/COE e de preos pblicos; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">IV -</strong> o primeiro despacho do pedido de documento do COE dever conter, ainda, a deciso a respeito da iseno e da dispensa de pagamento. </p>

	</div>
</template>

