<template>
	<div class="Capitulo6">
		
		<h1 class="cap_lei">CAPTULO VI  DOS PROCEDIMENTOS FISCALIZATRIOS RELATIVOS  ATIVIDADE EDILCIA</h1>
	
		<h2 class="secao_lei">Seo I - Verificao da Regularidade da Obra</h2>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 81. </strong>Toda obra, edificao, servio e equipamento pode, a qualquer tempo, ser vistoriado pela Prefeitura para a verificao do cumprimento das normas estabelecidas neste Cdigo.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 80. </strong>Compete s Prefeituras Regionais, por meio dos seus servidores municipais, regularmente investidos em cargo pblico e com poderes fiscalizatrios, a fiscalizao das disposies estabelecidas no COE e neste decreto, bem como a aplicao das penalidades previstas no Anexo III do COE, quando for o caso. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">Pargrafo nico. </strong>Ressalvado o disposto no inciso XI do artigo 5 da Constituio Federal, dever ser franqueado o imediato e irrestrito acesso ao local objeto de vistoria ao servidor municipal responsvel pela fiscalizao, desde que devidamente identificado por documento fornecido pela Administrao Municipal.<strong class="subtitulo_txt"></strong></p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 82. </strong>Deve ser mantido, no local da obra ou servio, o documento que comprova o licenciamento da atividade edilcia em execuo, sob pena de lavratura de autos de intimao e de multa, nos termos deste Cdigo e legislao pertinente  matria, ressalvada a situao prevista no art. 14 deste Cdigo.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 81. </strong>At o trmino dos trabalhos, dever ser mantido, no local da obra ou servio, o documento que comprove o licenciamento da atividade edilcia em execuo, sob pena de lavratura de autos de intimao e de multa, nos termos do artigo 83, inciso III, do COE, observado o disposto no artigo 98 do COE e ressalvada a situao prevista no artigo 14 do COE. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">Pargrafo nico. </strong>A guarda dos documentos fora do local da obra ou servio no exclui a aplicao das penalidades previstas neste artigo. </p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 82. </strong>So documentos hbeis  comprovao da regularidade da atividade edilcia em execuo: </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">I -</strong> Alvar de Execuo; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">II -</strong> Alvar de Autorizao. </p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 83. </strong>Constatada irregularidade na execuo da obra, devem ser adotados os seguintes procedimentos:</p>
		<p class="corpo_lei"><strong class="destaque-lei">I - </strong>para a obra sem licena expedida pela Prefeitura, ao proprietrio ou possuidor, devem ser lavrados, concomitantemente:</p>
		<p class="corpo_lei"><strong class="destaque-lei">a)</strong> auto de multa por execuo da obra sem licena;</p>
		<p class="corpo_lei"><strong class="destaque-lei">b)</strong> auto de embargo;</p>
		<p class="corpo_lei"><strong class="destaque-lei">c)</strong> auto de intimao para adotar as providncias visando  soluo da irregularidade no prazo de 5 (cinco) dias;</p>
		<p class="corpo_lei"><strong class="destaque-lei">II - </strong>pelo desvirtuamento da licena, ao proprietrio ou possuidor e ao responsvel tcnico pela obra, devem ser lavrados:</p>
		<p class="corpo_lei"><strong class="destaque-lei">a)</strong> auto de intimao para adotar as providncias visando  soluo da irregularidade no prazo de 5 (cinco) dias e auto de multa por execuo da obra com desvirtuamento da licena;</p>
		<p class="corpo_lei"><strong class="destaque-lei">b)</strong> no caso do desatendimento da intimao, auto de embargo e correspondente auto de multa de embargo;</p>
		<p class="corpo_lei"><strong class="destaque-lei">III - </strong>pelo desatendimento de qualquer disposio deste Cdigo, devem ser lavrados:</p>
		<p class="corpo_lei"><strong class="destaque-lei">a)</strong> auto de intimao para adotar as providncias visando ao atendimento da disposio deste Cdigo no prazo de 5 (cinco) dias;</p>
		<p class="corpo_lei"><strong class="destaque-lei">b)</strong> auto de multa correspondente  infrao.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 1 </strong>Durante o embargo, fica permitida somente a execuo dos servios indispensveis  eliminao das infraes que o motivaram, observadas as exigncias da legislao pertinente  matria.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 2 </strong>O embargo cessa somente aps:</p>
		<p class="corpo_lei"><strong class="destaque-lei">I - </strong>a eliminao das infraes que o motivaram, em se tratando de obra com licena;</p>
		<p class="corpo_lei"><strong class="destaque-lei">II - </strong>a expedio de Alvar de Autorizao ou Alvar de Execuo, em se tratando de obra sem licena.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 83. </strong>Constatada irregularidade na execuo da obra, devero ser adotados os seguintes procedimentos: </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">I -</strong> para a obra sem licena expedida pela Prefeitura, ao proprietrio ou possuidor, devem ser imediatamente lavrados auto de multa por execuo da obra sem licena, auto de embargo e auto de intimao para adotar as providncias visando  soluo da irregularidade no prazo de 5 (cinco) dias;<strong class="subtitulo_txt"></strong></p>
		<p class="corpo_decreto"><strong class="destaque-decreto">II -</strong> pelo desvirtuamento da licena, ao proprietrio ou possuidor e ao responsvel tcnico pela obra, devem ser lavrados:</p>
		<p class="corpo_decreto"><strong class="destaque-decreto">a)</strong> imediatamente, auto de intimao para adotar as providncias visando  soluo da irregularidade no prazo de 5 (cinco) dias e auto de multa por execuo da obra com desvirtuamento da licena; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">b)</strong> no caso do desatendimento da intimao, auto de embargo e correspondente auto de multa de embargo; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">III -</strong> pelo desatendimento de qualquer disposio do COE e deste decreto, quando no houver outros procedimentos expressamente estabelecidos, devem ser lavrados, concomitantemente, auto de intimao para adotar as providncias visando ao atendimento da disposio do COE e deste decreto no prazo de 5 (cinco) dias e auto de multa correspondente  infrao.</p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 1 </strong>No atendidas s determinaes no prazo fixado na intimao, ser aplicada nova multa.</p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 2 </strong>No caso de obra, ser caracterizada a reincidncia quando a infrao disser respeito ao mesmo documento, nos termos do artigo 98 do COE, no que couber.</p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 3 </strong>Durante o embargo, fica permitida somente a execuo dos servios indispensveis  eliminao das infraes que o motivaram, observadas as exigncias da legislao pertinente  matria.</p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 4 </strong>O embargo cessar a pedido do interessado somente aps: </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">I -</strong> a eliminao das infraes que o motivaram, em se tratando de obra com licena; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">II -</strong> a expedio de Alvar de Autorizao ou Alvar de Execuo, em se tratando de obra sem licena. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 5 </strong>O pedido de desembargo da obra ser analisado pelo Coordenador de Planejamento e Desenvolvimento Urbano, da Prefeitura Regional correspondente ao local da infrao, em despacho fundamentado e publicado no DOC. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 6 </strong>O prosseguimento dos trabalhos no imvel enquanto no deferido o desembargo caracterizar a resistncia ao embargo, adotando-se as providncias estabelecidas no artigo 84 do COE, competindo ao Prefeito Regional a solicitao de auxlio policial e a requisio de instaurao de inqurito policial. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 7 </strong>A modificao fsica da obra desde a vistoria anterior caracterizar a resistncia ao embargo. </p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 84. </strong>A Prefeitura, nos 5 (cinco) dias subsequentes ao embargo, deve vistoriar a obra e, se constatada resistncia ao embargo, adotar os seguintes procedimentos:</p>
		<p class="corpo_lei"><strong class="destaque-lei">I - </strong>aplicar multas dirias, ao proprietrio ou ao possuidor e ao responsvel tcnico pela obra, at a sua paralisao ou at que a regularizao da situao seja comunicada ao setor competente e confirmada pela Prefeitura no prazo de 5 (cinco) dias contados da data do protocolo da comunicao;</p>
		<p class="corpo_lei"><strong class="destaque-lei">II - </strong>caso a aplicao das multas dirias se mostre insuficiente, solicitar auxlio policial bem como providenciar os meios necessrios ao imediato cumprimento do embargo, tais como a apreenso de materiais e o desmonte ou lacrao de equipamentos e edificaes transitrias, lavrando o respectivo auto;</p>
		<p class="corpo_lei"><strong class="destaque-lei">III - </strong>noticiar imediatamente,  autoridade policial, o desrespeito ao embargo, requerendo a instaurao de inqurito policial para a apurao da responsabilidade do infrator por crime de desobedincia.</p>
		<p class="corpo_lei"><strong class="destaque-lei">Pargrafo nico. </strong>Para os fins do disposto neste Cdigo, considera-se resistncia ao embargo o prosseguimento dos trabalhos no imvel sem a eliminao das irregularidades exigidas no auto de intimao.</p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 85. </strong>Esgotadas todas as providncias administrativas para a paralisao da obra, o servidor municipal deve:</p>
		<p class="corpo_lei"><strong class="destaque-lei">I - </strong>extrair cpia das principais peas do processo administrativo para encaminhamento  Delegacia de Polcia, a fim de instruir o inqurito policial;</p>
		<p class="corpo_lei"><strong class="destaque-lei">II - </strong>expedir ofcios ao CREA ou CAU com as informaes do processo administrativo para a apurao da responsabilidade profissional;</p>
		<p class="corpo_lei"><strong class="destaque-lei">III - </strong>encaminhar o processo original ao setor jurdico para manifestao e posterior encaminhamento ao Departamento Judicial da Procuradoria Geral do Municpio para as providncias de ajuizamento da ao judicial cabvel, sem prejuzo da incidncia de multas dirias, em processo prprio, caso persistam as irregularidades.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 84. </strong>Esgotadas todas as providncias administrativas para a paralisao da obra, o Supervisor de Fiscalizao dever: </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">I -</strong> extrair cpia das principais peas do processo administrativo para encaminhamento  Delegacia de Polcia, a fim de instruir o inqurito policial; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">II -</strong> solicitar que o Gabinete da Prefeitura Regional expea ofcios ao CREA ou CAU com as informaes do processo administrativo para a apurao da responsabilidade profissional; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">III -</strong> encaminhar o processo original ao setor jurdico para manifestao e posterior encaminhamento ao Departamento de Defesa do Meio Ambiente e do Patrimnio, da Procuradoria Geral do Municpio, para as providncias de ajuizamento da ao judicial cabvel, sem prejuzo da incidncia de multas dirias, em processo prprio, caso persistam as irregularidades. </p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 86. </strong>Constatada situao de risco, em vistoria tcnica realizada por servidor com competncia especfica, alm das autuaes referidas nos arts. 82 a 84 deste Cdigo, deve ser imediatamente lavrado o auto de interdio, seguindo-se, no que couber, os procedimentos previstos na Seo II deste Captulo.</p>
		<p class="corpo_lei"><strong class="destaque-lei">Pargrafo nico. </strong>Na hiptese prevista no caput deste artigo, pode ocorrer o levantamento parcial do embargo para o fim especfico da execuo das medidas necessrias  eliminao do risco, ficando condicionado  apresentao de ART ou RRT relacionando os servios a serem executados e seu cronograma de execuo.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 85. </strong>Mesmo durante o embargo, acaso constatada situao de risco, em vistoria tcnica realizada por servidor com competncia especfica, alm das autuaes referidas nos artigos 82 a 84 do COE, deve ser imediatamente lavrado o auto de interdio, seguindo-se, no que couber, os procedimentos previstos neste captulo. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 1 </strong>Na hiptese prevista no caput deste artigo, poder ocorrer o levantamento parcial do embargo para o fim especfico da execuo das medidas necessrias  eliminao do risco, ficando condicionado  apresentao de ART ou RRT relacionando os servios a serem executados e seu cronograma de execuo. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 2 </strong>Eliminado o risco, retorna-se  situao de embargo, o qual somente cessar nas hipteses estabelecidas no  4 do artigo 83 deste decreto. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 3 </strong>Quaisquer trabalhos ou servios que extrapolem o previsto no  1 deste artigo caracterizaro resistncia ao embargo e  interdio, ensejando as penalidades previstas no COE. </p>
		<h2 class="secao_lei">Seo II - Da Verificao da Estabilidade, Segurana e Salubridade da Obra</h2>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 87. </strong>Verificada a inexistncia de condies de estabilidade, segurana ou salubridade de uma obra, edificao, servio ou equipamento, o proprietrio ou o possuidor e o responsvel tcnico pela obra devem ser intimados a dar incio s medidas necessrias  soluo da irregularidade no prazo de 5 (cinco) dias, devendo ainda ser lavrado o auto de interdio total ou parcial do imvel, dando-se cincia aos proprietrios e ocupantes.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 1 </strong>No caso de a irregularidade constatada apresentar perigo de runa ou contaminao, ocorrer, se necessrio, a interdio do entorno do imvel.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 2 </strong>O no cumprimento da intimao para a regularizao necessria ou interdio implica responsabilidade exclusiva do infrator, eximindo-se a Prefeitura da responsabilidade pelos danos decorrentes de eventual sinistro.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 3 </strong>Durante a interdio, fica permitida somente a execuo dos servios indispensveis  eliminao da irregularidade constatada.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 86. </strong>Verificada a inexistncia de condies de estabilidade, segurana ou salubridade de uma obra, edificao, servio ou equipamento, o proprietrio ou o possuidor e o responsvel tcnico pela obra devem ser intimados a dar incio s medidas necessrias  soluo da irregularidade no prazo de 5 (cinco) dias, devendo ainda ser lavrado o auto de interdio total ou parcial do imvel, dando-se cincia aos proprietrios e ocupantes.<strong class="subtitulo_txt"></strong></p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 1 </strong>Compete aos servidores tcnicos municipais, com formao especfica, lotados nas Prefeituras Regionais vistoriar e avaliar edificaes, obras servios ou equipamentos com respeito s suas condies de estabilidade, segurana e ou salubridade e determinar a adoo das medidas pertinentes.</p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 2 </strong>Compete aos servidores municipais, investidos em cargo pblico e com poderes fiscalizatrios, lotados nas Prefeituras Regionais, lavrar os autos de interdio e de intimao, colher as assinaturas, tanto do tcnico que avaliou a situao de risco e determinou a interdio, como do proprietrio ou possuidor a qualquer ttulo ou dos ocupantes do local a ser interditado, ou, ainda, atestar a impossibilidade ou recusa.<strong class="subtitulo_txt"></strong></p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 3 </strong>Do auto de interdio necessariamente devero constar as assinaturas do tcnico responsvel pela vistoria que ensejou a interdio e do agente pblico municipal com poderes de fiscalizao. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 4 </strong>No caso de a irregularidade constatada apresentar perigo de runa ou contaminao, ocorrer, se necessrio, a interdio do entorno do imvel. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 5 </strong>O no cumprimento da intimao para a regularizao necessria ou interdio implica responsabilidade exclusiva do infrator, eximindo-se a Prefeitura da responsabilidade pelos danos decorrentes de eventual sinistro. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 6 </strong>Durante a interdio, fica permitida somente a execuo dos servios indispensveis  eliminao da irregularidade constatada. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 7 </strong>Em se tratando de edificao erigida em imvel pblico municipal, cuja ocupao seja clandestina, alm da interdio total ou parcial, a intimao prevista neste artigo dever determinar a desocupao da rea pblica municipal, conforme legislao municipal pertinente. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 8 </strong>A pedido do interessado, o imvel poder ser desinterditado, sendo competncia do Coordenador de Planejamento e Desenvolvimento Urbano, da Prefeitura Regional do local da infrao, a sua anlise, em despacho fundamentado e publicado no DOC.</p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 88. </strong>Decorrido o prazo concedido, a Prefeitura deve adotar as seguintes medidas:</p>
		<p class="corpo_lei"><strong class="destaque-lei">I - </strong>pelo desatendimento da intimao, aplicar multas dirias ao infrator at que sejam adotadas as medidas exigidas;</p>
		<p class="corpo_lei"><strong class="destaque-lei">II - </strong>verificada a desobedincia  interdio:</p>
		<p class="corpo_lei"><strong class="destaque-lei">a)</strong> solicitar auxlio policial para o imediato cumprimento da interdio, lavrando o respectivo auto;</p>
		<p class="corpo_lei"><strong class="destaque-lei">b)</strong> noticiar imediatamente,  autoridade policial, o desrespeito  interdio, requerendo a instaurao de inqurito policial para a apurao da responsabilidade do infrator por crime de desobedincia;</p>
		<p class="corpo_lei"><strong class="destaque-lei">c)</strong> encaminhar o processo para as providncias de ajuizamento da ao judicial cabvel, sem prejuzo da incidncia de multas dirias caso persista o desatendimento da intimao prevista no inciso I deste artigo.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 87. </strong>Decorrido o prazo concedido, a Prefeitura dever adotar as seguintes medidas:<strong class="destaque-decreto"></strong></p>
		<p class="corpo_decreto"><strong class="destaque-decreto">I -</strong> pelo desatendimento da intimao, aplicar multas dirias ao infrator at que sejam adotadas as medidas exigidas; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">II -</strong> verificada a desobedincia  interdio:</p>
		<p class="corpo_decreto"><strong class="destaque-decreto">a)</strong> solicitar auxlio policial para o imediato cumprimento da interdio, lavrando o respectivo auto;</p>
		<p class="corpo_decreto"><strong class="destaque-decreto">b)</strong> noticiar imediatamente,  autoridade policial, o desrespeito  interdio, requerendo a instaurao de inqurito policial para a apurao da responsabilidade do infrator por crime de desobedincia;</p>
		<p class="corpo_decreto"><strong class="destaque-decreto">c)</strong> encaminhar o processo para as providncias de ajuizamento da ao judicial cabvel, sem prejuzo da incidncia de multas dirias caso persista o desatendimento da intimao prevista no caput do artigo 86 deste decreto. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">III -</strong> em se tratando de edificao erigida em imvel pblico municipal, cuja ocupao seja clandestina, instruir processo com o auto de intimao, auto de interdio e o relatrio de vistoria, encaminhando para as providncias previstas na legislao municipal pertinente.</p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 89. </strong>O atendimento da intimao no desobriga o proprietrio ou possuidor e o responsvel tcnico pela obra do cumprimento das formalidades necessrias  regularizao da obra, sob pena de aplicao das sanes cabveis.</p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 90. </strong>O proprietrio ou possuidor do imvel que constatar perigo de runa ou contaminao pode, devidamente assistido por profissional habilitado, dar incio imediato s obras de emergncia, comunicando o fato, por escrito,  Prefeitura e justificando e informando a natureza dos servios a serem executados, observadas as exigncias da legislao pertinente  matria.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 88. </strong>O proprietrio ou possuidor do imvel que constatar perigo de runa ou contaminao pode, devidamente assistido por profissional habilitado, dar incio imediato s obras de emergncia, comunicando o fato, por escrito,  Prefeitura Regional competente, justificando e informando a natureza dos servios a serem executados, observadas as exigncias da legislao pertinente  matria. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">Pargrafo nico. </strong>Quando estiver implementado o sistema informatizado, a comunicao digital suprir, para todos os fins, a prevista no caput deste artigo. </p>
		<h2 class="secao_lei">Seo III - Das Penalidades</h2>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 91. </strong>A inobservncia de qualquer disposio deste Cdigo constitui infrao sujeita  aplicao das penalidades previstas na Tabela de Multas constante do seu Anexo III.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 89. </strong>A inobservncia de qualquer disposio do COE constitui infrao sujeita  aplicao das penalidades previstas na Tabela de Multas, constante do seu Anexo III e do Anexo III deste decreto, e ensejar os procedimentos fiscais previstos neste captulo.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 90. </strong>Os autos previstos no COE e neste decreto, quando a infrao exigir lavraturas concomitantes, podero ser lavrados em um nico documento, quando emitidos por meio eletrnico.<strong class="subtitulo_txt"></strong></p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 92. </strong>As penalidades previstas por desrespeito s normas deste Cdigo aplicam-se tambm em relao a imveis de valor cultural, histrico, artstico, paisagstico ou ambiental preservados ou a serem preservados e, ainda, a imveis que, em razo do seu gabarito de altura e recuos, sejam necessrios  preservao da volumetria do entorno, sem prejuzo da incidncia das penalidades previstas em legislao prpria.</p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 93. </strong>A edificao concluda sem a obteno de Certificado de Concluso enseja a intimao do infrator para, no prazo de 5 (cinco) dias, solicitar o documento  Prefeitura, sob pena de lavratura do correspondente auto de multa.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 1 </strong>O pedido de Certificado de Concluso suspende a ao fiscalizatria at a emisso desse documento ou o indeferimento do pedido, o que ocorrer primeiro.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 2 </strong>A multa ser reaplicada a cada 90 (noventa) dias at a regularizao da edificao, limitado esse perodo a 1 (um) ano.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 91. </strong>A edificao concluda sem a obteno de Certificado de Concluso enseja a intimao do infrator para, no prazo de 5 (cinco) dias, solicitar o documento  Prefeitura, sob pena de lavratura do correspondente auto de multa.<strong class="destaque-decreto"></strong></p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 1 </strong>No atendida a intimao ou indeferido o pedido, ser aplicada a multa correspondente. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 2 </strong>A multa ser reaplicada a cada 90 (noventa) dias at a regularizao da edificao, limitado esse perodo a 1 (um) ano. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 3 </strong>A multa a que se refere o caput deste artigo independe do uso da edificao.  </p>
		<p class="corpo_decreto">4 O pedido de Certificado de Concluso suspende a ao fiscalizatria at a emisso desse documento ou o indeferimento do pedido, o que ocorrer primeiro.</p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 94. </strong>A edificao concluda sem a obteno de Certificado de Acessibilidade e de Certificado de Segurana enseja a intimao do infrator para, no prazo de 5 (cinco) dias, solicitar o documento  Prefeitura, sob pena de lavratura do correspondente auto de multa.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 1 </strong>O pedido de Certificado de Acessibilidade ou de Certificado de Segurana suspende a ao fiscalizatria at a emisso desse documento ou o indeferimento do pedido, o que ocorrer primeiro.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 2 </strong>A multa ser reaplicada a cada 90 (noventa) dias at a regularizao da edificao, limitado esse perodo a 1 (um) ano.<strong class="destaque-decreto"></strong></p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 92. </strong>A edificao concluda sem a obteno de Certificado de Acessibilidade e de Certificado de Segurana enseja a intimao do infrator para, no prazo de 5 (cinco) dias, solicitar o documento  Prefeitura, sob pena de lavratura do correspondente auto de multa.<strong class="destaque-decreto"></strong></p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 1 </strong>No atendida a intimao ou indeferido o pedido, ser aplicada a multa correspondente.</p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 2 </strong>A multa ser reaplicada a cada 90 (noventa) dias at a regularizao da edificao, limitado esse perodo a 1 (um) ano. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 3 </strong>O pedido de Certificado de Acessibilidade ou de Certificado de Segurana suspende a ao fiscalizatria at a emisso desse documento ou o indeferimento do pedido, o que ocorrer primeiro. </p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 93. </strong>Para os imveis que no apresentarem ligao de esgoto, conforme disposto no item 3.E do Anexo I deste decreto, situados em vias providas de rede coletora, as Prefeituras Regionais devero, concomitantemente: </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">I -</strong> aplicar a multa prevista na Tabela de Multas, contida no Anexo III do COE, correspondente ao valor fixado para as demais infraes das disposies do COE; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">II -</strong> intimar os proprietrios ou possuidores a qualquer ttulo das edificaes, para que no prazo de 30 (trinta) dias, executem as adaptaes necessrias em sua edificao, de modo a possibilitar a ligao do esgoto  rede coletora pblica pela concessionria, atendendo as exigncias previstas na legislao municipal pertinente, e para que requeira  concessionria de servios pblicos de coleta, tratamento e destinao final de esgoto a execuo da ligao de esgoto  rede coletora pblica, devendo apresentar, na Prefeitura Regional de sua jurisdio, o protocolo do respectivo pedido. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">Pargrafo nico. </strong>No atendida a intimao no prazo fixado, as Prefeituras Regionais devero reaplicar a multa a cada perodo de 30 (trinta) dias, at a apresentao do respectivo protocolo. </p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 94. </strong>Verificado o desrespeito ao estabelecido no subitem 3.4.2 do item 3 do Anexo I do COE, a Prefeitura Regional competente dever: </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">I -</strong> aplicar a multa prevista na Tabela de Multas, contida no Anexo III do COE, correspondente ao valor fixado para as demais infraes das disposies do COE; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">II -</strong> intimar os proprietrios ou possuidores a qualquer ttulo das edificaes, para que no prazo de 30 (trinta) dias, executem as adaptaes necessrias em sua edificao, ou de modo a possibilitar a canalizao sob o passeio  rede coletora de guas pluviais, de acordo com as normas emanadas do rgo competente. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">Pargrafo nico. </strong>No atendida a intimao no prazo fixado, as Prefeituras Regionais devero reaplicar a multa a cada perodo de 30 (trinta) dias, at que sejam tomadas as medidas necessrias para o saneamento da irregularidade.</p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 95. </strong>Para os efeitos deste Cdigo, considera-se infrator o proprietrio ou possuidor do imvel e, quando for o caso, o responsvel tcnico pela obra.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 1 </strong>O infrator deve ser notificado pessoalmente ou por via postal, com aviso de recebimento, ou, ainda, por edital nas hipteses de recusa do recebimento da notificao ou de sua no localizao.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 2 </strong>O infrator considera-se notificado quando encaminhada a notificao por via postal ao endereo constante do cadastro da Municipalidade.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 3 </strong>Quando prevista a aplicao de multa ao proprietrio ou possuidor e ao responsvel tcnico pela obra, a responsabilidade  solidria, considerando-se ambos os infratores.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 4 </strong>Os sucessores do proprietrio ou do possuidor do imvel tambm respondem pelas penalidades.</p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 96. </strong>Contra os atos de fiscalizao previstos neste Cdigo, cabe defesa ao Supervisor Tcnico de Fiscalizao, da Subprefeitura, no prazo de 15 (quinze) dias, contados:</p>
		<p class="corpo_lei"><strong class="destaque-lei">I - </strong>para a intimao e o embargo, a partir da data da respectiva notificao;</p>
		<p class="corpo_lei"><strong class="destaque-lei">II - </strong>para a multa, a partir da data de sua publicao no Dirio Oficial da Cidade.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 1 </strong>Contra o despacho decisrio que desacolher a defesa, caber um nico recurso, ao Subprefeito, no prazo de 15 (quinze) dias contados da data da publicao do despacho.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 2 </strong>As defesas e recursos suspendem a exigibilidade dos autos de multa impugnados.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 3 </strong>A defesa contra o auto de embargo no suspende a ao fiscalizatria e no obsta a aplicao de outras multas previstas neste Cdigo.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 95. </strong>Contra os atos de fiscalizao previstos no COE e neste decreto, cabe defesa ao Supervisor Tcnico de Fiscalizao, da Prefeitura Regional, no prazo de 15 (quinze) dias, contados: </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">I -</strong> para a intimao e o embargo, a partir da data da respectiva notificao; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">II -</strong> para a multa, a partir da data de sua publicao no DOC. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 1 </strong>Contra o despacho decisrio que desacolher a defesa, caber um nico recurso, ao Prefeito Regional, no prazo de 15 (quinze) dias contados da data da publicao do despacho. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 2 </strong>As defesas e recursos suspendem a exigibilidade dos autos de multa impugnados. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 3 </strong>A defesa contra o auto de embargo e/ou auto de intimao no suspende a ao fiscalizatria e no obsta a aplicao de outras multas previstas no COE e neste decreto. </p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 97. </strong>Ao proprietrio ou possuidor devem ser aplicadas multas nos valores indicados na Tabela de Multas e, ao responsvel tcnico pela obra, multas na proporo de 80% (oitenta por cento) dos referidos valores.</p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 98. </strong>A reincidncia da infrao, assim considerada a referente  mesma obra e ao mesmo documento, gera a aplicao das penalidades com acrscimo de 20% (vinte por cento) a cada reincidncia, at o limite de 2 (duas) vezes o valor da multa inicial.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 96. </strong>A reincidncia da infrao, assim considerada a referente  mesma obra e ao mesmo documento, gera a aplicao das penalidades com acrscimo de 20% (vinte por cento) a cada reincidncia, at atingir 2 (duas) vezes o valor da multa inicial, depois do que ser reaplicada sem novos acrscimos.<strong class="subtitulo_txt"></strong></p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 99. </strong>Para a aplicao dos dispositivos deste Captulo, os prazos devem ser dilatados at o triplo dos prazos previstos e reduzidos os valores das multas em 90% (noventa por cento) dos valores devidos para:</p>
		<p class="corpo_lei"><strong class="destaque-lei">I - </strong>as moradias econmicas;</p>
		<p class="corpo_lei"><strong class="destaque-lei">II - </strong>os templos religiosos.</p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 100. </strong>O valor da multa deve ser atualizado anualmente em 1 de fevereiro, pela variao do IPCA, apurado pelo IBGE, ou por outro ndice que venha a substitu-lo, verificada entre janeiro e dezembro do exerccio anterior.</p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 101. </strong>Quando no paga at a data do vencimento, o valor da multa deve ser atualizado da forma e pelo ndice de correo estabelecidos na Lei n 10.734, de 1989, com a redao dada pela Lei n 13.275, de 2002, e acrescido de juros moratrios calculados  taxa de 0,33% (trinta e trs centsimos por cento), por dia de atraso, sobre o valor do dbito, at o limite de 20% (vinte por cento), sem prejuzo, quando for o caso, do acrscimo de honorrios advocatcios, custas e demais despesas judiciais, conforme a legislao municipal pertinente.</p>

	</div>
</template>

