<template>
  	<div class="Capitulo2">

		<h1 class="cap_lei">CAPTULO II  DO CONTROLE DA ATIVIDADE EDILCIA</h1>
		<h2 class="secao_lei">Seo I - DAS RESPONSABILIDADES E DIREITOS</h2>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 4 </strong> direito e responsabilidade do proprietrio ou possuidor do imvel requerer perante a Prefeitura a emisso dos documentos de controle da atividade edilcia de que trata este Cdigo, respeitados o direito de vizinhana, a funo social da propriedade e a legislao municipal correlata.</p>
		<p class="corpo_lei"><strong class="destaque-lei">Pargrafo nico. </strong>O licenciamento de projetos e obras e instalao de equipamentos no implica o reconhecimento, pela Prefeitura, do direito de propriedade ou posse sobre o imvel.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 6 </strong>A Prefeitura emitir o documento de controle da atividade edilcia em nome do proprietrio ou do possuidor para o imvel descrito e caracterizado na matrcula ou, quando for o caso, na transcrio do Cartrio de Registro de Imveis, aps verificar que o pedido e o respectivo projeto atendem s disposies do PDE, LPUOS, LOE e legislao correlata.<strong class="destaque-decreto"></strong></p>
		<p class="corpo_decreto"><strong class="destaque-decreto">Pargrafo nico. </strong>Do documento emitido devero constar, no mnimo, as seguintes notas: </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">I - </strong>o licenciamento de projetos, de obras e instalao de equipamentos no implica no reconhecimento, pela Prefeitura, do direito de propriedade ou de posse sobre o imvel; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">II -</strong> o titular do documento de atividade edilcia, na qualidade de proprietrio ou de possuidor do imvel, responde perante terceiros a respeito da propriedade, posse, direitos reais, garantias e outros eventuais nus que incidam sobre o imvel; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">III -</strong> o titular do documento de atividade edilcia, na qualidade de proprietrio ou de possuidor do imvel, fica responsvel pela manuteno das condies de estabilidade, segurana e salubridade do imvel, edificaes e equipamentos, bem como pela observncia do projeto aprovado, das disposies do Cdigo de Obras e Edificaes  COE, do respectivo decreto regulamentar, das Normas Tcnicas  NTs aplicveis e da legislao municipal correlata, bem como do Plano Diretor Estratgico  PDE e da Legislao de Parcelamento, Uso e Ocupao do Solo  LPUOS;</p>
		<p class="corpo_decreto"><strong class="destaque-decreto">IV -</strong> a conformidade do projeto s normas tcnicas  NTs gerais e especficas de construo, bem como s Normas Tcnicas Oficiais  NTOs de acessibilidade e s disposies legais e regulamentares aplicveis aos aspectos interiores da edificao  responsabilidade exclusiva do responsvel tcnico pelo projeto; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">V -</strong> os responsveis tcnicos pela obra, nos limites de suas atuaes, respondem pela correta execuo da obra de acordo com o projeto aprovado e pela instalao e manuteno do equipamento, observadas as Normas Tcnicas  NTs aplicveis, zelando por sua segurana e assumindo as conseqncias diretas e indiretas advindas de sua atuao; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">VI -</strong> as peas grficas da edificao foram apresentadas na forma de projeto simplificado, conforme estabelecido nas normas municipais, sendo de total responsabilidade dos profissionais habilitados e do proprietrio ou possuidor do imvel: a) a definio e disposio interna dos compartimentos, suas dimenses e funes, especialmente no que se refere  acessibilidade e atendimento ao percentual mnimo de unidades acessveis e de unidades adaptveis, no que couber, conforme legislao federal, estadual ou municipal que regule a matria; b) a segurana no uso das edificaes, nos termos do Cdigo de Obras e Edificaes  COE e legislao correlata; c) a observncia das Normas Tcnicas NTs e das disposies tcnicas previstas no Anexo I do decreto regulamentar do COE. </p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 5 </strong>Para fins de aplicao das disposies deste Cdigo, considera-se:</p>
		<p class="corpo_lei"><span class="bolinha">I -</span><strong class="destaque-lei"> </strong>proprietrio: a pessoa fsica ou jurdica, detentora de ttulo de propriedade do imvel registrado no Cartrio de Registro de Imveis;</p>
		<p class="corpo_lei"><span class="bolinha">II -</span><strong class="destaque-lei"> </strong>possuidor: a pessoa fsica ou jurdica, bem como seu sucessor a qualquer ttulo, que tenha de fato o exerccio, pleno ou no, de usar o imvel objeto da obra.</p>
		<p class="corpo_lei"><strong class="destaque-lei">Pargrafo nico. </strong>No caso de rgo ou entidade da Administrao Pblica Direta e Indireta, a titularidade pode ser comprovada pela apresentao de mandado de imisso na posse, expedido em ao expropriatria do imvel, sendo admitido o licenciamento sobre parte da rea constante do ttulo de propriedade.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 7 </strong>O proprietrio poder requerer a emisso de documento de controle da atividade edilcia e comprovar a propriedade instruindo seu pedido com cpia da certido da matrcula ou, quando for o caso, da transcrio emitida pelo Cartrio de Registro de Imveis contendo o registro do seu ttulo de propriedade.<strong class="destaque-decreto"></strong></p>
		<p class="corpo_decreto"><strong class="destaque-decreto">Pargrafo nico. </strong>No caso de rgo ou entidade da Administrao Pblica Direta e Indireta, tais como autarquias, empresas pblicas, fundaes pblicas e sociedades de economia mista, a titularidade poder ser comprovada pela apresentao de mandado de imisso na posse, expedido em ao expropriatria do imvel, ou documento que, mesmo em rea maior, caracterize a propriedade sendo admitido o licenciamento sobre parte da rea constante do documento. </p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 6 </strong>O possuidor tem os mesmos direitos do proprietrio, desde que apresente a certido de registro imobilirio e um dos seguintes documentos:</p>
		<p class="corpo_lei"><strong class="destaque-lei">I - </strong>contrato com autorizao expressa do proprietrio;</p>
		<p class="corpo_lei"><strong class="destaque-lei">II - </strong>compromisso de compra e venda devidamente registrado no Cartrio de Registro de Imveis;<strong class="destaque-lei"></strong></p>
		<p class="corpo_lei"><strong class="destaque-lei">III - </strong>contrato representativo da relao jurdica existente entre o proprietrio e o possuidor direto;</p>
		<p class="corpo_lei"><strong class="destaque-lei">IV - </strong>escritura definitiva sem registro;<strong class="destaque-lei"></strong></p>
		<p class="corpo_lei"><strong class="destaque-lei">V - </strong>deciso judicial reconhecendo o direito de usucapio.</p>
		<p class="corpo_lei"><strong class="destaque-lei">Pargrafo nico. </strong>O proprietrio ou possuidor que autoriza a obra ou servio fica responsvel pela manuteno das condies de estabilidade, segurana e salubridade do imvel, edificaes e equipamentos, bem como pela observncia do projeto aprovado, das disposies deste Cdigo, do respectivo decreto regulamentar, das normas tcnicas aplicveis e da legislao municipal correlata, bem como do Plano Diretor Estratgico  PDE e da Legislao de Parcelamento, Uso e Ocupao do Solo  LPUOS.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 8 </strong>O possuidor tem os mesmos direitos do proprietrio e poder requerer a emisso do documento em seu nome, desde que apresente a certido da matrcula ou, quando for o caso, da transcrio emitida pelo Cartrio de Registro de Imveis, acompanhada de cpia de um dos seguintes documentos:<strong class="destaque-decreto"></strong></p>
		<p class="corpo_decreto"><strong class="destaque-decreto">I - </strong>contrato particular com autorizao expressa do proprietrio para obter o documento de controle da atividade edilcia para o imvel;<strong class="destaque-decreto"></strong></p>
		<p class="corpo_decreto"><strong class="destaque-decreto">II - </strong>compromisso ou promessa de compra e venda, registrado ou averbado no Cartrio de Registro de Imveis;<strong class="destaque-decreto"></strong></p>
		<p class="corpo_decreto"><strong class="destaque-decreto">III - </strong>contrato representativo da relao jurdica existente entre o proprietrio e o possuidor ou detentor do imvel; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">IV - </strong>escritura definitiva de transmisso da propriedade ainda no registrada perante o Cartrio de Registro de Imveis; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">V - </strong>deciso judicial reconhecendo o direito de usucapio ainda no transitada em julgado, desde que acompanhada do respectivo laudo pericial contendo a descrio e a caracterizao do imvel. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 1 </strong>O proprietrio do imvel poder suprir a ausncia dos documentos mencionados no caput deste artigo mediante autorizao expressa em favor do possuidor do imvel. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 2 </strong>No caso de pedido relativo  residncia unifamiliar, para o possuidor exercer os direitos previstos no COE, poder instruir os requerimentos com o compromisso ou promessa de compra e venda ou de cesso de quaisquer direitos ou recibo de pagamento de aquisio total ou parcial, independentemente de autenticao, reconhecimento de firma ou registro em cartrio. </p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 7 </strong>Todos os pedidos de documentos de controle da atividade edilcia devem ser subscritos pelo proprietrio ou possuidor em conjunto com um profissional habilitado.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 1 </strong>A veracidade das informaes e documentos apresentados nos pedidos e cadastro de que trata este Cdigo  de inteira responsabilidade do proprietrio ou possuidor e do profissional habilitado.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 2 </strong>O proprietrio, o possuidor e o profissional habilitado ficam obrigados  observncia das disposies deste Cdigo, das regras indispensveis ao seu cumprimento fixadas no respectivo decreto regulamentar e das normas tcnicas aplicveis, submetendo-se s penalidades previstas nesta lei.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 9 </strong>Somente o profissional habilitado no CREA ou CAU, o proprietrio ou o possuidor e seus representantes podero tratar, perante a Prefeitura, de assuntos relacionados com o projeto ou obra de sua responsabilidade.<strong class="destaque-decreto"></strong></p>
		<p class="corpo_decreto"><strong class="destaque-decreto">Pargrafo nico. </strong>Durante a anlise do pedido, fica assegurado, ao profissional habilitado, proprietrio ou possuidor e seus representantes o atendimento pessoal pelo tcnico municipal encarregado de sua anlise, ficando facultada a indicao de outro profissional habilitado para este fim. </p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 8 </strong>Considera-se profissional habilitado o tcnico registrado perante os rgos federais fiscalizadores do exerccio profissional, respeitadas as atribuies e limitaes consignadas por aqueles organismos.</p>
		<p class="corpo_lei"><span class="bolinha"> 1</span> O profissional habilitado pode assumir as funes de:</p>
		<p class="corpo_lei"><strong class="destaque-lei">I - </strong>responsvel tcnico pelo projeto, sendo responsvel pelo atendimento  legislao pertinente na elaborao do projeto, pelo contedo das peas grficas e pelas especificaes e exequibilidade de seu trabalho;</p>
		<p class="corpo_lei"><strong class="destaque-lei">II - </strong>responsvel tcnico pela obra, sendo responsvel pela correta execuo da obra de acordo com o projeto aprovado e pela instalao e manuteno do equipamento, observadas as normas tcnicas aplicveis, zelando por sua segurana e assumindo as consequncias diretas e indiretas advindas de sua atuao.</p>
		<p class="corpo_lei"><span class="bolinha"> 2</span><strong class="destaque-lei"> </strong>O profissional habilitado pode atuar individual ou solidariamente e como pessoa fsica ou responsvel por pessoa jurdica, facultado ao mesmo profissional a assuno das funes de responsvel tcnico pelo projeto, de responsvel tcnico pela obra, de responsvel pela instalao do equipamento e de responsvel pela manuteno do equipamento.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 3 </strong>Fica facultada a transferncia da responsabilidade profissional, sendo obrigatria em caso de impedimento do tcnico atuante, assumindo o novo profissional, perante a Prefeitura, a responsabilidade pela parte j executada, sem prejuzo da responsabilizao do profissional anterior.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 4 </strong>No caso de alterao do projeto com simultnea troca do seu responsvel tcnico, o profissional inicial dever ser comunicado do ocorrido.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 10. </strong>A baixa, a assuno e a transferncia de responsabilidade tcnica ocorrem na data da apresentao da devida comunicao  Prefeitura.<strong class="destaque-decreto"></strong></p>
		<p class="corpo_decreto"><strong class="destaque-decreto">Pargrafo nico. </strong>No caso de baixa de responsabilidade tcnica, em pedidos de Alvar de Execuo, o proprietrio ou possuidor dever ser comunicado, pela Prefeitura, para apresentar o novo responsvel tcnico pela obra no prazo de 30 (trinta) dias, sob pena de indeferimento. </p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 9 </strong>A observncia das disposies deste Cdigo no desobriga o profissional do cumprimento das normas disciplinadoras de sua regular atuao, impostas pelo respectivo conselho profissional, e daquelas decorrentes da legislao federal, estadual e municipal.</p>
		<p class="corpo_lei"><strong class="destaque-lei">Pargrafo nico. </strong>A Prefeitura se exime do reconhecimento dos direitos autorais ou pessoais referentes  autoria do projeto e  responsabilidade tcnica.</p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 10. </strong>A Prefeitura no se responsabiliza pela estabilidade da edificao e do equipamento ou por qualquer sinistro ou acidente decorrente de deficincias do projeto, de sua execuo ou instalao, bem como de sua utilizao.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 11. </strong>Os responsveis tcnicos, nos limites de sua atuao, respondem pela correta execuo da obra e instalao de equipamentos segundo as Normas Tcnicas  NTs vigentes, pela estabilidade da edificao e equipamento e por qualquer sinistro ou acidente decorrente de deficincias de projeto, de execuo e de instalao. <strong class="destaque-decreto"></strong></p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 11. </strong>A conformidade do projeto s normas tcnicas gerais e especficas de construo e s disposies legais e regulamentares aplicveis aos aspectos interiores das edificaes  de responsabilidade exclusiva do responsvel tcnico pelo projeto, de acordo com a declarao de responsabilidade a ser apresentada nos termos deste Cdigo.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 1 </strong>O projeto de edificao ou equipamento deve observar as disposies tcnicas estabelecidas no Anexo I deste Cdigo, independentemente da demonstrao nas peas grficas apresentadas, bem como estar em consonncia com a legislao estadual e federal aplicvel e as normas pertinentes.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 2 </strong>O projeto de segurana de uso deve observar as disposies estabelecidas nas normas pertinentes ao sistema construtivo e de estabilidade, condies de escoamento, condies construtivas especiais de segurana de uso, potencial de risco, instalaes eltricas e sistema de proteo contra descargas atmosfricas, equipamentos e sistemas de segurana contra incndio e aos sistemas complementares.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 3 </strong>Podem ser aceitas outras solues tcnicas, com igual ou superior desempenho em relao ao estabelecido neste Cdigo, desde que devidamente justificadas.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 4 </strong>O projeto deve observar as normas especficas e aquelas emitidas pelas concessionrias de servios pblicos, tais como de gua, esgoto, energia eltrica e gs.</p>
		<h2 class="secao_lei">Seo II - Dos Documentos de Controle da Atividade Edilcia</h2>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 12. </strong>A atividade edilcia depende de controle a ser exercido por meio da emisso de alvar, certificado, autorizao ou registro em cadastro de acordo com o tipo de obra, servio e equipamento a ser executado ou instalado, mediante procedimento administrativo e a pedido do interessado.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 1 </strong>Esto sujeitas a alvar de aprovao e execuo as seguintes atividades:</p>
		<p class="corpo_lei"><strong class="destaque-lei">I - </strong>construo de edificao nova em lote no edificado;</p>
		<p class="corpo_lei"><strong class="destaque-lei">II - </strong>reforma de edificao existente;</p>
		<p class="corpo_lei"><strong class="destaque-lei">III - </strong>requalificao de edificao existente;</p>
		<p class="corpo_lei"><strong class="destaque-lei">IV - </strong>demolio de bloco existente isolado, com ou sem a simultnea manuteno de outros blocos existentes no lote;</p>
		<p class="corpo_lei"><strong class="destaque-lei">V - </strong>reconstruo de edificao regular, no todo ou em parte;</p>
		<p class="corpo_lei"><strong class="destaque-lei">VI - </strong>execuo de muro de arrimo quando desvinculado de obra de edificao;</p>
		<p class="corpo_lei"><strong class="destaque-lei">VII - </strong>movimento de terra quando desvinculado de obra de edificao.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 2 </strong>Esto sujeitas a certificado as seguintes atividades:</p>
		<p class="corpo_lei"><strong class="destaque-lei">I - </strong>concluso de obra licenciada;</p>
		<p class="corpo_lei"><strong class="destaque-lei">II - </strong>regularizao de edificao existente;</p>
		<p class="corpo_lei"><strong class="destaque-lei">III - </strong>adaptao de edificao existente s condies de acessibilidade;</p>
		<p class="corpo_lei"><strong class="destaque-lei">IV - </strong>adaptao de edificao existente s condies de segurana de uso.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 3 </strong>Esto sujeitas a autorizao:</p>
		<p class="corpo_lei"><strong class="destaque-lei">I - </strong>implantao ou utilizao de edificao transitria ou equipamento transitrio;</p>
		<p class="corpo_lei"><strong class="destaque-lei">II - </strong>utilizao temporria de edificao licenciada para uso diverso;</p>
		<p class="corpo_lei"><strong class="destaque-lei">III - </strong>avano de tapume sobre parte do passeio pblico;</p>
		<p class="corpo_lei"><strong class="destaque-lei">IV - </strong>avano de grua sobre o espao pblico;</p>
		<p class="corpo_lei"><strong class="destaque-lei">V - </strong>instalao de canteiro de obras e estande de vendas em imvel distinto daquele em que a obra ser executada.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 4 </strong>Esto sujeitas a cadastro e manuteno os seguintes equipamentos:</p>
		<p class="corpo_lei"><strong class="destaque-lei">I - </strong>equipamento mecnico de transporte permanente, tais como elevador, escada rolante e plataforma de elevao;</p>
		<p class="corpo_lei"><strong class="destaque-lei">II - </strong>tanque de armazenagem, bomba, filtro de combustvel e equipamentos afins;</p>
		<p class="corpo_lei"><strong class="destaque-lei">III - </strong>equipamento de sistema especial de segurana da edificao, definido nos termos deste Cdigo.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 12. </strong>Para a instruo dos pedidos de documentos de controle de atividade edilcia, o proprietrio ou possuidor dever apresentar, alm dos documentos previstos no COE, requerimento-padro devidamente preenchido e assinado, contendo identificao dos respectivos dados de RG/CPF ou CNPJ e CREA/CAU do responsvel tcnico pelo projeto e do responsvel tcnico pela obra, quando for o caso, alm da indicao do nmero de contribuinte relativo ao terreno, constante do carn do IPTU, ou, no caso de rea rural, do CCIR, e indicao do objeto do pedido, alm de outros documentos <strong>fixados em portaria</strong><sup>4</sup>, nos termos do  2 do artigo 1 deste decreto.</p>

		<div id="02_capitulo-II_b01" class="glossario-box">
			<p class="titulo-referencias"><span class="vermelho2">Portaria</span></p>
			<p class="corpo_referencias1"><sup>4</sup> Ver documentao na Portaria, cap. 2 e 3.</p>
		</div>

		<p class="corpo_decreto"><strong class="destaque-decreto">Pargrafo nico. </strong>Os pedidos de expedio por via eletrnica seguiro os procedimentos definidos em sua regulamentao especifica.</p>
		<p class="corpo_lei"><span class="bolinha">Art. 13.</span><strong class="artigo-lei"> </strong>No esto sujeitas a licenciamento, nos termos deste Cdigo, a execuo de:</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 13. </strong>So de inteira responsabilidade do proprietrio ou possuidor, e quando for o caso, do profissional habilitado, as obras e servios relacionados no artigo 13 do COE. </p>
		<p class="corpo_lei"><strong class="destaque-lei">I - </strong>obra e servio de reparo e limpeza;</p>
		<p class="corpo_lei"><strong class="destaque-lei">II - </strong>restauro, entendido como a recuperao de imvel sob o regime de preservao municipal, estadual ou federal, de modo a lhe restituir as caractersticas originais, a ser autorizado pelo <strong>rgo competente</strong><sup class="referencias-notas">5</sup>;</p>
	
		<div id="02_capitulo-II_b02" class="glossario-box">
			<p class="corpo_referencias1"><sup>5</sup> <span class="glossario-museo">Conselho Municipal de Preservao do Patrimnio Histrico  CONPRESP</span> tem como atribuio deliberar sobre os tombamentos de bens mveis e imveis, definir rea envoltria 	destes bens, etc.</p>
			<p class="corpo_referencias1"><span class="glossario-museo">Conselho de Defesa do Patrimnio Histrico Arqueolgico, Artstico e Turstico - CONDEPHAAT</span> tem funo de proteger, valorizar e divulgar o patrimnio cultural no Estado de So Paulo. Nessa categoria se encaixam bens mveis, imveis, edificaes, monumentos, bairros, 	ncleos histricos, reas naturais, bens imateriais, dentre outros.</p>
			<p class="corpo_referencias1"> <span class="glossario-museo"> IPHAN (Instituto do Patrimnio Histrico e Artstico Nacional)</span> responde pela preservao do Patrimnio Cultural Brasileiro, cabendo a este rgo a proteo e promoo dos bens culturais do Pas, assegurando sua permanncia e usufruto para as geraes presentes e futuras. Tambm  responsvel pela conservao, salvaguarda e monitoramento dos bens culturais brasileiros inscritos na Lista do Patrimnio Mundial e na Lista do 	Patrimnio Cultural Imaterial da Humanidade.</p>
		</div>

		<div id="g-02_capitulo-II_d01-box" class="ai2html">
			<div id="g-02_capitulo-II_d01-01" class="g-artboard g-artboard-v4" data-max-width='960'>
