				<div id="g-ai0-1" class="g-Camada_1 g-aiAbs g-aiPointText" style="top:1.7933%;margin-top:-11.9px;left:0%;width:232px;">
					<p class="g-pstyle0"><span class="g-cstyle0">A</span><span class="g-cstyle1">G</span>ENTES RES<span class="g-cstyle2">P</span>ONS<span class="g-cstyle3"></span>VEI<span class="g-cstyle4">S</span></p>
				</div>
				<div id="g-ai0-2" class="g-Camada_1 g-aiAbs g-aiPointText" style="top:1.78%;margin-top:-9.8px;left:73.1598%;width:268px;">
					<p class="g-pstyle1">Lei Art. 5&ordm; Inciso I e II, Art. 8&ordm; &sect; 1&ordm; e &sect; 2&ordm;</p>
				</div>
				<div id="g-ai0-3" class="g-Camada_1 g-aiAbs g-aiPointText" style="top:4.5696%;margin-top:-9.8px;left:0%;width:440px;">
					<p class="g-pstyle1"><span class="g-cstyle5">P</span>a<span class="g-cstyle6">r</span>a ns de a<span class="g-cstyle7">p</span>licao das disposies des<span class="g-cstyle8">t</span>e Cdig<span class="g-cstyle9">o</span>, conside<span class="g-cstyle6">r</span>a-se:</p>
				</div>
				<div id="g-ai0-4" class="g-Camada_1 g-aiAbs g-aiPointText" style="top:11.8719%;margin-top:-11.9px;left:8.921%;width:144px;">
					<p class="g-pstyle0"><span class="g-cstyle2">P</span><span class="g-cstyle10">R</span>OPR<span class="g-cstyle2">I</span><span class="g-cstyle11">E</span><span class="g-cstyle12">T</span><span class="g-cstyle2"></span>R<span class="g-cstyle2">I</span><span class="g-cstyle4">O</span></p>
				</div>
				<div id="g-ai0-5" class="g-Camada_1 g-aiAbs g-aiPointText" style="top:11.8719%;margin-top:-11.9px;left:59.0637%;width:353px;">
					<p class="g-pstyle2">RESPONSVEL TCNICO PELO PROJETO</p>
				</div>
				<div id="g-ai0-6" class="g-Camada_1 g-aiAbs" style="top:13.3182%;left:8.921%;width:32.8125%;">
					<p class="g-pstyle3">Pessoa fsica ou jurdica, proprietria do ttulo de propriedade do imvel registrado em Cartrio.</p>
				</div>
				<div id="g-ai0-7" class="g-Camada_1 g-aiAbs" style="top:13.3182%;left:59.0637%;width:34.5833%;">
					<p class="g-pstyle3">Elaborao do projeto, contedo das peas grficas, especificaes e exerquibilidade de seu trabalho.</p>
				</div>
				<div id="g-ai0-8" class="g-Camada_1 g-aiAbs g-aiPointText" style="top:23.1203%;margin-top:-11.9px;left:59.0637%;width:353px;">
					<p class="g-pstyle2">RESPONSVEL TCNICO PELO PROJETO</p>
				</div>
				<div id="g-ai0-9" class="g-Camada_1 g-aiAbs g-aiPointText" style="top:23.2506%;margin-top:-11.4px;left:8.921%;width:119px;">
					<p class="g-pstyle2">POSSUIDOR</p>
				</div>
				<div id="g-ai0-10" class="g-Camada_1 g-aiAbs" style="top:24.6566%;left:8.921%;width:32.8125%;">
					<p class="g-pstyle3">Pessoa fsica, jurdica, ou sucessor que tenha o exerccio de usar o imvel objeto.</p>
				</div>
				<div id="g-ai0-11" class="g-Camada_1 g-aiAbs" style="top:24.6566%;left:59.0637%;width:33.6458%;">
					<p class="g-pstyle3">Correta execuo da obra de acordo com o projeto aprovado e pela instalao e manuteno do equipamento</p>
				</div>
				<div id="g-ai0-12" class="g-Camada_1 g-aiAbs g-aiPointText" style="top:34.1888%;margin-top:-11.9px;left:0%;width:265px;">
					<p class="g-pstyle4">BAIXO IMPACTO URBANSTICO</p>
				</div>
				<div id="g-ai0-13" class="g-Camada_1 g-aiAbs g-aiPointText" style="top:34.1756%;margin-top:-9.8px;left:86.0044%;width:146px;">
					<p class="g-pstyle1">Lei Art. 13 &sect;1&ordm; e &sect;2&ordm;</p>
				</div>
				<div id="g-ai0-14" class="g-Camada_1 g-aiAbs g-aiPointText" style="top:36.9652%;margin-top:-9.8px;left:0%;width:564px;">
					<p class="g-pstyle5">Considera-se baixo impacto urbanstico a construo/demolio/instalao/substituio de:</p>
				</div>
				<div id="g-ai0-15" class="g-Camada_1 g-aiAbs" style="top:48.8633%;left:0%;width:15.9375%;">
					<p>Obras complementares  edificao com rea construda mxima de 30m&sup2;</p>
				</div>
				<div id="g-ai0-16" class="g-Camada_1 g-aiAbs" style="top:48.8633%;left:25.2259%;width:15.9375%;">
					<p>Salincia como jardineira, floreira, brise com at 0,4m de profundidade; beiral com at 1,5m de largura</p>
				</div>
				<div id="g-ai0-17" class="g-Camada_1 g-aiAbs" style="top:48.8633%;left:50.4687%;width:15.9375%;">
					<p>Marquise em balano que avance at 50% das faixas de recuo obrigatrio e com rea mxima de 30m&sup2;</p>
				</div>
				<div id="g-ai0-18" class="g-Camada_1 g-aiAbs" style="top:48.8633%;left:75.6771%;width:15.9375%;">
					<p>Muro no alinhamento e de divisa</p>
				</div>
				<div id="g-ai0-19" class="g-Camada_1 g-aiAbs" style="top:68.1207%;left:0%;width:15.9375%;">
					<p>Muro de arrimo com altura mxima de 2m</p>
				</div>
				<div id="g-ai0-20" class="g-Camada_1 g-aiAbs" style="top:68.1207%;left:25.2259%;width:16.0417%;">
					<p class="g-pstyle6">Espelho d&rsquo;gua, poo e fossa</p>
				</div>
				<div id="g-ai0-21" class="g-Camada_1 g-aiAbs" style="top:68.1207%;left:50.4342%;width:16.0417%;">
					<p class="g-pstyle6">Piscina em residncia unifamiliar/unidade em conjunto de habitaes agrupadas horizontalmente</p>
				</div>
				<div id="g-ai0-22" class="g-Camada_1 g-aiAbs" style="top:68.1207%;left:75.6657%;width:16.0417%;">
					<p class="g-pstyle6">Material de revestimento exterior de parede e piso ou cobertura ou telhado</p>
				</div>
				<div id="g-ai0-23" class="g-Camada_1 g-aiAbs g-aiPointText" style="top:83.952%;margin-top:-11.9px;left:50.4687%;width:443px;">
					<p class="g-pstyle0">N<span class="g-cstyle0"></span>O <span class="g-cstyle2">S</span>E <span class="g-cstyle1">C</span>ON<span class="g-cstyle2">S</span>I<span class="g-cstyle2">D</span>E<span class="g-cstyle13">R</span>A<span class="g-cstyle2"> BAI</span><span class="g-cstyle4">X</span>O <span class="g-cstyle2">IM</span><span class="g-cstyle14">P</span><span class="g-cstyle15">A</span><span class="g-cstyle2">C</span><span class="g-cstyle8">T</span>O<span class="g-cstyle2"> </span>UR<span class="g-cstyle2">BA</span>N<span class="g-cstyle2">S</span>T<span class="g-cstyle2">I</span><span class="g-cstyle1">C</span>O<span class="g-cstyle4">:</span></p>
				</div>
				<div id="g-ai0-24" class="g-Camada_1 g-aiAbs" style="top:86.7482%;left:67.1875%;width:31.0417%;">
					<p class="g-pstyle7">Obra que venha a causar modificao na estrutura da edificao e executada em imvel/rea envoltria de imvel de preservao cultural, histrica, artstica, paisagstica ou ambiental ou em vias de preservao, de interesse municipal, estadual ou federal.</p>
				</div>
				<div id="g-ai0-25" class="g-Camada_1 g-aiAbs" style="top:88.8179%;left:0%;width:15.9375%;">
					<p>Passagem coberta com largura mxima de 3m e sem vedao lateral</p>
				</div>
				<div id="g-ai0-26" class="g-Camada_1 g-aiAbs" style="top:88.8179%;left:25.3356%;width:15.9375%;">
					<p>Mobilirios definidos pelo Art. 3&ordm; Inciso XV da Lei (guarita, jirau, estufa, quiosque, churrasqueira, prgula, etc.)</p>
				</div>
				<div id="g-ai0-27" class="g-Camada_1 g-aiAbs g-aiPointText" style="top:99.8008%;margin-top:-5px;left:2.4525%;width:24px;">
					<p class="g-pstyle8"> </p>
				</div>
			</div>
		</div>

		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 14. </strong>O restauro da edificao, para a restituio de suas caractersticas originais, depende de autorizao do rgo de preservao das esferas municipal, estadual e federal. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">Pargrafo nico. </strong>Havendo necessidade de reforma ou adaptao alm das caractersticas originais da edificao, ser necessrio seu licenciamento, nos termos do COE e deste decreto. </p>
		<p class="corpo_lei"><strong class="destaque-lei">III - </strong>alterao do interior da edificao que no implique modificao na estrutura que interfira na estabilidade da construo;</p>
		<p class="corpo_lei"><strong class="destaque-lei">IV - </strong>modificao do interior da edificao que no implique na reduo das condies de acessibilidade e segurana existentes;</p>
		<p class="corpo_lei"><strong class="destaque-lei">V </strong><strong class="destaque-lei">- </strong>execuo de obra e servio de baixo impacto urbanstico de acordo com o disposto neste Cdigo.</p>
		<p class="corpo_lei"><span class="bolinha"> 1</span><strong class="destaque-lei"> </strong>Consideram-se de baixo impacto urbanstico, dentre outras, a:</p>
		<p class="corpo_lei"><strong class="destaque-lei">I - </strong>construo e demolio de obras complementares  edificao com rea construda de, no mximo, 30,00 m (trinta metros quadrados);</p>
		<p class="corpo_lei"><strong class="destaque-lei">II - </strong>instalao de salincia, com as seguintes caractersticas e dimenses em relao ao plano da fachada da edificao:</p>
		<p class="corpo_lei"><strong class="destaque-lei">a) </strong>elemento arquitetnico, ornato, jardineira, floreira, brise, aba horizontal e vertical, com at 0,40 m (quarenta centmetros) de profundidade;</p>
		<p class="corpo_lei"><strong class="destaque-lei">b) </strong>beiral da cobertura com at 1,50 m (um metro e meio) de largura;</p>
		<p class="corpo_lei"><strong class="destaque-lei">c)</strong> marquise em balano, no sobreposta, que avance no mximo at 50% (cinquenta por cento) das faixas de recuo obrigatrio e com rea mxima de 30,00 m (trinta metros quadrados);</p>
		<p class="corpo_lei"><strong class="destaque-lei">III - </strong>construo de muro no alinhamento e de divisa;</p>
		<p class="corpo_lei"><strong class="destaque-lei">IV - </strong>construo de muro de arrimo com altura mxima de 2,00 m (dois metros);</p>
		<p class="corpo_lei"><strong class="destaque-lei">V - </strong>construo de espelho dgua, poo e fossa;</p>
		<p class="corpo_lei"><strong class="destaque-lei">VI - </strong>construo de piscina em edificao residencial unifamiliar e unidade habitacional no conjunto de habitaes agrupadas horizontalmente;</p>
		<p class="corpo_lei"><strong class="destaque-lei">VII - </strong>substituio de material de revestimento exterior de parede e piso ou de cobertura ou telhado;</p>
		<p class="corpo_lei"><strong class="destaque-lei">VIII - </strong>passagem coberta com largura mxima de 3 m (trs metros) e sem vedao lateral.</p>
		<p class="corpo_decreto"><strong class="destaque-decreto">Art. 15.</strong><strong class="destaque-decreto"> </strong>Tambm  considerada de baixo impacto a instalao do mobilirio definido no COE dentro dos limites do inciso II do artigo 102 e Tabela 2  Mobilirio do Anexo IV deste decreto.</p>
		<p class="corpo_lei"><span class="bolinha"> 2</span><strong class="destaque-lei"> </strong>No se considera de baixo impacto urbanstico a obra que venha a causar modificao na estrutura da edificao e aquela executada em imvel:</p>
		<p class="corpo_lei"><strong class="destaque-lei">I - </strong>sob o regime de preservao cultural, histrica, artstica, paisagstica ou ambiental ou em vias de preservao, de interesse municipal, estadual ou federal;</p>
		<p class="corpo_lei"><strong class="destaque-lei">II - </strong>situado em rea envoltria de imvel referido no inciso I deste pargrafo.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 3 </strong>As obras de que trata o  2 deste artigo devem ser aprovadas por rgo de preservao municipal, estadual ou federal, conforme for o caso, e devem ser adaptadas s condies de segurana de uso e de acessibilidade estabelecidas neste Cdigo.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 4 </strong>Quando forem necessrias as obras de adaptao previstas no  3 deste artigo, deve ser solicitada a aprovao do projeto de reforma ou de reconstruo, conforme o caso.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 5 </strong>A obra e servio de baixo impacto urbanstico nos termos deste artigo no so considerados para o clculo da taxa de ocupao e no so descontados no clculo de reas permeveis do projeto.</p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 14. </strong>A atividade edilcia em imvel da Unio, do Estado e do Municpio e de suas respectivas autarquias universitrias independe da expedio dos documentos de que trata este Cdigo, ficando, no entanto, sujeita ao atendimento de suas disposies e da legislao pertinente  matria.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 16. </strong>A construo e reforma em imvel de propriedade da Unio, do Estado ou do Municpio, bem como de suas respectivas autarquias universitrias, independe do licenciamento edilcio do empreendimento e da expedio dos documentos de que trata o artigo 12 do COE. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 1 </strong>O disposto neste artigo no dispensa a necessidade de submisso e anuncia dos rgos competentes, nas seguintes hipteses: </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">I - </strong>empreendimento que dependa de contrapartida financeira mediante o pagamento de outorga onerosa do potencial construtivo adicional, transferncia de potencial construtivo ou vinculao de CEPACs em reas de OU e OUC; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">II -</strong> empreendimento gerador de impacto ambiental; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">III - </strong>empreendimento gerador de impacto de vizinhana; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">IV -</strong> empreendimento que envolva usos especiais ou incmodos. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 2 </strong>As obras executadas em imvel sob o regime de preservao cultural, histrica, artstica, paisagstica ou ambiental ou com abertura de processo de tombamento por rgo municipal, estadual ou federal devem ser aprovadas pelos respectivos rgos de preservao, conforme for o caso.</p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 3 </strong>O disposto neste artigo no dispensa a necessidade de elaborao do projeto e do acompanhamento da obra por profissional habilitado, cabendo aos mesmos a assuno das responsabilidades previstas no COE. </p>
		<h3 class="subsecao_lei">Subseo I - Do Alvar de Aprovao</h3>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 15.</strong> Mediante procedimento administrativo e a pedido do proprietrio ou do possuidor do imvel, a Prefeitura emite Alvar de Aprovao, que licencia o projeto para:</p>
		<p class="corpo_lei"><strong class="destaque-lei">I - </strong>construo de edificao nova;</p>
		<p class="corpo_lei"><strong class="destaque-lei">II - </strong>reforma de edificao existente;</p>
		<p class="corpo_lei"><strong class="destaque-lei">III - </strong>requalificao de edificao existente.</p>
		<p class="corpo_lei"><strong class="destaque-lei">Pargrafo nico. </strong>O Alvar de Aprovao deve incluir, quando necessrio  implantao do projeto, as informaes relativas  previso de:</p>
		<p class="corpo_lei"><strong class="destaque-lei">I - </strong>demolio parcial ou total do existente;</p>
		<p class="corpo_lei"><strong class="destaque-lei">II - </strong>execuo de muro de arrimo;</p>
		<p class="corpo_lei"><strong class="destaque-lei">III - </strong>execuo de movimento de terra;</p>
		<p class="corpo_lei"><strong class="destaque-lei">IV - </strong>instalao de equipamento mecnico de transporte permanente, tanque de armazenagem, bomba, filtro de combustvel e equipamentos afins e de equipamento de sistema especial de segurana da edificao, nos termos deste Cdigo;</p>
		<p class="corpo_lei"><strong class="destaque-lei">V - </strong>(VETADO)<strong class="artigo-lei"></strong></p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 16. </strong>O pedido de Alvar de Aprovao deve ser instrudo com:</p>
		<p class="corpo_lei"><strong class="destaque-lei">I - </strong>documentao referente ao imvel;</p>
		<p class="corpo_lei"><strong class="destaque-lei">II - </strong>peas grficas do projeto simplificado assinadas por profissional habilitado, conforme estabelecido neste Cdigo e decreto regulamentar;</p>
		<p class="corpo_lei"><strong class="destaque-lei">III - </strong>levantamento topogrfico elaborado por profissional habilitado, de acordo com os requisitos tcnicos a serem regulamentados;</p>
		<p class="corpo_lei"><strong class="destaque-lei">IV - </strong>declarao assinada pelo profissional habilitado, atestando a conformidade do projeto no que diz respeito aos aspectos interiores da edificao em relao s disposies deste Cdigo e legislao correlata;</p>
		<p class="corpo_lei"><strong class="destaque-lei">V - </strong>declarao assinada pelo profissional habilitado, atestando a conformidade das condies de instalao de equipamento mecnico de transporte permanente, tanque de armazenagem, filtro, bomba de combustvel e equipamentos afins e de sistema especial de segurana da edificao em relao s normas e legislao pertinente, quando for o caso.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 1 </strong>Se uma edificao for constituda por um conjunto de blocos cujos projetos forem elaborados por profissionais diferentes, respondem eles solidariamente pela implantao de todo o conjunto.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 2 </strong>Somente so aceitas divergncias de at 5% (cinco por cento) entre as dimenses e rea constantes do documento de propriedade apresentado e as apuradas no levantamento topogrfico.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 3 </strong>Quando dentro do limite estabelecido no  2 deste artigo, so observados os ndices fixados pelo PDE e LPUOS em relao s menores dimenses e rea apuradas.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 4 </strong>Havendo divergncia superior a 5% (cinco por cento) entre qualquer dimenso ou rea constante do documento de propriedade e a apurada no levantamento topogrfico, o Alvar de Aprovao pode ser emitido, ficando a emisso do Alvar de Execuo condicionada  apresentao da certido de matrcula do imvel com dimenses e rea retificadas.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 17. </strong>Nos pedidos de Alvar de Aprovao<sup>6</sup>, alm dos documentos previstos no artigo 16 do COE e no artigo 12 deste decreto, devero ainda conter:<strong class="destaque-decreto"></strong></p>

		<div id="02_capitulo-II_b03" class="glossario-box">
			<p class="titulo-referencias"><span class="vermelho2">Portaria</span></p>
			<p class="corpo_referencias1"><sup>6</sup> Ver documentao na Portaria, cap. 3, seo 3.A.</p>
		</div>


		<p class="corpo_decreto"><strong class="destaque-decreto">I - </strong>as peas grficas do projeto simplificado necessrias para o perfeito entendimento do projeto, contemplando todos os elementos pertinentes  caracterizao da obra, fixados em portaria;</p>
		<p class="corpo_decreto"><strong class="destaque-decreto">II - </strong>a planta de levantamento planialtimtrico elaborada por profissional habilitado, numerada na sequncia das demais folhas do projeto simplificado, em escala adequada, fixados em portaria;<strong class="destaque-decreto"></strong></p>
		<p class="corpo_decreto"><strong class="destaque-decreto">III -</strong> as declaraes assinadas pelos profissionais habilitados, fixadas em portaria.</p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 1 </strong>As peas grficas de projetos que se enquadrem como Polos Geradores de Trfego  PGT, Empreendimentos Geradores de Impacto de Vizinhana  EGIV e Empreendimentos Geradores de Impacto Ambiental  EGIA devero observar a representao grfica dos elementos mencionados no 1 do artigo 17 do COE. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 2 </strong>A critrio dos profissionais envolvidos, poder ser definida a responsabilidade individualizada pelo projeto de cada bloco e pela implantao de todo conjunto.<strong class="subtitulo_txt"></strong></p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 17. </strong>As peas grficas do projeto simplificado devem conter:</p>
		<p class="corpo_lei"><strong class="destaque-lei">I - </strong>implantao da edificao;</p>
		<p class="corpo_lei"><strong class="destaque-lei">II - </strong>planta baixa do permetro de todos os andares;</p>
		<p class="corpo_lei"><strong class="destaque-lei">III - </strong>corte esquemtico;</p>
		<p class="corpo_lei"><strong class="destaque-lei">IV - </strong>no caso de reforma com alterao de rea, a indicao das edificaes existentes e dos acrscimos ou decrscimos de rea;</p>
		<p class="corpo_lei"><strong class="destaque-lei">V - </strong>quadro de reas e demonstrativos do atendimento ao PDE e LPUOS;</p>
		<p class="corpo_lei"><strong class="destaque-lei">VI - </strong>informao sobre o manejo arbreo, quando for o caso;</p>
		<p class="corpo_lei"><strong class="destaque-lei">VII - </strong>demonstrao do atendimento s disposies deste Cdigo.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 1 </strong>No caso de projetos para usos no residencial especial ou incomodo  vizinhana residencial definido na LPUOS, a planta apresentada dever conter ainda:</p>
		<p class="corpo_lei"><strong class="destaque-lei">I - </strong>identificao das rotas de fuga;</p>
		<p class="corpo_lei"><strong class="destaque-lei">II - </strong>localizao das escadas de segurana;</p>
		<p class="corpo_lei"><strong class="destaque-lei">III - </strong>localizao da circulao comum horizontal;</p>
		<p class="corpo_lei"><strong class="destaque-lei">IV - </strong>clculo da lotao dos pavimentos e do escoamento da populao segundo as condies estabelecidas no item 6 do Anexo I desta lei.</p>
		<p class="corpo_lei"><span class="bolinha"> 2</span><strong class="destaque-lei"> </strong>Ato do Executivo deve regulamentar a forma de apresentao e representao do projeto simplificado, de acordo com o porte e complexidade dos empreendimentos.</p>
		<p class="corpo_lei"><span class="bolinha">Art. 18.</span><strong class="artigo-lei"> </strong>O Alvar de Aprovao perde a eficcia em 2 (dois) anos contados da data da publicao do despacho de deferimento do pedido, devendo, neste prazo, ser solicitado o respectivo Alvar de Execuo.</p>
		<p class="corpo_lei"><strong class="destaque-lei">Pargrafo nico. </strong>Quando se tratar de edificao constituda de mais de um bloco isolado, o prazo do Alvar de Aprovao fica dilatado por mais 1 (um) ano para cada bloco excedente, at o prazo mximo de 5 (cinco) anos.</p>
		<p class="corpo_lei"><span class="bolinha">Art. 19.</span><strong class="artigo-lei"> </strong>O Alvar de Aprovao pode ser revalidado desde que o projeto aprovado atenda  legislao em vigor por ocasio do deferimento do pedido de revalidao.</p>
		<p class="corpo_lei"><strong class="destaque-lei">Pargrafo nico. </strong>Se houver necessidade de anlise tcnica em funo da edio de legislao posterior, deve ser solicitado novo alvar.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 18. </strong>O Alvar de Aprovao prescrito pode ser revalidado desde que o projeto aprovado atenda a legislao em vigor por ocasio do deferimento do pedido de revalidao.<strong class="destaque-decreto"></strong></p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 1 </strong>Se houver necessidade de anlise tcnica em funo da edio de legislao posterior, dever ser solicitado novo alvar. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 2 </strong>Os casos que se enquadrarem no caput deste artigo, sob nenhuma hiptese sero objeto de chancela de novas peas grficas. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 3 </strong>O prazo de revalidao ser o mesmo daquele estabelecido no artigo 18 do COE para o Alvar de Aprovao. </p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 20. </strong>O Alvar de Aprovao pode, enquanto vigente, ser objeto de <strong>apostilamento</strong><sup>7</sup> para constar eventuais alteraes de dados.</p>

		<div id="02_capitulo-II_b04" class="glossario-box">
			<p class="corpo_referencias1"><sup>7 </sup>Conforme Art. 51 do Decreto 57.776,  o meio pelo qual o documento de controle da atividade edilcia  aditado para constar eventuais alteraes de dados, desde que no impliquem em modificaes do projeto. </p>
		</div>

		<p class="corpo_lei"><strong class="destaque-lei">Pargrafo nico. </strong>A alterao do projeto aprovado dar-se- por meio da emisso de novo Alvar de Aprovao.</p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 21. </strong>Pode ser emitido mais de um Alvar de Aprovao para o mesmo imvel.</p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 22. </strong>O Alvar de Aprovao pode ser expedido juntamente com o Alvar de Desmembramento, por meio do mesmo procedimento, de acordo com o regulamento.</p>
		<h3 class="subsecao_lei">Subseo II - Do Alvar de Execuo</h3>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 23. </strong>Mediante procedimento administrativo e a pedido do proprietrio ou possuidor do imvel, a Prefeitura emite Alvar de Execuo, que autoriza a execuo e  indispensvel para o incio das obras de:</p>
		<p class="corpo_lei"><strong class="destaque-lei">I - </strong>construo de edificao nova;</p>
		<p class="corpo_lei"><strong class="destaque-lei">II - </strong>reforma de edificao existente;</p>
		<p class="corpo_lei"><strong class="destaque-lei">III - </strong>requalificao de edificao existente;</p>
		<p class="corpo_lei"><strong class="destaque-lei">IV - </strong>reconstruo de edificao que sofreu sinistro;</p>
		<p class="corpo_lei"><strong class="destaque-lei">V - </strong>demolio total de edificao ou de bloco isolado quando desvinculado de obra de edificao;</p>
		<p class="corpo_lei"><strong class="destaque-lei">VI - </strong>execuo de muro de arrimo quando desvinculado de obra de edificao;</p>
		<p class="corpo_lei"><strong class="destaque-lei">VII - </strong>movimento de terra quando desvinculado de obra de edificao.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 1 </strong>Um nico Alvar de Execuo pode incluir, quando for o caso, o licenciamento de mais de um tipo de servio ou obra elencado no caput deste artigo.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 2 </strong>O Alvar de Execuo para edificao nova, reforma ou requalificao de edificao deve incluir, quando for o caso, a licena para:</p>
		<p class="corpo_lei"><strong class="destaque-lei">I - </strong>demolio parcial ou total da edificao existente;</p>
		<p class="corpo_lei"><strong class="destaque-lei">II - </strong>execuo de muro de arrimo;</p>
		<p class="corpo_lei"><strong class="destaque-lei">III - </strong>movimento de terra necessrio  execuo do projeto;</p>
		<p class="corpo_lei"><strong class="destaque-lei">IV - </strong>instalao de equipamento mecnico de transporte permanente, tanque de armazenagem, bomba, filtro de combustvel e equipamentos afins e de sistema especial de segurana da edificao, nos termos das disposies deste Cdigo;</p>
		<p class="corpo_lei"><strong class="destaque-lei">V -</strong> (VETADO)</p>
		<p class="corpo_lei"><strong class="destaque-lei">VI -</strong> (VETADO)</p>
		<p class="corpo_lei"><strong class="destaque-lei">VII -</strong> (VETADO)</p>
		<p class="corpo_lei"><strong class="destaque-lei">VIII -</strong> (VETADO)</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 3 </strong>No caso do Alvar de Execuo se referir a um conjunto de servios ou obras a serem executados sob a responsabilidade de diversos profissionais, dele deve constar a rea de atuao de cada um deles.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 4 </strong>No caso de pedido para demolio de bloco isolado, independente de construo de outras obras ou de reforma no mesmo terreno, o licenciamento se d de forma declaratria, pelo proprietrio, devendo ser assistido por profissional habilitado somente se a edificao apresentar 3 (trs) ou mais pavimentos.</p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 24. </strong>O pedido de Alvar de Execuo deve ser instrudo com documentos referentes ao terreno e ao projeto, assinado pelo profissional habilitado, de acordo com a natureza do pedido.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 1 </strong>O responsvel tcnico deve formalizar declarao de responsabilidade pela correta execuo da obra, de acordo com o projeto aprovado, observadas as normas tcnicas aplicveis.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 2 </strong>Quando o pedido abranger a instalao de equipamento mecnico de transporte permanente, tais como elevador, escada rolante e plataforma de elevao, ou de tanque de armazenagem, bomba, filtro de combustvel e equipamentos afins ou dispuser de sistema especial de segurana da edificao, deve ser formalizada declarao de responsabilidade assinada pelo profissional habilitado responsvel pela instalao, atestando que os servios atendero s normas e s disposies legais pertinentes.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 3 </strong>O Alvar de Execuo somente pode ser emitido aps a comprovao do atendimento a eventuais ressalvas constantes do Alvar de Aprovao e o pagamento integral da <strong>outorga onerosa</strong><sup>8</sup> previsto na legislao urbanstica, quando for o caso.</p>

		<div id="02_capitulo-II_b05" class="glossario-box">
		
			<p class="corpo_referencias1"><sup>8 </sup><span class="glossario-museo">Outorga Onerosa</span>  a concesso, pelo poder pblico de potencial construtivo adicional acima do resultando da aplicao do Coeficiente de Aproveitamento Bsico, at o limite estabelecido pelo Coeficiente de Aproveitamento Mximo, de alterao de uso e parmetros urbansticos, mediante pagamento de contrapartida financeira. </p>
			<h6 class="corpo-referencias-pequeno">Fonte: Quadro 01 da Lei n 16.050/14</h6>
			<p class="corpo_referencias2"><span class="glossario-museo">CEPAC - Certificado de Potencial Adicional de Construo</span>  uma forma de contrapartida financeira de outorga onerosa do potencial construtivo adicional, alterao de uso e parmetros urbansticos para uso especfico nas Operaes Urbanas Consorciadas. </p>
			<h6 class="corpo-referencias-pequeno">Fonte: Quadro 01 da Lei n 16.050/14</h6>
		</div>

		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 19. </strong>Os pedidos de Alvar de Execuo, alm dos documentos previstos no artigo 24 do COE e no artigo 12 deste decreto, devero ser instrudos com os documentos fixados em portaria.<strong class="destaque-decreto"></strong></p>
		<p class="corpo_decreto"><strong class="destaque-decreto">Pargrafo nico. </strong>A instalao de elevadores e aparelhos de transporte permanente ser licenciada conjuntamente com a emisso do Alvar de Execuo da edificao, devendo ser apresentada a declarao assinada pelo responsvel tcnico pela instalao, de que o projeto e a instalao atendero as NTOs em vigor.<strong class="subtitulo_txt"></strong></p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 25. </strong>Quando houver mais de um Alvar de Aprovao em vigor para o mesmo imvel, o Alvar de Execuo pode ser concedido apenas para um deles.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 20. </strong>Quando houver mais de um Alvar de Aprovao em vigor para o mesmo imvel, o Alvar de Execuo pode ser concedido apenas para um deles. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 1 </strong>A emisso do Alvar de Execuo implicar na desistncia tcita dos demais Alvars de Aprovao emitidos para o imvel. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 2 </strong>Ficam assegurados os direitos concedidos no Alvar de Aprovao por ocasio da emisso do correspondente Alvar de Execuo, ressalvado o disposto no artigo 50 deste decreto. </p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 26. </strong>Pode ser requerido Alvar de Execuo parcial para cada bloco no caso do Alvar de Aprovao compreender edificao constituda de mais de um bloco, observado o seu prazo de vigncia.</p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 27. </strong>Aps a emisso do Alvar de Execuo, somente so aceitas pequenas alteraes no projeto, no se admitindo mudana de uso, categoria de uso ou subcategoria de uso e alterao da rea de terreno.</p>
		<p class="corpo_lei"><strong class="destaque-lei">Pargrafo nico. </strong>Na hiptese prevista no caput deste artigo, o projeto modificativo a ser aprovado no pode conter, em relao ao projeto anteriormente aprovado:</p>
		<p class="corpo_lei"><strong class="destaque-lei">I - </strong>alterao superior a 5% (cinco por cento) nas reas computveis;</p>
		<p class="corpo_lei"><strong class="destaque-lei">II - </strong>alterao superior a 5% (cinco por cento) nas reas no computveis;</p>
		<p class="corpo_lei"><strong class="destaque-lei">III - </strong>alterao superior a 5% (cinco por cento) na taxa de ocupao.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 21. </strong>Aps a emisso do Alvar de Execuo, podero ser aceitas alteraes no projeto aprovado pelo Alvar de Aprovao, mediante a solicitao de Projeto Modificativo. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 1 </strong>Entende-se por Projeto Modificativo aquele cujas alteraes no projeto anteriormente aprovado no configurem mudana de uso, categoria de uso ou subcategoria de uso e variao da rea do terreno e que no contenham: </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">I -</strong> alterao que importe acrscimo superior a 5% (cinco por cento) nas reas computveis;<strong class="destaque-decreto"></strong></p>
		<p class="corpo_decreto"><strong class="destaque-decreto">II -</strong> alterao que importe acrscimo superior a 5% (cinco por cento) nas reas no computveis;<strong class="destaque-decreto"></strong></p>
		<p class="corpo_decreto"><strong class="destaque-decreto">III -</strong> alterao que importe acrscimo superior a 5% (cinco por cento) na taxa de ocupao. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 2 </strong>Ser admitida a alterao de atividades dentro de um mesmo grupo de atividade. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 3 </strong>Ser admitida a alterao da rea do terreno prevista no  1 deste artigo em razo de retificao de registro perante o Cartrio de Registro de Imveis. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 4 </strong>Podero ser aceitas alteraes superiores s estabelecidas nos incisos I, II e III do  1 deste artigo quando decorrentes de interveno do Poder Pblico, tais como a edio de lei de melhoramento que interfira na implantao do projeto aprovado ou o tombamento de imvel.</p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 5 </strong>A limitao prevista no artigo 27 do COE aplica-se aos pedidos protocolados a partir da data da vigncia deste decreto. </p>
		<p class="corpo_lei"><strong class="destaque-lei">Art. 28.</strong><strong class="artigo-lei"> </strong>Quando destinado  demolio total, execuo de muro de arrimo e movimento de terra desvinculados de obra de edificao, o Alvar de Execuo perde a eficcia se as obras no forem concludas dentro do prazo de 2 (dois) anos contados da data da publicao do despacho de deferimento do pedido.</p>
		<p class="corpo_lei"><span class="bolinha">Art. 29.</span><strong class="artigo-lei"> </strong>O Alvar de Execuo para edificao nova, reforma, requalificao e reconstruo de edificao perde a eficcia:</p>
		<p class="corpo_lei"><strong class="destaque-lei">I - </strong>caso a obra no tenha sido iniciada, em 2 (dois) anos a contar da data da publicao do despacho de deferimento do pedido;</p>
		<p class="corpo_lei"><strong class="destaque-lei">II - </strong>caso a obra tenha sido iniciada, se permanecer paralisada por perodo superior a 1 (um) ano.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 1 </strong>Considera-se incio de obra o trmino das fundaes da edificao ou de um dos blocos.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 2 </strong>O prazo de validade do alvar de execuo fica suspenso durante a tramitao de eventual projeto modificativo.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 3</strong> (VETADO)</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 22. </strong>O Alvar de Execuo para edificao nova, reforma, requalificao e reconstruo de edificao perder a validade:<strong class="destaque-decreto"></strong></p>
		<p class="corpo_decreto"><strong class="destaque-decreto">I -</strong> caso a obra no tenha sido iniciada, em 2 (dois) anos a contar da data da publicao do despacho de deferimento do pedido; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">II -</strong> caso a obra tenha sido iniciada, se permanecer paralisada por perodo superior a 1 (um) ano. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 1 </strong>Considera-se incio de obra, para fins da contagem do prazo de vigncia do Alvar de Execuo, o trmino das fundaes da edificao ou de um dos blocos, sendo que: </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">I -</strong> o interessado poder comunicar a Prefeitura o trmino das fundaes e o andamento da obra por meio de registro fotogrfico e documental, dentro do prazo de vigncia do Alvar de Execuo; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">II -</strong> a Prefeitura poder solicitar ao interessado documentao complementar para a comprovao da validade do Alvar de Execuo;</p>
		<p class="corpo_decreto"><strong class="destaque-decreto">III -</strong> caso no adotadas as providncias constantes dos incisos I e II deste pargrafo, o Alvar perder a validade. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 2 </strong>O prazo de vigncia dos Alvars de Aprovao e Execuo, expedidos em conjunto ou no, totalizar 4 (quatro) anos. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 3 </strong>Quando o Alvar de Execuo for expedido aps o prazo de validade do Alvar de Aprovao, o prazo de vigncia do Alvar de Execuo ser contado a partir do seu deferimento. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 4 </strong>Nos casos de reforma que no envolva novas fundaes, considera-se incio de obra o incio dos servios de demolio ou construo.</p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 5 </strong>Concluda a superestrutura da edificao, o Alvar de Execuo no mais prescrever. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 6 </strong>Concluda a superestrutura de um bloco, o Alvar de Execuo no mais prescrever para este bloco.</p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 30. </strong>A obra paralisada com o Alvar de Execuo caduco pode ser reiniciada aps o reexame do projeto e a revalidao simultnea dos Alvars de Aprovao e de Execuo, desde que o projeto aprovado atenda  legislao em vigor por ocasio do deferimento do pedido de revalidao.</p>
		<p class="corpo_lei"><strong class="destaque-lei">Pargrafo nico. </strong>Pode ser aceita a continuao de obra parcialmente executada e paralisada que no atenda  legislao em vigor, desde que a edificao venha a ser utilizada para uso permitido na zona pelo PDE e LPUOS e no seja agravada a eventual desconformidade em relao:</p>
		<p class="corpo_lei"><strong class="destaque-lei">I - </strong>aos ndices urbansticos e parmetros de instalao e incomodidade estabelecidos na LPUOS;</p>
		<p class="corpo_lei"><strong class="destaque-lei">II - </strong>s normas relativas s condies de higiene, salubridade, segurana de uso e acessibilidade estabelecidas na LOE.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 23. </strong>No caso de obra paralisada com Alvar de Execuo caduco e a superestrutura parcialmente concluda, se o projeto original no atender  legislao superveniente, poder ser solicitado Alvar de Reforma para a concluso da parte existente com fundamento na legislao anterior.<strong class="destaque-decreto"></strong></p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 1 </strong>Dever ser apresentado registro fotogrfico e documental para a comprovao da concluso da superestrutura. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 2 </strong>As reas acrescidas devem atender s condies de higiene, salubridade, segurana de uso e acessibilidade estabelecidas na LOE e os ndices e parmetros urbansticos da LPUOS.</p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 3 </strong>O Alvar de Reforma de que trata este artigo abranger a regularizao da edificao existente. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 4 </strong>O Certificado de Concluso do Alvar de Reforma incluir a regularizao de todo o imvel edificado. </p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 31. </strong>O Alvar de Execuo pode, enquanto vigente, ser objeto de apostilamento para constar eventuais alteraes de dados.</p>
		<p class="corpo_lei"><span class="bolinha">Art. 32.</span><strong class="artigo-lei"> </strong>O Alvar de Execuo pode ser expedido juntamente com o Alvar de Aprovao, por meio de um mesmo procedimento, sendo neste caso o prazo de validade equivalente  soma dos prazos de validade de cada Alvar.</p>

		<!-- 02_capitulo-II_d02.html -->
		<div id="g-02_capitulo-II_d02-box" class="ai2html">
			<div id="g-02_capitulo-II_d02-01" class="g-artboard g-artboard-v4" data-min-width='960'>
				<img id="g-02_capitulo-II_d02-01-img" class="g-aiImg" :src="caminho + '02_capitulo-II_d02-01.png'"/>
				<div id="g-ai0-1" class="g-Camada_2 g-aiAbs g-aiPointText" style="top:1.4311%;margin-top:-9.8px;right:-0.0238%;width:167px;">
					<p class="g-pstyle0">Lei Art. 18, 19, 29 e 32.</p>
				</div>
				<div id="g-ai0-2" class="g-Camada_2 g-aiAbs g-aiPointText" style="top:1.6995%;margin-top:-11.9px;left:0%;width:318px;">
					<p class="g-pstyle1">VALIDADE DE ALVARS - SITUAES</p>
				</div>
				<div id="g-ai0-3" class="g-Camada_2 g-aiAbs" style="top:4.1789%;left:0%;width:83.2292%;">
					<p class="g-pstyle2">Os Alvars de Aprovao e Execuo podem ser protocolados em um nico expediente ou separadamente, tendo sua validade exemplificada nas seguintes situaes:</p>
				</div>
				<div id="g-ai0-4" class="g-Camada_2 g-aiAbs" style="top:11.3428%;left:0%;width:58.0208%;">
					<p class="g-pstyle3">SITUAO 1: ALVAR DE APROVAO E EXECUO (PROTOCOLO NICO)</p>
				</div>
				<div id="g-ai0-5" class="g-Camada_2 g-aiAbs g-aiPointText" style="top:17.8236%;margin-top:-10px;left:32.3657%;width:30px;">
					<p class="g-pstyle4">4</p>
				</div>
				<div id="g-ai0-6" class="g-Camada_2 g-aiAbs g-aiPointText" style="top:18.6799%;margin-top:-7px;left:31.6507%;width:44px;">
					<p class="g-pstyle5">anos</p>
				</div>
				<div id="g-ai0-7" class="g-Camada_2 g-aiAbs" style="top:23.6238%;left:32.8356%;margin-left:-94.5px;width:19.6875%;">
					<p class="g-pstyle6">prazo nico de vigncia para realizao da obra</p>
				</div>
				<div id="g-ai0-8" class="g-Camada_2 g-aiAbs" style="top:26.3529%;left:58.8542%;margin-left:-76.5px;width:15.9375%;">
					<p class="g-pstyle7">Obra finalizada</p>
				</div>
				<div id="g-ai0-9" class="g-Camada_2 g-aiAbs" style="top:26.3529%;left:0%;width:15.9375%;">
					<p class="g-pstyle8">Alvar de Aprovao e Execuo deferidos pela Prefeitura</p>
				</div>
				<div id="g-ai0-10" class="g-Camada_2 g-aiAbs" style="top:38.6339%;left:0%;width:74.7917%;">
					<p class="g-pstyle3">SITUAO 2: ALVAR DE EXECUO EMITIDO NA VIGNCIA DO ALVAR DE APROVAO</p>
				</div>
				<div id="g-ai0-11" class="g-Camada_2 g-aiAbs g-aiPointText" style="top:45.2852%;margin-top:-10px;left:24.3712%;width:30px;">
					<p class="g-pstyle4">2</p>
				</div>
				<div id="g-ai0-12" class="g-Camada_2 g-aiAbs g-aiPointText" style="top:46.0562%;margin-top:-7px;left:23.6562%;width:44px;">
					<p class="g-pstyle5">anos</p>
				</div>
				<div id="g-ai0-13" class="g-Camada_2 g-aiAbs g-aiPointText" style="top:49.3788%;margin-top:-10px;left:41.1421%;width:30px;">
					<p class="g-pstyle4">2</p>
				</div>
