		<h3 class="subsecao_lei">Subseo III - Do Certificado de Concluso</h3>

		<p class="corpo_lei"><strong class="artigo-lei">Art. 33. </strong>Mediante procedimento administrativo e a pedido do proprietrio ou possuidor, a Prefeitura expede Certificado de Concluso quando da concluso de obra ou servio licenciado por meio de Alvar de Execuo para:</p>
		<p class="corpo_lei"><strong class="destaque-lei">I - </strong>construo de edificao nova;</p>
		<p class="corpo_lei"><strong class="destaque-lei">II - </strong>reforma de edificao existente;</p>
		<p class="corpo_lei"><strong class="destaque-lei">III - </strong>requalificao de edificao existente;</p>
		<p class="corpo_lei"><strong class="destaque-lei">IV - </strong>reconstruo de edificao que sofreu sinistro;</p>
		<p class="corpo_lei"><strong class="destaque-lei">V - </strong>demolio total de edificao ou de bloco isolado;</p>
		<p class="corpo_lei"><strong class="destaque-lei">VI - </strong>execuo de muro de arrimo;</p>
		<p class="corpo_lei"><strong class="destaque-lei">VII - </strong>movimento de terra.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 1 </strong>Pode ser concedido Certificado de Concluso em carter parcial se a parte concluda da edificao atender s exigncias previstas na LOE, PDE e LPUOS para o uso a que se destina.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 2 </strong>No caso de edificao irregular, no todo ou em parte, que no atenda ao disposto na LOE, PDE e LPUOS, o Certificado de Concluso para reforma, parcial ou total, s pode ser concedido aps a supresso da infrao.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 3 </strong>Para emisso do Certificado de Concluso so aceitas pequenas alteraes que no descaracterizem o projeto aprovado e que no impliquem em divergncia superior a 5% (cinco por cento) entre as medidas lineares e quadradas da edificao e de sua implantao constantes do projeto aprovado e aquelas observadas na obra executada.</p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 34. </strong>O Certificado de Concluso  o documento hbil para a comprovao da regularidade da edificao, sendo vlido quando acompanhado das peas grficas aprovadas referentes ao Alvar de Execuo, inclusive para fins de registro no Cartrio de Registro de Imveis.</p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 35. </strong>O pedido de Certificado de Concluso deve ser instrudo com:</p>
		<p class="corpo_lei"><strong class="destaque-lei">I - </strong>declarao do profissional responsvel pela obra, atestando a sua concluso e execuo de acordo com as normas tcnicas aplicveis e as disposies da legislao municipal, em especial deste Cdigo;</p>
		<p class="corpo_lei"><strong class="destaque-lei">II - </strong>documentos e licenas eventualmente ressalvadas no Alvar de Execuo.</p>
		<p class="corpo_lei"><strong class="destaque-lei">Pargrafo nico. </strong>O cadastro de elevador e demais equipamentos mecnicos de transporte permanente, de tanque de armazenagem, bomba, filtro de combustvel e equipamentos afins e de equipamento de sistema especial de segurana da edificao nos respectivos sistemas da Prefeitura  requisito para a emisso do Certificado de Concluso, quando for o caso.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 24. </strong>O Certificado de Concluso  o documento hbil para a comprovao da regularidade da edificao, sendo vlido quando acompanhado das peas grficas aprovadas referentes ao Alvar de Execuo, inclusive para fins de registro no Cartrio de Registro de Imveis.<strong class="destaque-decreto"></strong></p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 1 </strong>Os documentos necessrios  emisso do Certificado de Concluso so aqueles constantes do artigo 35 do COE, bem como os fixados em portaria<sup>9</sup>. </p>

		<div id="02_capitulo-II_b06" class="glossario-box">
			<p class="titulo-referencias"><span class="vermelho2">Portaria</span></p>
			<p class="corpo_referencias1"><sup>9 </sup>Ver documentao na Portaria, cap. 3, seo 3.D.</p>
		</div>

		<p class="corpo_decreto"><strong class="destaque-decreto"> 2 </strong>Nas situaes em que os Alvars de Aprovao e de Execuo tenham sido analisados e expedidos por meio eletrnico, o Certificado de Concluso ser vlido quando acompanhado das peas grficas aprovadas referentes ao Alvar de Aprovao, inclusive para fins de registro no Cartrio de Registro de Imveis. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 3 </strong>A emisso do Certificado de Concluso independe da pendncia do pagamento de quaisquer multas. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 4 </strong>No ser emitido o Certificado de Concluso de obra embargada ou interditada, enquanto perdurar a infrao relativa  obra objeto do pedido. </p>
		<h3 class="subsecao_lei">Subseo IV - Do Certificado de Regularizao</h3>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 36. </strong>Mediante procedimento administrativo e a pedido do proprietrio ou possuidor, a Prefeitura expede Certificado de Regularizao quando da concluso de obra ou servio executado sem prvia licena da Prefeitura, para o qual seja obrigatria a emisso de Alvar de Execuo, desde que observadas:</p>
		<p class="corpo_lei"><strong class="destaque-lei">I - </strong>as prescries da LOE e LPUOS vigentes durante o perodo da construo e a edificao esteja adaptada s condies de segurana e acessibilidade estabelecidas neste Cdigo;</p>
		<p class="corpo_lei"><strong class="destaque-lei">II - </strong>a legislao edilcia e urbanstica vigente na ocasio da emisso do Certificado de Regularizao.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 1 </strong>Para a aplicao do disposto no inciso I do caput deste artigo a edificao destinada a uso residencial unifamiliar e conjunto residencial horizontal cujas unidades tenham acesso direto para o logradouro pblico, tambm devem ser consideradas as leis de anistia e de regularizao especficas publicadas no perodo referido nesse dispositivo, assim como toda a legislao posterior que possibilite a regularizao da edificao.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 2 </strong>Pode ser aceita divergncia de, no mximo, 5% (cinco por cento) entre as medidas lineares e quadradas exigidas na LOE e LPUOS e aquelas observadas na obra executada.</p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 37. </strong>O Certificado de Regularizao  o documento hbil para a comprovao da regularidade da edificao que no tenha sido objeto de Alvar de Execuo e de Certificado de Concluso, sendo vlido quando acompanhado das peas grficas aprovadas referentes  edificao, obra ou servio executado, inclusive para fins de registro no Cartrio de Registro de Imveis, substituindo o Certificado de Concluso.</p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 38. </strong>O pedido de Certificado de Regularizao deve ser instrudo com:</p>
		<p class="corpo_lei"><strong class="destaque-lei">I - </strong>documentao referente ao imvel;</p>
		<p class="corpo_lei"><strong class="destaque-lei">II - </strong>peas grficas do projeto simplificado da edificao executada, assinadas por profissional habilitado, conforme estabelecido neste Cdigo e decreto regulamentar;</p>
		<p class="corpo_lei"><strong class="destaque-lei">III - </strong>levantamento topogrfico para a verificao das dimenses, rea e localizao do imvel, quando necessrio;</p>
		<p class="corpo_lei"><strong class="destaque-lei">IV - </strong>declarao assinada por profissional habilitado, atestando que a obra est concluda e em conformidade com as disposies do art. 36 deste Cdigo e legislao correlata;</p>
		<p class="corpo_lei"><strong class="destaque-lei">V - </strong>outros documentos e licenas exigidos na legislao municipal, conforme o caso.</p>
		<p class="corpo_lei"><strong class="destaque-lei">Pargrafo nico. </strong>O cadastro de elevador e demais equipamentos mecnicos de transporte permanente, de tanque de armazenagem, bomba, filtro de combustvel e equipamentos afins e de equipamento de sistema especial de segurana da edificao nos respectivos sistemas da Prefeitura  requisito para a emisso do Certificado de Regularizao, quando for o caso.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 25. </strong>O Certificado de Regularizao  documento hbil para a comprovao da regularidade da edificao ou servio executado sem prvia licena da Prefeitura, sendo vlido quando acompanhado das respectivas peas grficas, inclusive para fins de registro no Cartrio de Registro de Imveis. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 1 </strong>O pedido de Certificado de Regularizao dever ser instrudo com os documentos constantes do artigo 38 do COE, bem como com os documentos fixados em portaria<sup>10</sup>.</p>

		<div id="02_capitulo-II_b07" class="glossario-box">
		
			<p class="titulo-referencias"><span class="vermelho2">Portaria</span></p>
			<p class="corpo_referencias1"><sup>10 </sup>Ver documentao na Portaria, cap. 3, seo 3.E.</p>
		</div>

		<p class="corpo_decreto"><strong class="destaque-decreto"> 2 </strong>O Certificado de Regularizao ser emitido mediante a comprovao do atendimento de um das hipteses previstas nos incisos I ou II do artigo 36 do COE.<strong class="destaque-decreto"></strong></p>
		<h3 class="subsecao_lei">Subseo V - Do Certificado de Acessibilidade</h3>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 39. </strong>Mediante procedimento administrativo e a pedido do proprietrio ou possuidor, a Prefeitura expede Certificado de Acessibilidade quando da concluso da adaptao da edificao existente s condies de acessibilidade s pessoas com deficincia ou mobilidade reduzida, conforme as disposies deste Cdigo, normas regulamentares, normas tcnicas e legislao correlata.</p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 40. </strong>Devem ser adaptadas s condies de acessibilidade as edificaes existentes destinadas ao uso:</p>
		<p class="corpo_lei"><strong class="destaque-lei">I - </strong>pblico, entendida como aquela administrada por rgo ou entidade da Administrao Pblica Direta e Indireta ou por empresa prestadora de servio pblico e destinada ao pblico em geral;</p>
		<p class="corpo_lei"><strong class="destaque-lei">II - </strong>coletivo, entendida como aquela destinada  atividade no residencial;</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 26. </strong>As edificaes existentes que se enquadrarem nos incisos I e II do artigo 40 do COE devem requerer perante os rgos competentes, no mbito das suas competncias, o Certificado de Acessibilidade. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 1 </strong>Esto dispensadas de Certificado de Acessibilidade as edificaes que tenham: </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">I -</strong> Certificado de Concluso emitido com fundamento na Lei n 11.228, de 25 de junho de 1992, ou legislao posterior; ou </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">II -</strong> Certificado de Acessibilidade vlido. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 2 </strong>O Certificado de Acessibilidade no substitui qualquer outro documento expedido pela Prefeitura, destinado a comprovar a regularidade da edificao. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 3 </strong>O Certificado de Acessibilidade dever ser requerido em processo prprio, previamente ou simultaneamente aos pedidos de Certificado de Segurana ou Alvar de Funcionamento de Local de Reunio. </p>
		<p class="corpo_lei"><strong class="destaque-lei">III - </strong>privado, entendida como aquela destinada  habitao classificada como multifamiliar.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 1 </strong>Na edificao habitacional multifamiliar todas as reas comuns devem ser acessveis.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 2 </strong>O atendimento ao disposto no caput deste artigo pode ser dispensado quando a adaptao necessria  edificao acarretar nus desproporcional ou indevido ao seu proprietrio ou possuidor, desde que tecnicamente justificado, conforme definido em regulamento.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 27. </strong>A impraticabilidade das adaptaes dever ser atestada por profissional legalmente habilitado, com a respectiva ART ou RRT, sob as penas da lei, por meio de declarao especifica. </p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 41. </strong>O pedido de Certificado de Acessibilidade deve ser instrudo com:</p>
		<p class="corpo_lei"><strong class="destaque-lei">I - </strong>documentao referente ao imvel;</p>
		<p class="corpo_lei"><strong class="destaque-lei">II - </strong>peas grficas do projeto simplificado das obras e servios de adaptao propostos, assinadas por profissional habilitado, conforme estabelecido neste Cdigo e decreto regulamentar;</p>
		<p class="corpo_lei"><strong class="destaque-lei">III - </strong>declarao do profissional responsvel pela obra, atestando a sua concluso e execuo de acordo com as disposies da legislao municipal e, em especial, deste Cdigo, bem como das normas pertinentes  acessibilidade no interior da edificao na data do protocolo do processo.</p>
		<p class="corpo_lei"><strong class="destaque-lei">Pargrafo nico. </strong>O cadastro de elevador e demais equipamentos mecnicos de transporte permanente nos sistemas da Prefeitura  requisito para a emisso do Certificado de Acessibilidade, quando for o caso.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 28. </strong>O pedido de Certificado de Acessibilidade dever ser instrudo com os documentos fixados em portaria <sup>11</sup>, bem como com aqueles constantes do artigo 41 do COE.</p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 42. </strong>O Certificado de Acessibilidade pode ser requerido junto com o Certificado de Regularizao ou Certificado de Segurana relativo  edificao.</p>

		<div id="02_capitulo-II_b08" class="glossario-box">
		<p class="titulo-referencias"><span class="vermelho2">Portaria</span></p>
		<p class="corpo_referencias1"><sup>11 </sup>Ver documentao na Portaria, cap. 3, seo 3.F.</p>
		</div>

		<h3 class="subsecao_lei">Subseo VI - Do Certificado de Segurana</h3>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 43. </strong>Mediante procedimento administrativo e a pedido do proprietrio ou possuidor, a Prefeitura expede Certificado de Segurana, documento que comprova a adaptao da edificao existente s condies de segurana de uso, conforme o disposto neste Cdigo, as normas tcnicas aplicveis e a legislao correlata.</p>
		<p class="corpo_lei"><strong class="destaque-lei">Pargrafo nico. </strong>O Certificado de Segurana deve incluir o Certificado de Acessibilidade caso a edificao ainda no tenha este documento.</p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 44. </strong>O pedido de Certificado de Segurana deve ser instrudo com:</p>
		<p class="corpo_lei"><strong class="destaque-lei">I - </strong>documentao referente ao imvel;</p>
		<p class="corpo_lei"><strong class="destaque-lei">II - </strong>peas grficas do projeto simplificado das obras e servios de adaptao propostos, assinadas por profissional habilitado;</p>
		<p class="corpo_lei"><strong class="destaque-lei">III - </strong>declarao assinada por profissional habilitado, atestando a conformidade da edificao s disposies deste Cdigo e legislao correlata;</p>
		<p class="corpo_lei"><strong class="destaque-lei">IV - </strong>Certificado de Acessibilidade ou documentao exigida neste Cdigo para a sua emisso, quando for o caso;</p>
		<p class="corpo_lei"><strong class="destaque-lei">V - </strong>outras declaraes referentes s condies de uso dos equipamentos, exigidas em legislao municipal.</p>
		<p class="corpo_lei"><strong class="destaque-lei">Pargrafo nico. </strong>O cadastro de elevador e demais equipamentos mecnicos de transporte permanente, de tanque de armazenagem, bomba, filtro de combustvel e equipamentos afins e de equipamento de sistema especial de segurana da edificao nos sistemas da Prefeitura  requisito para a emisso do Certificado de Segurana, quando for o caso.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 29. </strong>O pedido de Certificado de Segurana poder ser solicitado pelo proprietrio ou possuidor da edificao existente que necessitar de espao de circulao protegido, conforme NTO, e no tiverem Auto de Verificao de Segurana  AVS.<strong class="destaque-decreto"></strong></p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 1 </strong>O pedido de Certificado de Segurana deve ser instrudo com os documentos fixados em portaria <sup>12</sup>, bem como com aqueles constantes do artigo 44 do COE.</p>

		<div id="02_capitulo-II_b09" class="glossario-box">
			<p class="titulo-referencias"><span class="vermelho2">Portaria</span></p>
			<p class="corpo_referencias1"><sup>12 </sup>Ver documentao na Portaria, cap. 3, seo 3.G.</p>
		</div>

		<p class="corpo_decreto"><strong class="destaque-decreto"> 2 </strong>Excluem-se da necessidade de Certificado de Segurana: </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">I -</strong> as edificaes residenciais; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">II -</strong> as edificaes aprovadas aps 20 de junho de 1975, data da edio da Lei n 8.266, de 20 de junho de 1975, que tenham Auto ou Certificado de Concluso e que no sofreram alteraes de ordem fsica ou de utilizao em relao ao regularmente licenciado. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 3 </strong>Nas edificaes no residenciais com rea construda total acima de 750 m (setecentos e cinquenta metros quadrados) e que no se enquadrem no caput deste artigo, o Certificado de Segurana poder ser substitudo pelo AVCB. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 4 </strong>O pedido de Certificado de Segurana inclui a emisso do Cadastro do Sistema Especial de Segurana, devendo a TEV/ COE do projeto ser recolhida somente para o assunto requerido. <strong class="subtitulo_txt"></strong></p>
		<h3 class="subsecao_lei">Subseo VII - Do Alvar de Autorizao</h3>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 45. </strong>Mediante procedimento administrativo e a pedido do proprietrio ou possuidor, a Prefeitura concede Alvar de Autorizao para:</p>
		<p class="corpo_lei"><strong class="destaque-lei">I - </strong>implantao ou utilizao de edificao transitria ou equipamento transitrio, includo estande de vendas no mesmo local de implantao da obra;</p>
		<p class="corpo_lei"><strong class="destaque-lei">II - </strong>utilizao temporria de edificao licenciada para uso diverso;</p>
		<p class="corpo_lei"><strong class="destaque-lei">III - </strong>avano de tapume sobre parte do passeio pblico;</p>
		<p class="corpo_lei"><strong class="destaque-lei">IV - </strong>avano de grua sobre o espao pblico;</p>
		<p class="corpo_lei"><strong class="destaque-lei">V - </strong>instalao de canteiro de obras e estande de vendas em imvel distinto daquele em que a obra ser executada.</p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 46. </strong>O pedido de Alvar de Autorizao deve ser instrudo com:</p>
		<p class="corpo_lei"><strong class="destaque-lei">I - </strong>documentao referente ao imvel;</p>
		<p class="corpo_lei"><strong class="destaque-lei">II - </strong>peas grficas do projeto simplificado da edificao assinadas por profissional habilitado, quando for o caso;</p>
		<p class="corpo_lei"><strong class="destaque-lei">III - </strong>declarao assinada por profissional habilitado, atestando a conformidade da edificao s disposies deste Cdigo e legislao correlata;</p>
		<p class="corpo_lei"><strong class="destaque-lei">IV - </strong>outros documentos e licenas exigidos na legislao municipal, conforme o caso.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 30. </strong>Em funo de sua natureza, o pedido de Alvar de Autorizao ser acompanhado dos documentos previstos no artigo 46 do COE e fixados em portaria<sup>13</sup>, seo 3.H.)</span>.</p>

		<div id="02_capitulo-II_b10" class="glossario-box">
			<p class="titulo-referencias"><span class="vermelho2">Portaria</span></p>
			<p class="corpo_referencias1"><sup>13 </sup>Ver documentao na Portaria, cap. 3, seo 3.H.</p>
		</div>

		<p class="corpo_lei"><strong class="destaque-lei"> 1 </strong>O cadastro de elevador e demais equipamentos mecnicos de transporte permanente, de tanque de armazenagem, bomba, filtro de combustvel e equipamentos afins e de equipamento de sistema especial de segurana da edificao nos respectivos sistemas da Prefeitura  requisito para a emisso do Alvar de Autorizao, quando for o caso.</p>
		<p class="corpo_lei"><strong class="destaque-lei"> 2 </strong>O Alvar de Autorizao de que trata o caput deste artigo perde a eficcia no prazo de 6 (seis) meses, podendo ser revalidado uma vez por igual perodo, a pedido do interessado.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 31. </strong>O Alvar de Autorizao ter validade de 6 (seis) meses, podendo ser revalidado 1 (uma) vez por igual perodo, a pedido do interessado, mediante recolhimento de novas taxas. </p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 32. </strong>So licenas de uso regidas por lei especifica: </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">I -</strong> a utilizao temporria de edificao licenciada para uso diverso do licenciado; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">II -</strong> o uso de edificao inacabada; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">III -</strong> a autorizao de uso para evento temporrio. </p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 33. </strong>O Alvar de Autorizao ser emitido a ttulo precrio, ficando as obras e a adequao s posturas municipais sob inteira responsabilidade do proprietrio ou possuidor e responsveis tcnicos. </p>
		<h3 class="subsecao_lei">Subseo VIII - Do Cadastro e Manuteno de Equipamentos</h3>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 47. </strong>Mediante procedimento administrativo, o proprietrio ou possuidor deve cadastrar nos sistemas da Prefeitura os seguintes equipamentos:</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 34. </strong>Mediante procedimento administrativo, o proprietrio, possuidor ou responsvel tcnico deve cadastrar, nos sistemas da Prefeitura, os seguintes equipamentos: </p>
		<p class="corpo_lei"><strong class="destaque-lei">I - </strong>equipamento mecnico de transporte permanente, tais como elevador, escada rolante e plataforma de elevao;</p>
		<p class="corpo_decreto"><strong class="destaque-decreto">I -</strong> equipamentos mecnicos de transporte permanente, tais como elevador, escada rolante, plataforma de elevao e sistema de estacionamento mecnico vertical; </p>
		<p class="corpo_lei"><strong class="destaque-lei">II - </strong>tanque de armazenagem, bomba, filtro de combustvel e equipamentos afins;</p>
		<p class="corpo_decreto"><strong class="destaque-decreto">II -</strong> tanques de armazenagem, bombas, filtros de combustvel e equipamentos afins quando referentes a:</p>
		<p class="corpo_decreto"><strong class="destaque-decreto">a)</strong> tanques estacionrios de armazenamento de produtos qumicos inflamveis, combustveis e/ou perigosos, nas fases l- quida ou gasosa, com volume superior a 500L (quinhentos litros);</p>
		<p class="corpo_decreto"><strong class="destaque-decreto">b)</strong> bombas de abastecimento de lquido, dispensers de abastecimento de gs, bombas de recalque, mquinas e motores associados aos tanques de que trata a alnea a deste inciso; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">c)</strong> filtros de combustvel para abastecimento de veculos;</p>
		<p class="corpo_decreto"><strong class="destaque-decreto">d)</strong> equipamentos afins definidos em portaria; </p>
		<p class="corpo_lei"><strong class="destaque-lei">III - </strong>equipamento de sistema especial de segurana da edificao, nos termos deste Cdigo.</p>
		<p class="corpo_decreto"><strong class="destaque-decreto">III -</strong> equipamentos de sistema especial de segurana nas edificaes que: </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">a)</strong> atendam s condies de segurana de uso definidas nas Leis n 8.266, de 20 de junho de 1975, e n 11.228, de 25 de junho de 1992, e no Decreto n 10.878, de 7 de fevereiro de 1974;</p>
		<p class="corpo_decreto"><strong class="destaque-decreto">b)</strong> tenham recebido documentos conforme disposies relativas  segurana definidas nas Leis n 10.199, de 3 de dezembro de 1986, e n 13.558 de 14 de abril de 2003, e que no tenham sido objeto de alteraes de ordem fsica e/ou de utilizao em relao ao documento recebido; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">c)</strong> que venham a receber o Certificado de Concluso, o Certificado de Segurana ou o Certificado de Regularizao, emitidos nos termos do COE e deste decreto. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 1 </strong>No se incluem dentre os equipamentos constantes do inciso I do caput deste artigo os guinchos usados em obras, para transporte de material, guindastes, empilhadeiras mveis, elevadores para canteiros de obras de construo civil e outros no relacionados nos incisos I a XV do artigo 2 da Lei n 10.348, de 4 de setembro de 1987. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 2 </strong>No se incluem dentre os equipamentos constantes do inciso II do caput deste artigo, os tanques de gases medicinais, tanques de gases inertes, silos ou quaisquer tanques que no sejam de armazenagem, como aqueles envolvidos em processos ou presentes em equipamentos de refrigerao. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 3 </strong>No so passveis do cadastramento de que trata o inciso III do caput deste artigo: as edificaes de uso residencial e as edificaes de uso no residenciais com rea construda total acima de 750,00 m (setecentos e cinquenta metros quadrados) e que necessitem de menos de 2 (duas) escadas a prova de fumaa, conforme NTOs e Its, podendo o cadastro ser substitudo pelo AVCB. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 4 </strong>O licenciamento do funcionamento de elevadores e aparelhos de transporte se dar quando do seu cadastro no sistema de licenciamento eletrnico, composto da inscrio do aparelho e do Relatrio de Inspeo Anual  RIA, observadas as seguintes normas: </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">I -</strong> no ato da inscrio no Cadastro de Equipamentos, devero ser fornecidos os dados tcnicos do aparelho e dados cadastrais do imvel no qual est instalado;</p>
		<p class="corpo_decreto"><strong class="destaque-decreto">II -</strong> ser de responsabilidade da empresa conservadora a confeco da chapa a ser fixada no elevador, obedecendo os padres estabelecidos pela Prefeitura; </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">III -</strong> as sanes previstas no COE e nos artigos 13, 14 e 15 da Lei n 10.348, de 1987 sero aplicadas, quando couber, desde que no sejam conflitantes entre si. </p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 48. </strong>O cadastro deve ser instrudo com:</p>
		<p class="corpo_lei"><strong class="destaque-lei">I - </strong>peas grficas e memorial com a descrio e localizao de cada equipamento, assinada por profissional habilitado;</p>
		<p class="corpo_lei"><strong class="destaque-lei">II - </strong>declarao assinada por profissional habilitado, atestando que o equipamento foi instalado conforme o projeto aprovado e atende s normas tcnicas aplicveis e s disposies da legislao municipal na data do protocolo.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 35. </strong>Em funo da natureza do equipamento ou sistema de segurana, podero ser exigidos outros documentos, a serem fixados em portaria <sup>14</sup>, alm dos daqueles previstos no artigo 48 do COE. </p>

		<div id="02_capitulo-II_b11" class="glossario-box">
			<p class="titulo-referencias"><span class="vermelho2">Portaria</span></p>
			<p class="corpo_referencias1"><sup>14 </sup>Ver documentao na Portaria, cap. 3, seo 3.I e 3.J.</p>
		</div>

		<p class="corpo_lei"><strong class="artigo-lei">Art. 49. </strong>O responsvel tcnico pela manuteno das condies de uso do equipamento deve renovar o cadastro, sob pena de caducidade e aplicao das sanes previstas neste Cdigo, a cada perodo de:</p>
		<p class="corpo_lei"><strong class="destaque-lei">I - </strong>1 (um) ano, no caso de elevador e demais equipamentos mecnicos de transporte permanente;</p>
		<p class="corpo_lei"><strong class="destaque-lei">II - </strong>5 (cinco) anos, no caso de tanque de armazenagem, bomba, filtro de combustvel e equipamentos afins e de equipamento de sistema especial de segurana da edificao.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 36. </strong>A manuteno de equipamentos se efetiva pela renovao do cadastro, conforme procedimentos estabelecidos neste decreto e exigncias fixadas em portaria. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">Pargrafo nico. </strong>O responsvel tcnico pela manuteno das condies de uso do equipamento deve renovar o cadastro nos perodos determinados no artigo 49 do COE, sob pena de caducidade e aplicao das sanes nele previstas e neste decreto. </p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 37. </strong>O controle e manuteno das condies apresentadas ao Cadastro e Manuteno de Equipamentos sero efetuados mediante verificaes e testes peridicos, a cargo do proprietrio ou possuidor ou seu responsvel legal, assistido, quando for o caso, por profissional habilitado. </p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 38. </strong>A qualquer momento, o rgo responsvel pelo Cadastro e Manuteno de Equipamentos poder vistoriar as edificaes e intimar os responsveis pelos equipamentos a sanar eventuais irregularidades. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 1 </strong>Constatada a ausncia total ou parcial do cadastro e sua manuteno, o rgo competente poder intimar os responsveis a requerer o cadastro e manuteno de equipamentos, no prazo de 30 (trinta) dias corridos, contados a partir da data de sua publicao no DOC. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 2 </strong>O desatendimento da intimao de que trata o  1 deste artigo, acarretar a aplicao das multas previstas no Anexo III do COE e deste decreto. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 3 </strong>A ausncia do cadastramento e manuteno de equipamentos torna o local passvel da aplicao das multas previstas no Anexo III do COE e deste decreto. </p>
		<h3 class="subsecao_lei">Subseo IX - Da Ficha Tcnica e Diretrizes de Projeto</h3>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 50. </strong>A pedido do interessado, a Prefeitura emite <strong>Ficha Tcnica do imvel</strong><sup>15</sup>, da qual devem constar as informaes relativas ao uso e ocupao do solo,  incidncia de melhoramentos urbansticos e aos demais dados cadastrais disponveis.</p>
	
		<div id="02_capitulo-II_b12" class="glossario-box">
			<p class="corpo_referencias1"><sup>15 </sup>A Ficha Tcnica  documento equivalente ao Boletim de Dados Tcnicos (BDT), podendo ser requerida junto  Municipalidade previamente  autuao do processo de licenciamento.</p>
		</div>

		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 39. </strong>Qualquer muncipe poder solicitar Ficha Tcnica por meio de requerimento eletrnico. </p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 40. </strong>O pedido poder abranger um ou mais imveis, desde que contguos e pertencentes a uma mesma quadra fiscal. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto">Pargrafo nico. </strong>No caso de os imveis no serem contguos, o pedido ser cancelado. </p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 41. </strong>O requerente responder por eventual erro e inexatido no preenchimento do pedido que, ao ser constatado, no gerar direito  emisso da Ficha Tcnica. </p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 42. </strong>A Ficha Tcnica ser emitida pela Secretaria Municipal de Urbanismo e Licenciamento, no prazo de 10 (dez) dias teis, e enviada para o endereo eletrnico cadastrado pelo interessado. </p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 43. </strong>Nos casos em que o pedido se referir a zoneamento indisponvel por meio do sistema informatizado, ser autuado expediente administrativo para consulta, a ser submetido  unidade responsvel, no se aplicando o prazo referido no caput deste artigo. </p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 44. </strong>A autenticidade da Ficha Tcnica poder ser verificada por meio do cdigo de segurana. </p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 45. </strong>Da Ficha Tcnica constaro as informaes cadastrais disponveis, na data de sua emisso, para anlise do projeto de edificao. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 1 </strong>As informaes constantes da Ficha Tcnica so vlidas desde que no haja alteraes legais. </p>
		<p class="corpo_decreto"><strong class="destaque-decreto"> 2 </strong>A Ficha Tcnica poder instruir os pedidos de Diretrizes de Projeto e os Alvars de Aprovao e Execuo. </p>
		<p class="corpo_lei"><strong class="artigo-lei">Art. 51. </strong>A pedido do interessado, a Prefeitura analisa consultas para o esclarecimento quanto  aplicao do PDE, LPUOS e COE em projetos arquitetnicos e emite as Diretrizes de Projeto.</p>
		<p class="corpo_lei"><strong class="destaque-lei">Pargrafo nico. </strong>O pedido deve ser instrudo com documentao e peas grficas que permitam o entendimento do projeto e da consulta formulada.</p>
		<p class="corpo_decreto"><strong class="artigo-decreto">Art. 46. </strong>As consultas a respeito de aplicao do PDE, LPUOS e COE devero ser protocoladas como Diretrizes de Projeto, instrudas com projeto arquitetnico que permita a compreenso do pedido e demais documentos fixados em portaria<sup>16</sup>.</p>

		<div id="02_capitulo-II_b13" class="glossario-box">
			<p class="titulo-referencias"><span class="vermelho2">Portaria</span></p>
			<p class="corpo_referencias1"><sup>16 </sup>Ver documentao na Portaria, cap. 3, seo 3.K.</p>
		</div>

	</div>
</template>

